// api.js

export function sendQuestionToChatGPT(questionText, possibleAnswers, questionType, inputCount, currentOrder, isMultipleCorrect = false, imageBase64Strings = []) {
    return new Promise((resolve, reject) => {
        const formattedPrompt = formatQuestionForChatGPT(questionText, possibleAnswers, questionType, inputCount, currentOrder, isMultipleCorrect, imageBase64Strings);
        const imageUrl = imageBase64Strings.length > 0 ? imageBase64Strings[0] : null;
        chrome.runtime.sendMessage({
            type: "QUERY_CHATGPT",
            questionText: formattedPrompt,
            imageUrl: imageUrl,
            originalQuestionText: questionText

        }, (response) => {
            if (response && response.answer) {
                resolve(response.answer);
            } else {
                reject("No answer received from ChatGPT.");
            }
        });
    });
}
