function formatQuestionForChatGPT(questionText, possibleAnswers, questionType, inputCount = 1, currentOrder = [], isMultipleCorrect = false, imageBase64Strings) {
    let prompt = `${questionText}\n\n`;

    // Kontynuujemy tworzenie promptu jak wczeniej
    if (questionType === 'multipleChoice') {
        if (!Array.isArray(possibleAnswers)) {
            console.error("possibleAnswers is not an array:", possibleAnswers);
            throw new Error("possibleAnswers should be an array for multipleChoice");
        }

        if (imageBase64Strings && imageBase64Strings.length > 0) {
            prompt += "Rozwi na bazie zaczonego zdjcia, zanim zaznaczy odpowied przeanalizuj zdjcie.\n";
        }

        if (isMultipleCorrect) {
            prompt += "Podaj wszystkie poprawne odpowiedzi. Musisz poda co najmniej jedn odpowied. Napisz bardzo krtkie uzasadnienie, nastpnie daj znak '|$|' a po nim napisz jedynie numery poprawnych odpowiedzi.\n\n";
        } else {
            prompt += "Jest jedynie jedna poprawna odpowied, zaznacz t ktra wydaje si by najbardziej prawidowa. Napisz bardzo krtkie uzasadnienie nastpnie daj znak '|$|' a po nim napisz jedynie numer poprawnej.\n\n";
        }

        possibleAnswers.forEach((answer, index) => {
            prompt += `${index + 1}. ${answer}\n`;
        });


    } else if (questionType === 'trueFalse') {
        if (!Array.isArray(possibleAnswers)) {
            console.error("possibleAnswers is not an array:", possibleAnswers);
            throw new Error("possibleAnswers should be an array for trueFalse");
        }
        prompt += "Podaj poprawn odpowied (True/False). Podaj jedynie sam odpowied nic wicej.\n\n";
        possibleAnswers.forEach((answer, index) => {
            prompt += `${index + 1}. ${answer}\n`;
        });
    } else if (questionType === 'matching') {
        if (!Array.isArray(possibleAnswers)) {
            console.error("possibleAnswers is not an array:", possibleAnswers);
            throw new Error("possibleAnswers should be an array for matching");
        }
        prompt += "Dopasuj kady termin do jednej z podanych opcji. Odpowiedz wycznie numerami opcji w kolejnoci ich wystpowania, oddzielajc je przecinkami, np. '3, 1, 2'. Bez dodatkowych komentarzy.\n\n";
        possibleAnswers.forEach(pair => {
            if (!pair || !Array.isArray(pair.options)) {
                console.error("pair.options is not an array:", pair);
                throw new Error("pair.options should be an array in matching");
            }
            prompt += `Termin: ${pair.term}\nOpcje:\n`;
            pair.options.forEach((option, index) => {
                prompt += `  ${index + 1}. ${option}\n`;
            });
        });
    } else if (questionType === 'cloze') {
        if (!Array.isArray(possibleAnswers)) {
            console.error("possibleAnswers is not an array:", possibleAnswers);
            throw new Error("possibleAnswers should be an array for cloze");
        }
        prompt += "Dopasuj odpowiednie odpowiedzi do kadej luki w tekcie. Podaj numer kadej poprawnej opcji w kolejnoci ich wystpowania, bez dodatkowych komentarzy. Na przykad, jeli poprawne odpowiedzi to 'dugoterminowe planowanie', 'strategiczne planowanie', odpowied powinna wyglda tak: '1, 4'.\n\n";
        possibleAnswers.forEach((options, index) => {
            if (!Array.isArray(options)) {
                console.error("options is not an array:", options);
                throw new Error("options should be an array in cloze");
            }
            prompt += `Luka ${index + 1}:\n`;
            options.forEach((option, i) => {
                prompt += `  ${i + 1}. ${option}\n`;
            });
        });
    } else if (questionType === 'cloze-multianswer') {
        prompt += "Jeli pytanie ma list opcji, podaj cyfry numerkw poprawnych odpowiedzi. Jeli pytanie wymaga odpowiedzi tekstowej i nie ma moliwych odpowiedzi, odpowiedz jednym sowem do wpisania. Nie pisz nic poza odpowiedzi. Albo piszesz np. '1,2' albo np. 'well-paid' nie dodawaj nic poza odpowiedzi.\n\n";
    } else if (questionType === 'shortAnswer') {
        if (inputCount > 1) {
            prompt += "Podaj odpowiedzi dla kadego pola tekstowego w osobnym wierszu, w takiej kolejnoci, jak s one widoczne. Nie dodawaj adnych dodatkowych informacji ani wyjanie.\n\n";
        } else {
            prompt += "Podaj jedno sowo lub krtk fraz jako odpowied na ponisze pytanie. Nie dodawaj adnych dodatkowych informacji ani wyjanie. Po prostu wpisz odpowied.\n\n";
        }
    } else if (questionType === 'longAnswer') {
        prompt += "Podaj pen odpowied na ponisze pytanie. Prosz, aby odpowied bya szczegowa i wyczerpujca ale ma by w kilku zdaniach, nie przekraczaj 600 znakw.\n\n";
    } else if (questionType === 'dragAndDrop') {
        prompt += "Dopasuj kad fraz do odpowiedniego miejsca w tekcie. Podaj numer kadej poprawnej frazy w kolejnoci ich wystpowania, bez dodatkowych komentarzy. Na przykad, jeli poprawne odpowiedzi to '1, 3, 2', odpowied powinna wyglda tak: '1, 3, 2'.\n\n";
        possibleAnswers.forEach((answer, index) => {
            prompt += `${index + 1}. ${answer}\n`;
        });
    } else if (questionType === 'ordering') {
        prompt = `${questionText.trim()}\n\n`;
        prompt += "Podaj poprawn kolejno elementw, podajc ich nazwy w odpowiedniej kolejnoci, oddzielajc je przecinkami. Na przykad: 'Element1, Element2, Element3'.\n\n";
        currentOrder.forEach(item => {
            prompt += `${item.trim()}\n`;
        });
        return prompt;
    } else if (questionType === 'opowiedzZAI') {
        prompt = "Prosz opowiedzie o poniszym tekcie w kilku zdaniach:\n\n";
        prompt += questionText;
    } else if (questionType === 'openEnded') {
        prompt += "Podaj jedno sowo lub krtk fraz jako odpowied na ponisze pytanie. Nie dodawaj adnych dodatkowych informacji ani wyjanie (chyba e ewidetnie z treci pytania wynika, e trzeba si rozpisa). Po prostu wpisz odpowied. Przykdowo dla treci pytania 'She has been working here ___ five years.' twoja odpowiedz powinna brzmie dokadnie tak: 'for'\n\n";
        prompt += questionText;
    }

    else {
        prompt += "Podaj poprawn odpowied.\n\n";
        if (Array.isArray(possibleAnswers)) {
            possibleAnswers.forEach((answer, index) => {
                prompt += `${index + 1}. ${answer}\n`;
            });
        } else {
            console.error("possibleAnswers is not an array:", possibleAnswers);
            throw new Error("possibleAnswers should be an array");
        }
    }

    return prompt;
}

