/**********************************************************************
 *  Universal DOM Changes Toggle System  v3.1  (persistent + HTML-safe)
 *********************************************************************/

/* usuwa jedynie pojedynczy bold/span z pierwsz liter */
function stripOneLetterBold(html) {
    return html.replace(
        /<(span\b[^>]*|b|strong)>([A-Za-z])<\/\1>/g,
        '$2'
    );
}

class DOMChangesToggler {
    /* ----------  K O N S T R U K T O R  ---------- */
    constructor() {
        this.isVisible = true;
        this.changedElements = new Map();
        this.svgElements = new Map();
        this.addedElements = new Map();
        this.changedClasses = new Map();
        this.observer = null;

        this.observeConfig = {
            attributes: true,
            attributeOldValue: true,
            attributeFilter: ['style', 'class'],
            childList: true,
            subtree: true
        };

        this.setupKeyboardShortcut();
        this.injectStyles();

        /*  pobierz zapisany stan z chrome.storage */
        chrome.storage.sync.get({ domTogglerVisible: true }, data => {
            this.isVisible = data.domTogglerVisible;

            this.scanExistingChanges();
            this.startObserving();

            if (!this.isVisible) this.hideChanges();   // ukryj od razu, jeli trzeba
        });
    }

    /* ----------  P O M O C N I C Z E  ---------- */
    pauseObserver() { if (this.observer) this.observer.disconnect(); }
    resumeObserver() { if (this.observer) this.observer.observe(document.body, this.observeConfig); }
    saveVisibility() { chrome.storage.sync.set({ domTogglerVisible: this.isVisible }); }

    hideJustAdded(el, meta) {
        switch (meta.type) {
            case 'style':
            case 'border':
            case 'outline':
                if (meta.originalStyle) el.setAttribute('style', meta.originalStyle);
                else el.removeAttribute('style');
                el.style.setProperty('font-weight', 'normal', 'important');
                break;
            case 'class':
                el.className = meta.originalClass;
                break;
            case 'svg':
                if (meta.originalStyle) el.setAttribute('style', meta.originalStyle);
                else el.removeAttribute('style');
                break;
            case 'savemode-text':
                el.innerHTML = meta.originalHTML;          //   zachowaj <select>
                break;
            case 'added':
                el.setAttribute('style', (el.getAttribute('style') || '') + '; display: none !important;');
                break;
        }
    }

    /* ----------  D E T E K T O R Y  ---------- */
    isTrackedStyle(s) {
        return s.includes('color: green') || s.includes('#4CAF50') ||
            s.includes('rgb(76, 175, 80)') ||
            /font-weight\s*:\s*(bold|[67]00)/.test(s) ||
            /color:\s*rgb\(153,\s*151,\s*151\)/.test(s) ||
            /color:\s*rgb\(211,\s*211,\s*211\)/.test(s) ||
            s.includes('#999797') || s.includes('#D3D3D3') ||
            s.includes('#A9A9A9') || s.includes('rgb(169, 169, 169)');
    }

    isAnswerElement(el) {
        const st = el.getAttribute('style') || '';
        const txt = el.textContent || '';
        return el.classList?.contains('correct-answer-info') ||
            el.classList?.contains('correct-answer-date-info') ||
            (st.includes('#4CAF50') || st.includes('rgb(76, 175, 80)')) && st.includes('font-weight') ||
            txt.includes('Poprawna odpowied') || txt.includes('Correct answer');
    }

    isFirstLetterBoldTag(n) { return n && ['B', 'STRONG'].includes(n.tagName) && n.textContent.length === 1; }
    isSavemodeSpan(n) {
        if (!n || n.tagName !== 'SPAN' || n.textContent.length !== 1) return false;
        const w = n.style.fontWeight || getComputedStyle(n).fontWeight;
        return w === 'bold' || w === '600' || w === '700' || parseInt(w, 10) >= 600;
    }

    /* ----------  S K A N   P O C Z  T K O W Y  ---------- */
    scanExistingChanges() {
        /* style / kolor / bold inline */
        document.querySelectorAll(`
        [style*="color: green"], [style*="#4CAF50"], [style*="rgb(76, 175, 80)"],
        [style*="font-weight: 600"], [style*="font-weight:700"], [style*="font-weight: bold"],
        [style*="#999797"], [style*="#D3D3D3"], [style*="#A9A9A9"],
        [style*="rgb(169, 169, 169)"], [style*="rgb(153, 151, 151)"], [style*="rgb(211, 211, 211)"]
      `).forEach(el => {
            if (this.changedElements.has(el)) return;
            this.changedElements.set(el, { originalStyle: el.dataset.originalStyle || '', modifiedStyle: el.getAttribute('style') || '', type: 'style' });
        });

        /* bold jako <b>/<strong> */
        document.querySelectorAll('b,strong').forEach(tag => {
            if (!this.isFirstLetterBoldTag(tag)) return;
            const par = tag.closest('label,.YEVVod,.SG0AAe,p,pre,div') || tag.parentElement;
            if (!par || this.changedElements.has(par)) return;
            if (!par.dataset.originalText) par.dataset.originalText = stripOneLetterBold(par.innerHTML);
            this.changedElements.set(par, {
                originalHTML: par.dataset.originalText,
                modifiedHTML: par.innerHTML,
                type: 'savemode-text'
            });
        });

        /* kolor przez klas */
        document.querySelectorAll('*').forEach(el => {
            const inl = el.getAttribute('style') || '';
            const isGreen = /rgb\( ?7?6?, ?17?5?, ?8?0 ?\)/.test(getComputedStyle(el).color);
            if (isGreen && !inl.includes('color') && !this.changedClasses.has(el)) {
                el.dataset.originalClass = el.className;
                this.changedClasses.set(el, { originalClass: el.className, modifiedClass: el.className, type: 'class' });
            }
        });

        /* divy z odpowiedziami */
        document.querySelectorAll('.correct-answer-info,.correct-answer-date-info').forEach(el => {
            if (this.addedElements.has(el)) return;
            this.addedElements.set(el, { originalDisplay: getComputedStyle(el).display, inlineStyles: el.getAttribute('style') || '', className: el.className, innerHTML: el.innerHTML, type: 'added' });
        });

        /* SVG */
        document.querySelectorAll('svg path[style*="stroke"],svg path[style*="stroke-width"]').forEach(p => {
            const s = p.getAttribute('style') || '';
            if (!/stroke.*green|stroke-width:\s*2|stroke:\s*#ccc|rgb\(204, 204, 204\)/.test(s)) return;
            this.svgElements.set(p, { originalStyle: '', modifiedStyle: s, type: 'svg' });
        });
    }

    /* ----------  T O G G L E  ---------- */
    toggle() {
        this.isVisible = !this.isVisible;
        this.saveVisibility();
        this.isVisible ? this.showChanges() : this.hideChanges();
        this.showNotification(this.isVisible ? 'Odpowiedzi pokazane' : 'Odpowiedzi ukryte');
    }

    /* ----------  H I D E  ---------- */
    hideChanges() {
        this.pauseObserver();

        this.changedElements.forEach((d, el) => {
            if (d.type === 'savemode-text') {
                if (!el.dataset.togglerModifiedHtml) el.dataset.togglerModifiedHtml = el.innerHTML;
                el.innerHTML = d.originalHTML;                           //  uywamy HTML!
            } else if (['style', 'border', 'outline'].includes(d.type)) {
                if (!el.dataset.togglerModifiedStyle) el.dataset.togglerModifiedStyle = el.getAttribute('style') || '';
                if (d.originalStyle) el.setAttribute('style', d.originalStyle); else el.removeAttribute('style');
                el.style.setProperty('font-weight', 'normal', 'important');
            }
        });

        this.changedClasses.forEach((d, el) => {
            if (!el.dataset.togglerModifiedClass) el.dataset.togglerModifiedClass = el.className;
            el.className = d.originalClass;
        });

        this.svgElements.forEach((d, el) => {
            if (!el.dataset.togglerModifiedStyle) el.dataset.togglerModifiedStyle = el.getAttribute('style') || '';
            if (d.originalStyle) el.setAttribute('style', d.originalStyle); else el.removeAttribute('style');
        });

        this.addedElements.forEach((d, el) => {
            if (!el.dataset.togglerSavedStyles) el.dataset.togglerSavedStyles = el.getAttribute('style') || '';
            el.setAttribute('style', el.dataset.togglerSavedStyles + '; display: none !important;');
        });

        this.resumeObserver();
    }

    /* ----------  S H O W  ---------- */
    showChanges() {
        this.pauseObserver();

        this.changedElements.forEach((d, el) => {
            if (d.type === 'savemode-text') {
                el.innerHTML = el.dataset.togglerModifiedHtml || d.modifiedHTML;
            } else if (['style', 'border', 'outline'].includes(d.type)) {
                el.setAttribute('style', el.dataset.togglerModifiedStyle || d.modifiedStyle);
            }
        });

        this.changedClasses.forEach((d, el) => {
            el.className = el.dataset.togglerModifiedClass || d.modifiedClass;
        });

        this.svgElements.forEach((d, el) => {
            el.setAttribute('style', el.dataset.togglerModifiedStyle || d.modifiedStyle);
        });

        this.addedElements.forEach((d, el) => {
            const saved = el.dataset.togglerSavedStyles || d.inlineStyles;
            el.setAttribute('style', saved.replace(/;\s*display\s*:\s*none\s*!important/gi, ''));
        });

        this.resumeObserver();
    }

    /* ----------  O B S E R W A T O R  ---------- */
    startObserving() {
        if (this.observer) return;

        const schedule = fn => { clearTimeout(fn.t); fn.t = setTimeout(fn, 40); };

        this.observer = new MutationObserver(muts => schedule(() => {
            muts.forEach(m => {
                /* (a) zmiana stylu */
                if (m.type === 'attributes' && m.attributeName === 'style') {
                    const el = m.target, cur = el.getAttribute('style') || '';
                    if (!el.dataset.originalStyle && m.oldValue !== null) el.dataset.originalStyle = m.oldValue;

                    /* SVG */
                    if (el.tagName === 'path' && el.parentElement?.tagName === 'svg' && /stroke.*green|stroke-width:\s*2|stroke:\s*#ccc|rgb\(204, 204, 204\)/.test(cur)) {
                        if (!this.svgElements.has(el)) {
                            const meta = { originalStyle: el.dataset.originalStyle || m.oldValue || '', modifiedStyle: cur, type: 'svg' };
                            this.svgElements.set(el, meta); if (!this.isVisible) this.hideJustAdded(el, meta);
                        }
                    }
                    /* style/border/outline */
                    else if (this.isTrackedStyle(cur) || /border:\s*(1|2|3)px/.test(cur) || /outline:\s*green/.test(cur)) {
                        if (!this.changedElements.has(el)) {
                            const t = /border/.test(cur) ? 'border' : /outline/.test(cur) ? 'outline' : 'style';
                            const meta = { originalStyle: el.dataset.originalStyle || m.oldValue || '', modifiedStyle: cur, type: t };
                            this.changedElements.set(el, meta); if (!this.isVisible) this.hideJustAdded(el, meta);
                        }
                    }
                }

                /* (b) dziecko dodane */
                if (m.type === 'childList') {
                    m.addedNodes.forEach(node => {
                        if (node.nodeType !== 1) return;

                        if (this.isAnswerElement(node)) {
                            const meta = { originalDisplay: 'block', inlineStyles: node.getAttribute('style') || '', className: node.className, innerHTML: node.innerHTML, type: 'added' };
                            this.addedElements.set(node, meta); if (!this.isVisible) this.hideJustAdded(node, meta); return;
                        }

                        if (this.isFirstLetterBoldTag(node) || this.isSavemodeSpan(node)) {
                            const par = node.closest('label,.YEVVod,.SG0AAe,p,pre,div') || node.parentElement;
                            if (!par || this.changedElements.has(par)) return;
                            const meta = { originalHTML: stripOneLetterBold(par.innerHTML), modifiedHTML: par.innerHTML, type: 'savemode-text' };
                            this.changedElements.set(par, meta); if (!this.isVisible) this.hideJustAdded(par, meta);
                        }
                    });
                }
            });
        }));
        this.resumeObserver();
    }

    /* ----------  U I  ---------- */
    setupKeyboardShortcut() {
        document.addEventListener('keydown', e => {
            if ((e.ctrlKey || e.metaKey) && e.shiftKey && e.code === 'Space') { e.preventDefault(); this.toggle(); }
        });
    }

    injectStyles() {
        if (document.getElementById('sc-toggle-styles')) return;
        const st = document.createElement('style'); st.id = 'sc-toggle-styles';
        st.textContent = '.sc-notification{position:fixed;top:5px;right:5px;background:rgba(0,0,0,.82);color:#fff;padding:3px 5px;border-radius:2px;font-size:10px;z-index:999999;transition:opacity .3s;pointer-events:none}.sc-notification.fade-out{opacity:0}[style*="display: none !important"]{display:none!important}';
        document.head.appendChild(st);
    }

    showNotification(msg) {
        const n = document.createElement('div'); n.className = 'sc-notification'; n.textContent = msg;
        document.body.appendChild(n); setTimeout(() => { n.classList.add('fade-out'); setTimeout(() => n.remove(), 300); }, 1800);
    }

    /* ----------  D E B U G  ---------- */
    // rescan() { console.log('[DOMToggler] Rescan'); this.scanExistingChanges(); }
    // debugAll() { console.log(this); }
}

/* ===== Helpers & global  ============================================ */
const MarkingHelper = {
    saveOriginalStyle(el) { if (!el.dataset.origStyle) el.dataset.origStyle = el.getAttribute('style') || ''; },
    saveOriginalText(el) { if (!el.dataset.origText) el.dataset.origText = el.textContent; },
    markInputWithListener(inp) { inp.dataset.hasListeners = 'true'; if (!inp.dataset.origPh) inp.dataset.origPh = inp.placeholder || ''; },
    createAnswerDiv(txt, color = '#4CAF50') { const d = document.createElement('div'); d.className = 'correct-answer-info'; d.style.color = color; d.style.fontWeight = 'bold'; d.textContent = txt; return d; }
};

function enableAutoTracking() { return window.domToggler?.startObserving(); }

window.domToggler = new DOMChangesToggler();
export { DOMChangesToggler, MarkingHelper, enableAutoTracking };
