// Also listen for URL changes to handle SPA navigation
let lastUrl = location.href;
new MutationObserver(() => {
    if (location.href !== lastUrl) {
        lastUrl = location.href;
        ////(`URL zmieni si na ${location.href}, ponowne przetwarzanie formularza...`);

        setTimeout(() => {
            ////("Rozpoczcie ponownego przetwarzania po zmianie URL...");
            processGoogleForms().then(results => {
                ////(`Po zmianie URL przetworzono ${results.length} pyta`);
            }).catch(err => {
                ////(`Bd przetwarzania po zmianie URL: ${err.message}`);
            });
        }, 2000);
    }
}).observe(document, { subtree: true, childList: true });

