/*************************************************
 * 2. QUESTION TEXT EXTRACTION
 *************************************************/
async function extractQuestionTextGoogleForms(questionElement) {
    ////("Wyciganie tekstu pytania...");

    // Find the question title element
    const titleElement = questionElement.querySelector('.v-test-questions-title p');
    let questionText = "";

    if (titleElement) {
        questionText = titleElement.innerText.trim();
        ////("Wycignity tekst pytania:", questionText);

        // Extract math content if present
        const mathContent = await extractMathContent(titleElement);
        if (mathContent) {
            questionText += "\n" + mathContent;
        }
    } else {
        ////("Nie znaleziono elementu tytuu pytania (.M7eMe)");
    }

    // Extract any description text
    const descriptionElement = questionElement.querySelector('.v-test-questions-title p img');
    if (descriptionElement && descriptionElement.innerText.trim()) {
        const descText = descriptionElement.innerText.trim();
        questionText += "\n" + descText;
        ////("Wycignity opis pytania:", descText);
    }

    // Extract images
    const images = await extractImages(questionElement);
    if (images.length > 0) {
        ////(`Znaleziono ${images.length} obrazkw w pytaniu`);
    }

    return { text: questionText, images };
}

/*************************************************
 * 3. EXTRACT ANSWERS FOR CHOICE QUESTIONS
 *************************************************/
async function extractChoiceAnswersGoogleForms(questionElement) {
    //("Wyciganie opcji odpowiedzi...");

    // Different question types have different structures
    let answerElements = [];

    // For multiple choice questions (checkboxes)
    if (questionElement.querySelector('input[type="checkbox"]')) {
        answerElements = questionElement.querySelectorAll('.v-test-questions-checkbox-block');
        //(`Znaleziono ${answerElements.length} opcji wielokrotnego wyboru (checkbox)`);
    }
    // For single choice questions (radio buttons)
    else if (questionElement.querySelector('input[type="radio"]')) {
        answerElements = questionElement.querySelectorAll('.v-test-questions-radio-block');
        //(`Znaleziono ${answerElements.length} opcji pojedynczego wyboru (radio)`);
    }

    const options = [];
    const letters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";

    for (let i = 0; i < answerElements.length; i++) {
        const answerElement = answerElements[i];

        // Skip "other" option if present
        if (answerElement.classList.contains('vm96I')) {
            continue;
        }

        // Extract the option text based on question type
        let answerText = "";

        // Uproszczone selektory - szukamy paragrafu bezporednio
        const labelElement = answerElement.querySelector('p');

        if (labelElement) {
            answerText = labelElement.textContent;
        } else {
            // Fallback: try to get any visible text
            answerText = answerElement.textContent;
        }

        // Extract math content if present
        const mathContent = await extractMathContent(answerElement);
        if (mathContent) {
            answerText += "\n" + mathContent;
        }

        // Extract images
        const images = await extractImages(answerElement);

        const id = letters[i] || String(i + 1);
        options.push({
            id,
            label: answerText.trim(),
            images
        });

        //(`Wycignito opcj ${id}: ${answerText.trim()}`);
    }

    return options;
}

/*************************************************
 * 4. EXTRACT OPEN TEXT QUESTIONS
 *************************************************/
async function extractOpenTextQuestionGoogleForms(questionElement) {
    ////("Wyciganie danych pytania otwartego...");

    const inputElement = questionElement.querySelector('input[type="text"]:not([role="combobox"]), textarea');

    // if (inputElement) {
    //     ////(`Znaleziono element input z placeholder: "${inputElement.placeholder || 'Wprowad odpowied'}"`);
    // } else {
    //     ////("Nie znaleziono elementu input dla pytania otwartego");
    // }

    const images = await extractImages(questionElement);

    return {
        placeholder: inputElement ? inputElement.placeholder || "Wprowad odpowied" : "Wprowad odpowied",
        images
    }
}
