    //     // Determine which option is correct
    //     let correctLetter = "";
    //     if (typeof correctAnswerObj === "string") {
    //         correctLetter = correctAnswerObj.trim().toUpperCase();
    //         ////(`Poprawna odpowied to "${correctLetter}"`);
