    //     if (correctOptionText) {
    //         if (mode === "default") {
    //             // Otwrz dropdown, zmie kolor poprawnej odpowiedzi na zielony, a nastpnie zamknij dropdown
    //             dropdownElement.click();

    //             setTimeout(() => {
