// Function to wait for an image to load
async function waitForImageLoad(img, timeout = 5000) {
    return new Promise(resolve => {
        if (img.complete && img.naturalWidth !== 0) {
            return resolve();
        }
        const timer = setTimeout(resolve, timeout);
        img.addEventListener("load", () => {
            clearTimeout(timer);
            resolve();
        });
        img.addEventListener("error", () => {
            clearTimeout(timer);
            resolve();
        });
    });
}

// Extract images from an element
async function extractImages(element) {
    const imageElements = element.querySelectorAll('img');
    const images = [];
    for (const img of imageElements) {
        await waitForImageLoad(img);
        images.push({
            url: img.src,
            alt: img.alt || ""
        });
    }
    return images;
}

// Extract mathematical content if present
function extractMathContent(element, timeout = 300) {
    const pollInterval = 10;
    const startTime = Date.now();
    return new Promise((resolve) => {
        function poll() {
            // Look for math elements in Google Forms
            const mathElements = element.querySelectorAll('.mathml, .katex, [data-math]');
            if (mathElements.length > 0 || (Date.now() - startTime) >= timeout) {
                let mathContent = "";
                mathElements.forEach(mathEl => {
                    if (mathEl.hasAttribute('data-math')) {
                        mathContent += " " + mathEl.getAttribute('data-math').trim();
                    } else if (mathEl.innerText && mathEl.innerText.trim().length > 0) {
                        mathContent += " " + mathEl.innerText.trim();
                    }
                });

                // Look for LaTeX in HTML
                const rawHTML = element.innerHTML;
                const latexRegex = /\$(.*?)\$/g;
                let match;
                while ((match = latexRegex.exec(rawHTML)) !== null) {
                    if (match[1] && match[1].trim().length > 0) {
                        mathContent += " " + match[1].trim();
                    }
                }
                resolve(mathContent.trim());
            } else {
                setTimeout(poll, pollInterval);
            }
        }
        poll();
    });
}

/*************************************************
 * 1. QUESTION TYPE DETECTION
 *************************************************/
function detectQuestionTypeGoogleForms(questionElement) {
    //console.log("Wykrywanie typu pytania...");

    // Check if this is a name/email input that should be skipped
    // Look for common patterns in name/email fields across different languages
    const questionText = questionElement.querySelector('.M7eMe')?.innerText?.toLowerCase() || '';
    if (questionText.includes('email') ||
        questionText.includes('e-mail') ||
        questionText.includes('imi') ||
        questionText.includes('imie') ||
        questionText.includes('nazwisko') ||
        questionText.includes('name') ||
        questionText.match(/^\s*name\s*$/i)) {
        //console.log("Wykryto pole osobowe (email/imi/nazwisko) - pomijamy");
        return "personal";
    }

    // Check for grid/matrix questions with radio buttons
    if (questionElement.querySelector('.lLfZXe[role="radiogroup"]') &&
        questionElement.querySelector('.V4d7Ke.wzWPxe.OIC90c') &&
        questionElement.querySelectorAll('.lLfZXe[role="radiogroup"]').length > 1) {
        //console.log("Wykryto pytanie typu: grid/matrix z radio buttons");
        return "grid";
    }

    // Check for checkbox grid questions
    if (questionElement.querySelector('.mxSrOe') &&
        questionElement.querySelectorAll('.q9ZqCb').length > 0) {
        //console.log("Wykryto pytanie typu: grid/matrix z checkboxami");
        return "checkboxGrid";
    }

    // Check for scale questions (linear scale)
    if (questionElement.querySelector('.N9Qcwe') &&
        questionElement.querySelector('.lLfZXe.fnxRtf.BpKDyb[role="radiogroup"]')) {
        //console.log("Wykryto pytanie typu: skala liniowa");
        return "scale";
    }

    // Check for radio buttons (single choice)
    if (questionElement.querySelector('.Od2TWd[role="radio"]')) {
        //console.log("Wykryto pytanie typu: singleChoice (radio)");
        return "singleChoice";
    }

    // Check for checkboxes (multiple choice)
    if (questionElement.querySelector('.uVccjd[role="checkbox"]')) {
        //console.log("Wykryto pytanie typu: multipleChoice (checkbox)");
        return "multipleChoice";
    }

    // Check for dropdown lists - improved detection
    if (questionElement.querySelector('.jgvuAb[role="listbox"]') ||
        questionElement.querySelector('.MocG8c[role="option"]')) {
        //console.log("Wykryto pytanie typu: dropdown");
        return "dropdown";
    }

    // Check for date pickers
    if (questionElement.querySelector('input[type="date"]') ||
        questionElement.querySelector('[data-includesyear="true"]')) {
        //console.log("Wykryto pytanie typu: date");
        return "date";
    }

    // Check for time pickers
    if (questionElement.querySelector('.ocBCTb[role="group"]') ||
        questionElement.querySelector('[jscontroller="OZjhxc"]')) {
        //console.log("Wykryto pytanie typu: time");
        return "time";
    }

    // Look for text inputs (short answer)
    if (questionElement.querySelector('input[type="text"]:not([role="combobox"]), textarea')) {
        // Check if it's a long answer (textarea) or short answer (input)
        if (questionElement.querySelector('textarea')) {
            //console.log("Wykryto pytanie typu: longText (textarea)");
            return "longText";
        } else {
            //console.log("Wykryto pytanie typu: shortText (input)");
            return "shortText";
        }
    }

    //console.log("Nie udao si wykry typu pytania. Sprawdzam alternatywne selektory.");

    // Alternatywne selektory w przypadku nieznalezienia podstawowych
    if (questionElement.querySelector('.rFrNMe')) {
        //console.log("Wykryto pytanie typu alternatywnie: openText (via .rFrNMe)");
        return "openText";
    }

    if (questionElement.querySelector('.lLfZXe[role="radiogroup"]')) {
        //console.log("Wykryto pytanie typu alternatywnie: singleChoice (via radiogroup)");
        return "singleChoice";
    }

    //console.log("Nie udao si wykry typu pytania - zwracam unknown");
    return "unknown";
}


/*************************************************
 * EXTRACT SCALE QUESTION
 *************************************************/
async function extractScaleQuestionGoogleForms(questionElement) {
    //console.log("Wyciganie danych pytania typu skala...");

    // Extract scale options (the numbers or values in the scale)
    const scaleOptions = [];
    const scaleItems = questionElement.querySelectorAll('.T5pZmf');

    scaleItems.forEach((item, index) => {
        const optionValue = item.querySelector('.Zki2Ve')?.innerText.trim();
        if (optionValue) {
            scaleOptions.push({
                id: String(index + 1),
                label: optionValue
            });
            //console.log(`Wycignito opcj skali ${index + 1}: ${optionValue}`);
        }
    });

    // Extract scale labels (like "Strongly disagree" to "Strongly agree")
    const lowLabel = questionElement.querySelector('.g4s2gf')?.innerText.trim() || "";
    const highLabel = questionElement.querySelector('[jsname="jq1lEb"]')?.innerText.trim() || "";

    //console.log(`Etykiety skali: "${lowLabel}" do "${highLabel}"`);

    return {
        options: scaleOptions,
        lowLabel,
        highLabel
    };
}

/*************************************************
 * EXTRACT DROPDOWN QUESTION
 *************************************************/
async function extractDropdownQuestionGoogleForms(questionElement) {
    //console.log("Wyciganie opcji z listy rozwijanej...");

    const options = [];
    const letters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";

    // Find all dropdown options
    const dropdownOptions = questionElement.querySelectorAll('.MocG8c[role="option"]');

    // Skip the first option if it's a placeholder (like "Choose")
    const startIdx = dropdownOptions.length > 0 &&
        dropdownOptions[0].classList.contains('LMgvRb') ? 1 : 0;

    for (let i = startIdx; i < dropdownOptions.length; i++) {
        const option = dropdownOptions[i];
        const optionText = option.querySelector('.vRMGwf')?.innerText.trim() || "";

        if (optionText) {
            const id = letters[i - startIdx] || String(i - startIdx + 1);
            options.push({
                id,
                label: optionText
            });
            //console.log(`Wycignito opcj dropdown ${id}: ${optionText}`);
        }
    }

    return options;
}

/*************************************************
 * EXTRACT CHECKBOX GRID QUESTION
 *************************************************/
async function extractCheckboxGridGoogleForms(questionElement) {
    //console.log("Wyciganie danych pytania typu grid z checkboxami...");

    // Try multiple selectors for row headers (statements/terms)
    const rowSelectors = [
        '.V4d7Ke.wzWPxe.OIC90c',  // Main selector
        '.V4d7Ke.wzWPxe',         // Alternative selector
        '.mxSrOe .V4d7Ke.wzWPxe.OIC90c'  // Another alternative
    ];

    const statements = [];
    const letters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
    let rowElements = [];

    // Try each selector until we find row elements
    for (const selector of rowSelectors) {
        rowElements = questionElement.querySelectorAll(selector);
        if (rowElements.length > 0) {
            //console.log(`Znaleziono ${rowElements.length} wierszy uywajc selektora: ${selector}`);
            break;
        }
    }

    // Special case - if we still don't have rows but grid is detected, try looking at element text content
    if (rowElements.length === 0) {
        // Try to find rows directly in the grid by looking at row text
        const allGridTexts = questionElement.querySelectorAll('.mxSrOe');

        // Collect row texts that are likely to be row labels
        const possibleRows = [];
        allGridTexts.forEach((element, index) => {
            const rowText = element.querySelector('.wzWPxe')?.textContent.trim();
            if (rowText && rowText.length > 3) {
                possibleRows.push({ element, text: rowText });
            }
        });

        //console.log(`Alternatywnie znaleziono ${possibleRows.length} moliwych etykiet wierszy`);

        // Generate statements from these possible rows
        possibleRows.forEach((row, index) => {
            const id = letters[index] || String(index + 1);
            statements.push({
                id,
                text: row.text
            });
            //console.log(`Wycignito wiersz (alternatywnie) ${id}: ${row.text}`);
        });
    } else {
        // Process found rows normally
        for (let i = 0; i < rowElements.length; i++) {
            const rowText = rowElements[i].textContent.trim();
            if (rowText) {
                const id = letters[i] || String(i + 1);
                statements.push({
                    id,
                    text: rowText
                });
                //console.log(`Wycignito wiersz ${id}: ${rowText}`);
            }
        }
    }

    // Try multiple selectors for column headers (options)
    const columnSelectors = [
        '.ssX1Bd.KZt9Tc .V4d7Ke.OIC90c',  // Main selector
        '.ssX1Bd.KZt9Tc > .V4d7Ke',       // Alternative selector
        '.V4d7Ke.OIC90c'                  // More general selector
    ];

    const commonOptions = [];
    let columnHeaderElements = [];

    // Try each selector until we find column elements
    for (const selector of columnSelectors) {
        columnHeaderElements = questionElement.querySelectorAll(selector);
        // Skip the first element if it's empty (usually the grid corner)
        const startIndex = columnHeaderElements.length > 0 &&
            !columnHeaderElements[0].textContent.trim() ? 1 : 0;

        if (columnHeaderElements.length > startIndex) {
            //console.log(`Znaleziono ${columnHeaderElements.length - startIndex} kolumn uywajc selektora: ${selector}`);

            // Process the column headers
            for (let i = startIndex; i < columnHeaderElements.length; i++) {
                const columnText = columnHeaderElements[i].textContent.trim();
                if (columnText) {
                    const id = String(i - startIndex + 1);
                    commonOptions.push({
                        id,
                        label: columnText
                    });
                    //console.log(`Wycignito kolumn ${id}: ${columnText}`);
                }
            }
            break;
        }
    }

    // If we still don't have columns but have rows, create default columns
    if (commonOptions.length === 0 && statements.length > 0) {
        //console.log("Nie znaleziono kolumn. Tworzenie domylnych opcji.");

        // Look for checkbox elements to count columns
        const firstRow = questionElement.querySelector('.mxSrOe');
        const checkboxesInFirstRow = firstRow ? firstRow.querySelectorAll('.uVccjd[role="checkbox"]') : [];

        const columnCount = checkboxesInFirstRow.length > 0 ?
            checkboxesInFirstRow.length : 3; // Default to 3 if can't determine

        // Create default column options
        for (let i = 0; i < columnCount; i++) {
            const id = String(i + 1);
            const defaultLabel = `Opcja ${id}`;
            commonOptions.push({
                id,
                label: defaultLabel
            });
            //console.log(`Utworzono domyln kolumn ${id}: ${defaultLabel}`);
        }
    }

    return { statements, commonOptions };
}

