/*************************************************
 * 2. QUESTION TEXT EXTRACTION
 *************************************************/
async function extractQuestionTextGoogleForms(questionElement) {
    //console.log("Wyciganie tekstu pytania...");

    // Find the question title element
    const titleElement = questionElement.querySelector('.M7eMe');
    let questionText = "";

    if (titleElement) {
        questionText = titleElement.innerText.trim();
        //console.log("Wycignity tekst pytania:", questionText);

        // Extract math content if present
        const mathContent = await extractMathContent(titleElement);
        if (mathContent) {
            questionText += "\n" + mathContent;
        }
    } else {
        //console.log("Nie znaleziono elementu tytuu pytania (.M7eMe)");
    }

    // Extract any description text
    const descriptionElement = questionElement.querySelector('.gubaDc.OIC90c.RjsPE');
    if (descriptionElement && descriptionElement.innerText.trim()) {
        const descText = descriptionElement.innerText.trim();
        questionText += "\n" + descText;
        //console.log("Wycignity opis pytania:", descText);
    }

    // Extract images
    const images = await extractImages(questionElement);
    if (images.length > 0) {
        //console.log(`Znaleziono ${images.length} obrazkw w pytaniu`);
    }

    return { text: questionText, images };
}

/*************************************************
 * 3. EXTRACT ANSWERS FOR CHOICE QUESTIONS
 *************************************************/
async function extractChoiceAnswersGoogleForms(questionElement) {
    //console.log("Wyciganie opcji odpowiedzi...");

    // Different question types have different structures
    let answerElements = [];

    // For multiple choice questions (checkboxes)
    if (questionElement.querySelector('.uVccjd[role="checkbox"]')) {
        answerElements = questionElement.querySelectorAll('.eBFwI[role="listitem"]');
        //console.log(`Znaleziono ${answerElements.length} opcji wielokrotnego wyboru (checkbox)`);
    }
    // For single choice questions (radio buttons)
    else if (questionElement.querySelector('.Od2TWd[role="radio"]')) {
        answerElements = questionElement.querySelectorAll('.nWQGrd.zwllIb');
        //console.log(`Znaleziono ${answerElements.length} opcji pojedynczego wyboru (radio)`);
    }
    // For dropdown questions
    else if (questionElement.querySelector('.MocG8c[role="option"]')) {
        answerElements = questionElement.querySelectorAll('.MocG8c[role="option"]:not(:first-child)');
        //console.log(`Znaleziono ${answerElements.length} opcji w dropdown`);
    }
    // Alternative single choice layout
    else if (questionElement.querySelector('.lLfZXe[role="radiogroup"]')) {
        answerElements = questionElement.querySelectorAll('.nWQGrd.zwllIb');
        //console.log(`Znaleziono ${answerElements.length} opcji pojedynczego wyboru (alternatywny radio)`);

        // Alternatywny selektor jeli powyszy nie dziaa
        if (answerElements.length === 0) {
            answerElements = questionElement.querySelectorAll('.lLfZXe[role="radiogroup"] .docssharedWizToggleLabeledContainer');
            //console.log(`Znaleziono ${answerElements.length} opcji pojedynczego wyboru (gbszy alternatywny selektor)`);
        }
    }

    if (answerElements.length === 0) {
        //console.log("Nie znaleziono elementw odpowiedzi. Struktura HTML moe by inna.");
    }

    const options = [];
    const letters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";

    for (let i = 0; i < answerElements.length; i++) {
        const answerElement = answerElements[i];

        // Skip "other" option if present
        if (answerElement.classList.contains('vm96I')) {
            continue;
        }

        // Extract the option text based on question type
        let answerText = "";

        // For multiple choice
        const checkboxLabel = answerElement.querySelector('.aDTYNe, .ulDsOb span');
        // For single choice
        const radioLabel = answerElement.querySelector('.aDTYNe.snByac.OvPDhc');
        // For dropdown
        const dropdownOption = answerElement.querySelector('.vRMGwf.oJeWuf');

        if (checkboxLabel) {
            answerText = checkboxLabel.textContent;
        } else if (radioLabel) {
            answerText = radioLabel.textContent;
        } else if (dropdownOption) {
            answerText = dropdownOption.textContent;
        } else {
            // Fallback: try to get any visible text
            answerText = answerElement.textContent;
        }

        // Extract math content if present
        const mathContent = await extractMathContent(answerElement);
        if (mathContent) {
            answerText += "\n" + mathContent;
        }

        // Extract images
        const images = await extractImages(answerElement);

        const id = letters[i] || String(i + 1);
        options.push({
            id,
            label: answerText.trim(),
            images
        });

        //console.log(`Wycignito opcj ${id}: ${answerText.trim()}`);
    }

    return options;
}

