let quizizzAnswersCache = null;
let quizizzInterval = null;
let mode = 'default';
let lastQuestionText = null;
let waitingForQuizElement = false;
let quizPollingInterval = null;

export function initQuizizzHandler() {
    quizizzAnswersCache = null;
    lastQuestionText = null;
    stopCheckingCurrentQuestion();

    if (window.location.href.includes("quizizz.com/join/game/")) {
        startWaitingForQuizElements();
    }
}

function startWaitingForQuizElements() {
    if (waitingForQuizElement || quizPollingInterval) {
        return;
    }
    waitingForQuizElement = true;
    checkAndHandleQuizElements();
    quizPollingInterval = setInterval(checkAndHandleQuizElements, 1000);
}

function checkAndHandleQuizElements() {
    const questionContainer = document.querySelector('[data-testid="question-container"]');
    const questionTextEl = document.querySelector('#questionText');
    if (questionContainer && questionTextEl) {
        waitingForQuizElement = false;
        clearInterval(quizPollingInterval);
        quizPollingInterval = null;
        handleQuizizzAnswers();
        setupQuizizzNavObserver();
    }
}

function extractCurrentQuizizzQuestion() {
    const domQuestion = document.querySelector('[data-testid="question-container"]');
    if (!domQuestion) {
        console.warn("Nie znaleziono kontenera pytania");
        return null;
    }

    const questionParagraph = domQuestion.querySelector('#questionText');
    if (!questionParagraph) {
        console.warn("Nie znaleziono elementu tekstu pytania");
        return null;
    }

    let rawQuestion = questionParagraph.textContent.replace(/\n/g, ' ').replace(/\s+/g, ' ').trim();
    let questionText = fixMathNotation(rawQuestion);

    const quizContainer = domQuestion.closest('.quiz-container');
    if (!quizContainer) {
        console.warn("Nie znaleziono kontenera quizu");
        return null;
    }

    const optionButtons = quizContainer.querySelectorAll('.options-grid .option');
    const options = [];
    optionButtons.forEach((btn, index) => {
        const optionTextEl = btn.querySelector('#optionText');
        if (optionTextEl) {
            let rawOpt = optionTextEl.textContent.replace(/\n/g, ' ').replace(/\s+/g, ' ').trim();
            let optionText = fixMathNotation(rawOpt);
            options.push({
                text: optionText,
                id: String(index + 1)
            });
        } else {
            console.warn(`Opcja ${index + 1} nie ma elementu #optionText`);
        }
    });

    const isMultipleChoice = !!(
        document.querySelector('.multiple-choice-metadata-title') ||
        document.querySelector('[data-test="multiple-choice-info"]') ||
        document.querySelector('.msq-text') ||
        document.querySelector('.is-msq')
    );

    const questionType = isMultipleChoice ? "MSQ" : "MCQ";


    const questionData = {
        question: { text: questionText },
        options: options,
        type: questionType
    };

    return questionData;
}

export async function handleQuizizzAnswers() {
    try {
        await waitForQuizizzElements();
    } catch (error) {
        console.warn("Timeout podczas oczekiwania na elementy Quizizz", error);
        return;
    }

    const currentQuestionData = extractCurrentQuizizzQuestion();
    if (!currentQuestionData || !currentQuestionData.question?.text) {
        console.warn("Nie mona pobra tekstu aktualnego pytania");
        return;
    }

    const currentQuestionText = currentQuestionData.question.text;
    const currentUrl = window.location.href;

    if (quizizzAnswersCache && lastQuestionText === currentQuestionText) {
        chrome.storage.local.get(['markingMode', 'autoMode'], (result) => {
            mode = result.markingMode || 'default';
            const autoMode = !!result.autoMode;
            autoMode ? startCheckingCurrentQuestion() : stopCheckingCurrentQuestion();
        });
        return;
    }

    quizizzAnswersCache = null;
    lastQuestionText = currentQuestionText;

    chrome.storage.local.get(['markingMode', 'userEmail', 'sessionId', 'autoMode'], (result) => {
        mode = result.markingMode || 'default';
        const userEmail = result.userEmail;
        const sessionId = result.sessionId;
        const autoMode = !!result.autoMode;
        if (!sessionId || !userEmail) {
            console.error("Brak danych uwierzytelniajcych");
            return;
        }
        if (!autoMode) {
            return;
        }
        const quizId = extractQuizIdFromUrl(currentUrl);
        const quizPin = getQuizPin();

        chrome.runtime.sendMessage({
            type: "QUERY_CHATGPT_MF",
            platform: "quizizz",
            quizId: currentUrl,
            quizSimpleId: quizId,
            pin: quizPin,
            mode: mode,
            userEmail: userEmail,
            questionData: currentQuestionData
        }, (response) => {
            if (response && response.answer) {
                quizizzAnswersCache = response.answer.correctAnswer;
                if (autoMode) {
                    if (typeof startCheckingCurrentQuestion === 'function') {
                        startCheckingCurrentQuestion();
                    } else {
                        console.error("Funkcja startCheckingCurrentQuestion nie jest zdefiniowana!");
                    }
                }
            } else {
                console.error(" Bd:", response?.error || "Brak odpowiedzi");
            }
        });
    });
}
function getQuizPin() {
    // First try to get the pin from the button element
    const pinButton = document.querySelector('.v-popper--theme-tooltip button.room-code');
    if (pinButton) {
        return pinButton.innerText.replace(/\s+/g, '');
    }

    // If not found in the DOM, try to get it from localStorage
    try {
        const previousContext = localStorage.getItem('previousContext');
        if (previousContext) {
            const parsedContext = JSON.parse(previousContext);
            if (parsedContext && parsedContext.game && parsedContext.game.roomCode) {
                return parsedContext.game.roomCode;
            }
        }
    } catch (error) {
        console.error('Error reading from localStorage:', error);
    }

    return null;
}

function extractQuizIdFromUrl(url) {
    try {
        const urlObj = new URL(url);
        const parts = urlObj.pathname.split('/');
        const gameIndex = parts.indexOf('game');
        if (gameIndex !== -1 && gameIndex + 1 < parts.length) {
            return parts[gameIndex + 1];
        }
        return url.split('?')[0];
    } catch (e) {
        console.error("Bd przy ekstrakcji ID quizu:", e);
        return url;
    }
}

function cleanupFailedQuizitCache() {
    chrome.storage.local.get(['failedQuizitQuizzes'], (result) => {
        if (result.failedQuizitQuizzes) {
            try {
                const cache = JSON.parse(result.failedQuizitQuizzes);
                const now = Date.now();
                const oneWeek = 7 * 24 * 60 * 60 * 1000;
                let updatedCache = {};
                let needsUpdate = false;
                for (const [quizId, value] of Object.entries(cache)) {
                    if (typeof value === 'boolean') {
                        updatedCache[quizId] = { failed: true, timestamp: now };
                        needsUpdate = true;
                    } else if (typeof value === 'object' && value.timestamp) {
                        if (now - value.timestamp < oneWeek) {
                            updatedCache[quizId] = value;
                        } else {
                            needsUpdate = true;
                        }
                    }
                }
                if (needsUpdate) {
                    chrome.storage.local.set({ failedQuizitQuizzes: JSON.stringify(updatedCache) });
                }
            } catch (e) {
                console.error("Bd przy czyszczeniu cache:", e);
            }
        }
    });
}
setTimeout(cleanupFailedQuizitCache, 5000);
function waitForQuizizzElements() {
    return new Promise((resolve) => {
        function check() {
            chrome.storage.local.get('autoMode', (result) => {
                const autoMode = !!result.autoMode;
                if (!autoMode) {
                    resolve();
                    return;
                }
                const questionContainer = document.querySelector('[data-testid="question-container"]');
                const questionTextEl = document.querySelector('#questionText');
                if (questionContainer && questionTextEl) {
                    resolve();
                } else {
                    setTimeout(check, 500);
                }
            });
        }
        if (!document.body) {
            const interval = setInterval(() => {
                if (document.body) {
                    clearInterval(interval);
                    check();
                }
            }, 100);
        } else {
            check();
        }
    });
}


function highlightCurrentQuizizzQuestion(answersData) {

    const domQuestionData = extractCurrentQuizizzQuestion();
    if (!domQuestionData || !domQuestionData.question?.text) {
        console.warn("Nie mona pobra aktualnego pytania (DOM)");
        return;
    }
    const domQuestionText = domQuestionData.question.text;

    const matchedQuestion = findMatchingQuestion(domQuestionText, answersData);
    if (!matchedQuestion) {
        console.warn("Nie znaleziono dopasowania dla pytania:", domQuestionText);
        return;
    }
    const correctAnswers = matchedQuestion.answers;

    const domQuestion = document.querySelector('[data-testid="question-container"]');
    if (!domQuestion) {
        console.warn("Nie znaleziono kontenera pytania");
        return;
    }
    const quizContainer = domQuestion.closest('.quiz-container');
    if (!quizContainer) {
        console.warn("Nie znaleziono kontenera quizu");
        return;
    }
    const optionButtons = quizContainer.querySelectorAll('.options-grid .option');
    switch (mode) {
        case 'immediate':
            markCorrectAnswersImmediate(optionButtons, correctAnswers);
            break;
        case 'saveMode':
            markCorrectAnswersSavemode(optionButtons, correctAnswers);
            break;
        default:
            markCorrectAnswersDefault(optionButtons, correctAnswers);
            break;
    }
}


function debounce(func, wait) {
    let timeout;
    return function (...args) {
        const context = this;
        clearTimeout(timeout);
        timeout = setTimeout(() => func.apply(context, args), wait);
    };
}

function setupQuizizzNavObserver() {
    const observer = new MutationObserver(debounce(() => {
        if (!window.location.href.includes("quizizz.com/join/game/")) return;
        chrome.storage.local.get('autoMode', (result) => {
            const autoMode = !!result.autoMode;
            if (!autoMode) return;
            const currentQuestionData = extractCurrentQuizizzQuestion();
            if (!currentQuestionData || !currentQuestionData.question?.text) return;
            const currentQuestionText = currentQuestionData.question.text;
            if (lastQuestionText !== currentQuestionText) {
                handleQuizizzAnswers();
            }
        });
    }, 300));
    observer.observe(document.body, { childList: true, subtree: true, attributes: true });
    return observer;
}

function normalizeText(text) {
    return stripHtml(text).toLowerCase()
        .normalize("NFD").replace(/[\u0300-\u036f]/g, "")
        .replace(/\s+/g, ' ').trim();
}
function markCorrectAnswersDefault(optionButtons, correctAnswers) {
    const container = document.querySelector('[data-testid="question-container"]').closest('.quiz-container');
    const textarea = container.querySelector('textarea.typed-option-input');
    if (textarea) {
        const answerText = (Array.isArray(correctAnswers) && correctAnswers.length > 0 && correctAnswers[0].text)
            ? stripHtml(correctAnswers[0].text).trim()
            : "";
        let answerDiv = container.querySelector('.bg-correct-answer');
        if (!answerDiv) {
            answerDiv = document.createElement('div');
            answerDiv.classList.add('bg-correct-answer');
            answerDiv.style.color = 'green';
            answerDiv.style.fontWeight = "700";
            answerDiv.style.marginTop = '5px';
            textarea.parentElement.appendChild(answerDiv);
        }
        answerDiv.textContent = answerText;
        return;
    }


    if (!Array.isArray(correctAnswers)) {
        console.error("correctAnswers nie jest tablic:", correctAnswers);
        return;
    }


    correctAnswers.forEach((ans) => {
        let ansTextClean = "";
        if (typeof ans === 'object' && ans.text) {
            ansTextClean = stripHtml(ans.text).trim();
        } else if (typeof ans === 'string') {
            ansTextClean = stripHtml(ans).trim();
        } else if (typeof ans === 'number') {
            ansTextClean = String(ans);
        } else {
            console.warn("Nieznany format odpowiedzi:", ans);
            return;
        }

        let answerNumbers = [];
        if (ansTextClean.indexOf(',') > -1) {
            answerNumbers = ansTextClean.split(',').map(s => s.trim());
        } else {
            answerNumbers.push(ansTextClean);
        }

        answerNumbers.forEach(answerNum => {
            let matchFound = false;
            optionButtons.forEach((btn, index) => {
                const optionTextEl = btn.querySelector('#optionText');
                if (!optionTextEl) return;
                const optionText = optionTextEl.innerText.trim();

                let isCorrect = optionText === answerNum;
                if (!isCorrect) {
                    const normalizedAns = normalizeText(answerNum);
                    const normalizedOpt = normalizeText(optionText);
                    isCorrect = normalizedOpt === normalizedAns;
                }
                if (!isCorrect && !isNaN(parseInt(answerNum))) {
                    const ansIndex = parseInt(answerNum) - 1;
                    isCorrect = index === ansIndex;
                }
                if (isCorrect) {
                    matchFound = true;
                    btn.style.border = '3px solid #2ecc71';
                    btn.style.boxShadow = '0 0 10px rgba(46, 204, 113, 0.7)';
                    btn.style.backgroundColor = 'rgba(46, 204, 113, 0.2)';
                }
            });
            if (!matchFound) {
                console.warn(` Nie znaleziono dopasowania dla backendowej odpowiedzi: "${answerNum}"`);
            }
        });
    });
}

let markedAnswersCache = {};
function markCorrectAnswersImmediate(optionButtons, correctAnswers) {
    const container = document.querySelector('[data-testid="question-container"]').closest('.quiz-container');
    const textarea = container.querySelector('textarea.typed-option-input');
    if (textarea) {
        const answerText = (Array.isArray(correctAnswers) && correctAnswers.length > 0 && correctAnswers[0].text)
            ? stripHtml(correctAnswers[0].text).trim()
            : "";
        textarea.value = answerText;
        textarea.dispatchEvent(new Event('input', { bubbles: true }));
        return;
    }
    const questionTextElement = document.querySelector('[data-testid="question-container"] #questionText');
    if (!questionTextElement) return;
    const currentQuestion = questionTextElement.innerText.trim();
    const questionKey = normalizeText(currentQuestion).substring(0, 50);
    if (markedAnswersCache[questionKey]) {
        return;
    }
    const buttonsToClick = [];
    correctAnswers.forEach((ans) => {
        let ansTextClean = "";
        if (typeof ans === 'object' && ans.text) {
            ansTextClean = stripHtml(ans.text).trim();
        } else if (typeof ans === 'string') {
            ansTextClean = stripHtml(ans).trim();
        } else if (typeof ans === 'number') {
            ansTextClean = String(ans);
        } else {
            console.warn("Nieznany format odpowiedzi:", ans);
            return;
        }

        let answerNumbers = [];
        if (ansTextClean.indexOf(',') > -1) {
            answerNumbers = ansTextClean.split(',').map(s => s.trim());
        } else {
            answerNumbers.push(ansTextClean);
        }
        answerNumbers.forEach(answerNum => {
            optionButtons.forEach((btn, index) => {
                const optionTextEl = btn.querySelector('#optionText');
                if (!optionTextEl) return;
                const optionText = optionTextEl.innerText.trim();
                let isCorrect = optionText === answerNum ||
                    normalizeText(optionText) === normalizeText(answerNum);
                if (!isCorrect && !isNaN(parseInt(answerNum))) {
                    const ansIndex = parseInt(answerNum) - 1;
                    isCorrect = index === ansIndex;
                }
                if (isCorrect) {
                    buttonsToClick.push(index);
                }
            });
        });
    });
    if (buttonsToClick.length > 0) {
        buttonsToClick.forEach((index, i) => {
            setTimeout(() => {
                try {
                    const btn = optionButtons[index];
                    if (btn) {
                        btn.click();
                    }
                } catch (e) {
                    console.error(`Bd podczas klikania opcji ${index}:`, e);
                }
            }, i * 300);
        });
        markedAnswersCache[questionKey] = buttonsToClick;
        const cacheKeys = Object.keys(markedAnswersCache);
        if (cacheKeys.length > 20) {
            delete markedAnswersCache[cacheKeys[0]];
        }
    }
}
function markCorrectAnswersSavemode(optionButtons, correctAnswers) {
    // Check if we're on mobile layout by examining the DOM structure
    const optionsGrid = document.querySelector('.options-grid');
    const isMobileLayout = optionsGrid && optionsGrid.classList.contains('flex-col');

    // Handle text input questions
    const container = document.querySelector('[data-testid="question-container"]')?.closest('.quiz-container');
    if (!container) {
        console.warn("Nie znaleziono kontenera quizu");
        return;
    }

    const textarea = container.querySelector('textarea.typed-option-input');
    if (textarea) {
        const answerText = (Array.isArray(correctAnswers) && correctAnswers.length > 0 && correctAnswers[0].text)
            ? stripHtml(correctAnswers[0].text).trim()
            : "";
        textarea.addEventListener('focus', function () {
            textarea.setAttribute('placeholder', answerText);
        });
        textarea.addEventListener('blur', function () {
            textarea.setAttribute('placeholder', '');
        });
        return;
    }

    // Process each correct answer
    correctAnswers.forEach((ans) => {
        let ansTextClean = "";
        if (typeof ans === 'object' && ans.text) {
            ansTextClean = stripHtml(ans.text).trim();
        } else if (typeof ans === 'string') {
            ansTextClean = stripHtml(ans).trim();
        } else if (typeof ans === 'number') {
            ansTextClean = String(ans);
        } else {
            console.warn("Nieznany format odpowiedzi:", ans);
            return;
        }

        // Process each option button to find matches
        optionButtons.forEach((btn, index) => {
            // Find the option text element regardless of the layout
            const optionTextEl = btn.querySelector('#optionText');

            if (!optionTextEl) return;

            // Get the text content from the element
            const optionText = optionTextEl.innerText.trim();

            let isCorrect = optionText === ansTextClean ||
                normalizeText(optionText) === normalizeText(ansTextClean);

            // Try index-based matching if text matching fails
            if (!isCorrect && !isNaN(parseInt(ansTextClean))) {
                const ansIndex = parseInt(ansTextClean) - 1;
                isCorrect = index === ansIndex;
            }

            // Apply different highlighting based on layout
            if (isCorrect) {
                if (isMobileLayout) {
                    // Mobile layout (vertical options)
                    btn.style.boxShadow = '0 0 5px 3px rgba(255, 255, 255, 0.2)';
                    // Add a subtle background color for better visibility on mobile
                    btn.style.backgroundColor = 'rgba(255, 255, 255, 0.05)';
                } else {
                    // Desktop layout
                    btn.style.boxShadow = '0 0 5px 3px rgba(255, 255, 255, 0.2)';
                }
            }
        });
    });
}


function startCheckingCurrentQuestion() {
    stopCheckingCurrentQuestion();
    markedAnswersCache = {};
    quizizzInterval = setInterval(() => {
        chrome.storage.local.get('autoMode', async (result) => {
            const autoMode = !!result.autoMode;
            if (!autoMode) {
                stopCheckingCurrentQuestion();
                return;
            }
            try {
                const currentQuestionData = extractCurrentQuizizzQuestion();
                if (!currentQuestionData || !currentQuestionData.question?.text) {
                    console.warn("Nie mona pobra tekstu aktualnego pytania");
                    return;
                }
                const currentQuestionText = currentQuestionData.question.text;
                if (lastQuestionText !== currentQuestionText) {
                    await handleQuizizzAnswers();
                    return;
                }
                if (quizizzAnswersCache) {
                    highlightCurrentQuizizzQuestion(quizizzAnswersCache);
                }
            } catch (e) {
                console.error("Bd podczas sprawdzania pytania:", e);
            }
        });
    }, 1000);
}

function stopCheckingCurrentQuestion() {
    if (quizizzInterval) {
        clearInterval(quizizzInterval);
        quizizzInterval = null;
    }
}



function stripHtml(htmlString) {
    return htmlString.replace(/<[^>]*>/g, '');
}

function findMatchingQuestion(domQuestionText, questionsArray) {

    const normalizedDom = domQuestionText.replace(/\s+/g, '');

    for (const q of questionsArray) {
        if (!q.question || !q.question.text) {
            console.warn("Nieprawidowa struktura pytania:", q);
            continue;
        }
        const questionTextClean = stripHtml(q.question.text).trim();
        const normalizedBackend = questionTextClean.replace(/\s+/g, '');


        if (normalizedDom === normalizedBackend) {
            return q;
        }

        const normDom = normalizeText(domQuestionText);
        const normBackend = normalizeText(questionTextClean);

        if (normDom.includes(normBackend) || normBackend.includes(normDom)) {
            return q;
        }
    }

    if (questionsArray.length === 1) {
        return questionsArray[0];
    }

    console.warn("Nie znaleziono dopasowania");
    return null;
}
function fixMathNotation(text) {
    text = text.replace(/[\u{1D434}-\u{1D44D}]/gu, match =>
        String.fromCharCode(match.codePointAt(0) - 0x1D434 + 0x41));
    text = text.replace(/[\u{1D44E}-\u{1D467}]/gu, match =>
        String.fromCharCode(match.codePointAt(0) - 0x1D44E + 0x61));
    return text;
}
