// W content_script.js - Zaktualizuj t funkcj dla Naurok

function extractNaurokAnalytics() {
    // console.log("Naurok Scraper: Starting extraction (v5 - Revised Label Processing)...");
    const analytics = {
        platform: "Naurok",
        scoreEarned: null,
        scorePossible: null,
        scorePercentage: null,
        quizTitle: null,
        homeworkTitle: null,
        teacher: null,
        student: null,
        dateTime: null,
        totalQuestions: null,
        correctAnswersCount: null,
        incorrectAnswersCount: null,
        skippedAnswersCount: 0,
        totalTime: null,
        averageTimePerQuestion: null,
        questionsScoreRatio: null,
        questions: [],
        _rawScoreData: null,
        _rawPercentageResult: null,
        _rawAccuracyPercentage: null,
        error: null
    };

    const quizInfoContainer = document.querySelector('.homework-result-info');
    // console.log("Naurok Scraper: quizInfoContainer found?", !!quizInfoContainer);

    if (!quizInfoContainer) {
        analytics.error = "Main content container '.homework-result-info' not found.";
        // console.log("Naurok Scraper:", analytics.error);
        return analytics;
    }

    try {
        analytics.quizTitle = quizInfoContainer.querySelector('.homework-personal-stat-test')?.innerText.trim() || null;
        const detailsDiv = quizInfoContainer.querySelector('div[style="font-size:14px; line-height:1.5;"]');
        if (detailsDiv) {
            const detailElements = detailsDiv.querySelectorAll('div');
            analytics.homeworkTitle = detailElements[0]?.innerText.replace(': ', '').trim() || null;
            analytics.teacher = detailElements[1]?.innerText.replace(': ', '').trim() || null;
            analytics.student = detailElements[2]?.innerText.replace('(): ', '').trim() || null;
            analytics.dateTime = detailElements[3]?.innerText.trim() || null;
        }

        const totalQuestionsElement = quizInfoContainer.querySelector('.homework-personal-stat-number');
        if (totalQuestionsElement && totalQuestionsElement.firstChild) {
            const totalQuestionsText = totalQuestionsElement.firstChild.textContent.trim();
            if (!isNaN(parseInt(totalQuestionsText))) {
                analytics.totalQuestions = parseInt(totalQuestionsText);
            }
        }
        // console.log("Naurok Scraper: Basic info - Title:", analytics.quizTitle, "TotalQ:", analytics.totalQuestions);

        // --- NOWA LOGIKA PRZETWARZANIA ETYKIET I WARTOCI ---
        const allLabelElements = quizInfoContainer.querySelectorAll('.homework-personal-stat-label');
        // console.log("Naurok Scraper: Found allLabelElements count:", allLabelElements.length);

        allLabelElements.forEach((labelElement, index) => {
            const labelText = labelElement.innerText.trim();
            const labelTextUpper = labelText.toUpperCase(); // Do porwna niezalenych od wielkoci liter

            let valueElement = null;
            let spanValue = null;
            let valueFullText = null; // Peny tekst z elementu wartoci

            const labelColumn = labelElement.closest('div[class*="col-"]');
            if (labelColumn && labelColumn.nextElementSibling) {
                const valueCell = labelColumn.nextElementSibling;
                if (labelTextUpper === '') {
                    valueElement = valueCell.querySelector('.progress'); // Specjalny przypadek dla paska
                } else {
                    valueElement = valueCell.querySelector('.homework-personal-stat-value');
                }

                if (valueElement) {
                    spanValue = valueElement.querySelector('span')?.innerText.trim(); // Moe by null
                    valueFullText = valueElement.innerText.trim();
                }
            }

            // console.log(`Naurok Scraper: Processing Label Element ${index} - Text: "${labelText}", Upper: "${labelTextUpper}", ValueElement found: ${!!valueElement}, SpanValue: "${spanValue}", ValueFullText: "${valueFullText}"`);

            if (labelTextUpper === '' && valueElement && spanValue) {
                const parts = spanValue.split('/').map(s => s.trim());
                if (parts.length === 2 && !isNaN(parseInt(parts[0])) && !isNaN(parseInt(parts[1]))) {
                    analytics._rawScoreData = { earned: parseInt(parts[0]), total: parseInt(parts[1]), unit: valueFullText.split(/\s+/).pop() };
                    // console.log("Naurok Scraper: Parsed '':", analytics._rawScoreData);
                } else {
                    console.warn("Naurok Scraper: Failed to parse '' from spanValue:", spanValue, "Parts:", parts);
                }
            } else if (labelTextUpper === '' && valueElement && spanValue) {
                if (!isNaN(parseFloat(spanValue))) {
                    analytics._rawPercentageResult = parseFloat(spanValue);
                    // console.log("Naurok Scraper: Parsed '' (percentage):", analytics._rawPercentageResult);
                } else {
                    console.warn("Naurok Scraper: Failed to parse '' from spanValue:", spanValue);
                }
            } else if (labelTextUpper === '' && valueElement) { // valueElement to .progress
                const progressBar = valueElement.querySelector('.progress-bar');
                if (progressBar && progressBar.style.width) {
                    const accuracyValText = progressBar.style.width.replace('%', '');
                    if (!isNaN(parseFloat(accuracyValText))) {
                        analytics._rawAccuracyPercentage = parseFloat(accuracyValText);
                        // console.log("Naurok Scraper: Parsed '' (accuracy %):", analytics._rawAccuracyPercentage);
                    } else {
                        console.warn("Naurok Scraper: Could not parse float from  progress bar width:", progressBar.style.width);
                    }
                } else {
                    console.warn("Naurok Scraper: Progress bar or its width not found for ''. ValueElement was:", valueElement);
                }
            }
            // Mona doda obsug ' ' jeli jest potrzebna do czego innego
        });


        // --- Przetwarzanie wierszy bez etykiet (liczniki poprawnych/niepoprawnych, czas) ---
        // console.log("Naurok Scraper: Processing unlabeled rows...");
        const unlabeledStatRows = Array.from(quizInfoContainer.querySelectorAll('.homework-personal-stat-row'))
            .filter(row => !row.querySelector('.homework-personal-stat-label'));
        // console.log("Naurok Scraper: Found unlabeledStatRows count:", unlabeledStatRows.length);

        // Liczniki
        const countsRow = unlabeledStatRows.find(row => {
            const valuesText = Array.from(row.querySelectorAll('.homework-personal-stat-value')).map(el => el.innerText.trim()).join(' ');
            return valuesText.includes('') && valuesText.includes('');
        });
        // console.log("Naurok Scraper: countsRow found?", !!countsRow);
        if (countsRow) {
            const valueDivs = countsRow.querySelectorAll('.homework-personal-stat-value');
            const correctDiv = Array.from(valueDivs).find(d => d.innerText.includes(''));
            if (correctDiv) {
                const spanText = correctDiv.querySelector('span')?.innerText.trim();
                if (spanText && !isNaN(parseInt(spanText))) analytics.correctAnswersCount = parseInt(spanText);
            }
            const incorrectDiv = Array.from(valueDivs).find(d => d.innerText.includes(''));
            if (incorrectDiv) {
                const spanText = incorrectDiv.querySelector('span')?.innerText.trim();
                if (spanText && !isNaN(parseInt(spanText))) analytics.incorrectAnswersCount = parseInt(spanText);
            } else { analytics.incorrectAnswersCount = 0; }
            const skippedDiv = Array.from(valueDivs).find(d => d.innerText.includes(''));
            if (skippedDiv) {
                const spanText = skippedDiv.querySelector('span')?.innerText.trim();
                if (spanText && !isNaN(parseInt(spanText))) analytics.skippedAnswersCount = parseInt(spanText);
            } else { analytics.skippedAnswersCount = 0; }
        }
        // console.log(`Naurok Scraper: Counts - Correct: ${analytics.correctAnswersCount}, Incorrect: ${analytics.incorrectAnswersCount}, Skipped: ${analytics.skippedAnswersCount}`);

        // Czas
        const timeRow = unlabeledStatRows.find(row => {
            const valuesText = Array.from(row.querySelectorAll('.homework-personal-stat-value')).map(el => el.innerText.trim()).join(' ');
            return valuesText.includes(' ') && valuesText.includes('.  / ');
        });
        // console.log("Naurok Scraper: timeRow found?", !!timeRow);
        if (timeRow) {
            const totalTimeDiv = Array.from(timeRow.querySelectorAll('.homework-personal-stat-value')).find(d => d.innerText.includes(' '));
            if (totalTimeDiv) analytics.totalTime = totalTimeDiv.querySelector('span')?.innerText.trim();
            const avgTimeDiv = Array.from(timeRow.querySelectorAll('.homework-personal-stat-value')).find(d => d.innerText.includes('.  / '));
            if (avgTimeDiv) analytics.averageTimePerQuestion = avgTimeDiv.querySelector('span')?.innerText.trim();
        }
        // console.log(`Naurok Scraper: Time - Total: ${analytics.totalTime}, Avg: ${analytics.averageTimePerQuestion}`);

        if (typeof analytics.correctAnswersCount === 'number' && typeof analytics.totalQuestions === 'number' && analytics.totalQuestions > 0) {
            analytics.questionsScoreRatio = `${analytics.correctAnswersCount}/${analytics.totalQuestions}`;
        }
        // console.log("Naurok Scraper: questionsScoreRatio:", analytics.questionsScoreRatio);


        // --- Mapowanie na ustandaryzowane pola ---
        // console.log("Naurok Scraper: Attempting to map to standardized score fields. _rawScoreData:", analytics._rawScoreData, "_rawPercentageResult:", analytics._rawPercentageResult, "_rawAccuracyPercentage:", analytics._rawAccuracyPercentage);
        if (analytics._rawScoreData && typeof analytics._rawScoreData.earned === 'number' && typeof analytics._rawScoreData.total === 'number') {
            analytics.scoreEarned = analytics._rawScoreData.earned;
            analytics.scorePossible = analytics._rawScoreData.total;
        }
        if (typeof analytics._rawPercentageResult === 'number') {
            analytics.scorePercentage = analytics._rawPercentageResult;
        } else if (typeof analytics._rawAccuracyPercentage === 'number') { // Fallback na procent z ""
            analytics.scorePercentage = analytics._rawAccuracyPercentage;
        }
        if (analytics.scorePercentage === null && analytics.scoreEarned !== null && analytics.scorePossible !== null && analytics.scorePossible > 0) {
            analytics.scorePercentage = parseFloat(((analytics.scoreEarned / analytics.scorePossible) * 100).toFixed(1));
        }
        // console.log(`Naurok Scraper: Standardized - scoreEarned: ${analytics.scoreEarned}, scorePossible: ${analytics.scorePossible}, scorePercentage: ${analytics.scorePercentage}`);


        // --- Questions and Answers ---
        analytics.questions = [];
        const questionBlocks = document.querySelectorAll('.homework-stats .content-block');
        questionBlocks.forEach((block, index) => {
            const questionData = {};
            const questionLineElement = block.querySelector('.homework-stat-question-line');
            if (!questionLineElement) return;
            const questionTextElement = questionLineElement.querySelector('p');
            questionData.questionText = questionTextElement ? questionTextElement.innerText.trim() : `Pytanie ${index + 1} (brak tekstu)`;
            const fullQuestionLineText = questionLineElement.innerText.trim();
            const questionNumberMatch = fullQuestionLineText.match(/^(\d+)\./);
            questionData.questionNumber = questionNumberMatch ? parseInt(questionNumberMatch[1]) : (index + 1);
            questionData.options = [];
            const optionLines = block.querySelectorAll('.homework-stat-option-line');
            optionLines.forEach(optLine => {
                const optionValueDiv = optLine.querySelector('.homework-stat-option-value');
                if (optionValueDiv) {
                    const optionTextP = optionValueDiv.querySelector('p');
                    const optionText = optionTextP ? optionTextP.innerText.trim() : '';
                    const isCorrect = optionValueDiv.classList.contains('correct');
                    const isUserAnswer = !!optionValueDiv.querySelector('em');
                    questionData.options.push({ text: optionText, isCorrect: isCorrect, isUserAnswer: isUserAnswer });
                }
            });
            const pointsLabelElement = block.querySelector('.question-label');
            questionData.pointsAwarded = pointsLabelElement ? pointsLabelElement.innerText.trim() : 'N/A';
            questionData.isAnsweredCorrectly = block.classList.contains('success');
            analytics.questions.push(questionData);
        });

    } catch (e) {
        analytics.error = `Critical extraction error: ${e.message} at ${e.stack}`;
        // console.log("Naurok Scraper:", analytics.error);
    }

    delete analytics._rawScoreData;
    delete analytics._rawPercentageResult;
    delete analytics._rawAccuracyPercentage;

    // console.log("Naurok Scraper: Final standardized data for Naurok to be sent:", JSON.parse(JSON.stringify(analytics)));
    return analytics;
}

// Execute the function and print the results in a pretty format


function extractTestportalAnalytics() {
    const analytics = {
        platform: "Testportal",
        testTitle: null,
        resultPageUrl: window.location.href,
        respondentIdentifier: null,
        scorePercentage: null,
        pointsEarned: null,
        pointsPossible: null,
        testStatus: null, // e.g., "Test zdany", "Wynik ukryty"
        timeTaken: null,
        timeMax: null,
        startTime: null,
        endTime: null,
        date: null,
        totalQuestionsCountFromHeader: null, // From "(X)" near "Pytania"
        questions: [],
        // Fields to be added by the main content script caller
        // userEmail: null,
        // scrapedAt: null,
    };

    try {
        // --- Test Name ---
        analytics.testTitle = document.querySelector('.test-name')?.innerText.trim() || null;

        // --- Respondent Identifier ---
        // Usually in a tile with title "Rozwizujcy"
        const respondentTile = Array.from(document.querySelectorAll('.test-card')).find(card => card.querySelector('.test-page-title')?.innerText.trim() === 'Rozwizujcy');
        analytics.respondentIdentifier = respondentTile?.querySelector('.mdc-typography--headline4')?.innerText.trim() || null;

        // --- Overall Result ---
        const resultTile = Array.from(document.querySelectorAll('.result-tile .test-card')).find(card => card.querySelector('.test-page-title')?.innerText.trim() === 'Wynik');
        if (resultTile) {
            const percentageEl = resultTile.querySelector('.donut-percents');
            if (percentageEl) {
                analytics.scorePercentage = parseFloat(percentageEl.innerText.trim().replace('%', '')) || null;
            }

            const pointsEl = resultTile.querySelector('.donut-sub-value');
            if (pointsEl) {
                const pointsMatch = pointsEl.innerText.trim().match(/(\d+)\s*\/\s*(\d+)/);
                if (pointsMatch && pointsMatch.length === 3) {
                    analytics.pointsEarned = parseInt(pointsMatch[1]);
                    analytics.pointsPossible = parseInt(pointsMatch[2]);
                }
            }
            analytics.testStatus = resultTile.querySelector('.test-sheet-summary-header .mdc-typography--headline4')?.innerText.trim() || "Status nieznany";
        } else {
            // Handle cases where the entire result tile might be missing or different
            const hiddenResultMessage = Array.from(document.querySelectorAll('.test-card-body p'))
                .find(p => p.innerText.toLowerCase().includes('wynik testu jest ukryty') || p.innerText.toLowerCase().includes('wynik bdzie dostpny pniej'));
            if (hiddenResultMessage) {
                analytics.testStatus = hiddenResultMessage.innerText.trim();
            }
        }


        // --- Time Information ---
        const timerTile = Array.from(document.querySelectorAll('.timer-tile .test-card')).find(card => card.querySelector('.test-page-title')?.innerText.trim() === 'Czas');
        if (timerTile) {
            analytics.timeTaken = timerTile.querySelector('.configuration-progress__text')?.firstChild?.textContent?.trim() || null;
            analytics.timeMax = timerTile.querySelector('.configuration-progress__text-total-time')?.innerText.trim().replace('/', '').trim() || null;

            const timeProperties = timerTile.querySelectorAll('.timer-tile-properties-container .mdc-property');
            timeProperties.forEach(prop => {
                const label = prop.querySelector('.mdc-property-label')?.innerText.trim();
                const value = prop.querySelector('.mdc-property-value')?.innerText.trim();
                if (label === 'Start') analytics.startTime = value;
                if (label === 'Koniec') analytics.endTime = value;
                if (label === 'Data') analytics.date = value;
            });
        }

        // --- Total Questions Count (from header) ---
        const questionsHeaderCounter = document.querySelector('.questions-list-top-bar .test-page-title__counter');
        if (questionsHeaderCounter) {
            const match = questionsHeaderCounter.innerText.trim().match(/\((\d+)\)/);
            if (match && match[1]) {
                analytics.totalQuestionsCountFromHeader = parseInt(match[1]);
            }
        }

        // --- Individual Questions ---
        const questionContainers = document.querySelectorAll('.question-container');
        questionContainers.forEach((qContainer, index) => {
            const questionData = {
                questionNumberText: null, // e.g. "Pyt. 1"
                questionNumber: index + 1, // Fallback
                questionTextHTML: null,
                pointsEarnedForQuestion: null,
                pointsPossibleForQuestion: null,
                isAnsweredCorrectly: null, // boolean
                options: [], // For multiple-choice
                userAnswerText: null, // For short answer
                possibleCorrectAnswersText: [], // For short answer
                userSelectedOptionIds: [],
            };

            try {
                questionData.questionNumberText = qContainer.querySelector('.question_header_content')?.innerText.trim() || `Pyt. ${index + 1}`;
                const numMatch = questionData.questionNumberText.match(/Pyt\.\s*(\d+)/i);
                if (numMatch && numMatch[1]) questionData.questionNumber = parseInt(numMatch[1]);

                questionData.questionTextHTML = qContainer.querySelector('.question_essence')?.innerHTML.trim() || "";

                // Points for the question (e.g., "0/1p" or "1/1p")
                const pointsMarkerEl = qContainer.querySelector('.question-score-marker span[class*="theme-color-"]');
                if (pointsMarkerEl) {
                    const pointsText = pointsMarkerEl.innerText.trim();
                    const pointsMatch = pointsText.match(/(\d+)\s*\/\s*(\d+)/);
                    if (pointsMatch && pointsMatch.length === 3) {
                        questionData.pointsEarnedForQuestion = parseInt(pointsMatch[1]);
                        questionData.pointsPossibleForQuestion = parseInt(pointsMatch[2]);
                        questionData.isAnsweredCorrectly = (questionData.pointsEarnedForQuestion === questionData.pointsPossibleForQuestion && questionData.pointsPossibleForQuestion > 0);
                    }
                }
                // Alternative check for correctness if points are not explicit but visual icon is there
                if (questionData.isAnsweredCorrectly === null) {
                    const correctIconInHeader = qContainer.querySelector('.question-header--right-panel .question-score-marker span.theme-color-green');
                    const incorrectIconInHeader = qContainer.querySelector('.question-header--right-panel .question-score-marker span.theme-color-error');
                    if (correctIconInHeader) questionData.isAnsweredCorrectly = true;
                    else if (incorrectIconInHeader) questionData.isAnsweredCorrectly = false;
                }


                // User selected answer IDs (from question markers attribute)
                const questionMarkersDiv = qContainer.querySelector('.question-markers');
                const selectedAnswerIdsAttr = questionMarkersDiv?.getAttribute('selectedanswerids');
                if (selectedAnswerIdsAttr) {
                    try {
                        // Testportal's selectedanswerids can be a JSON string array or a single ID
                        // For safety, let's try to parse as JSON array, if fails, treat as single ID
                        let ids = [];
                        if (selectedAnswerIdsAttr.startsWith('[')) { // Check if it looks like a JSON array
                            ids = JSON.parse(selectedAnswerIdsAttr);
                        } else if (selectedAnswerIdsAttr) { // Treat as a single ID string if not an array
                            ids = [selectedAnswerIdsAttr];
                        }
                        questionData.userSelectedOptionIds = Array.isArray(ids) ? ids : (ids ? [ids] : []);
                    } catch (e) {
                        console.warn("Testportal Scraper: Could not parse selectedanswerids JSON:", selectedAnswerIdsAttr, e);
                        // If JSON parsing fails but attribute exists, treat it as a single ID string
                        if (selectedAnswerIdsAttr) questionData.userSelectedOptionIds = [selectedAnswerIdsAttr];
                    }
                }


                // Handle different question types (multiple choice vs short answer)
                const answerElements = qContainer.querySelectorAll('.answer-with-marker');
                if (answerElements.length > 0) { // Likely multiple choice
                    answerElements.forEach(ansEl => {
                        const option = {
                            text: ansEl.querySelector('.answer_body p, .answer_body img')?.outerHTML.trim() || ansEl.querySelector('.answer_body')?.innerText.trim() || "",
                            isCorrect: ansEl.querySelector('.answer_container.correct-answer-container') !== null || ansEl.querySelector('i.answer-correctness-mark.correct-answer-color, i.answer-correctness-mark svg[stroke="#0BC279"]') !== null,
                            isUserSelected: false,
                        };
                        const inputElement = ansEl.querySelector('input[type="checkbox"], input[type="radio"]');
                        if (inputElement) {
                            const answerIdFromInput = inputElement.id?.split(':')[1]; // e.g., id="answer:ID_HERE:selected"
                            if (answerIdFromInput && questionData.userSelectedOptionIds.includes(answerIdFromInput)) {
                                option.isUserSelected = true;
                            }
                        }
                        questionData.options.push(option);
                    });
                } else { // Potentially short answer
                    const providedAnswerEl = qContainer.querySelector('.provided-short-answer p');
                    if (providedAnswerEl) {
                        questionData.userAnswerText = providedAnswerEl.innerText.trim();
                    }
                    const possibleAnswerElements = qContainer.querySelectorAll('.possible-answer-option--text');
                    if (possibleAnswerElements.length > 0) {
                        possibleAnswerElements.forEach(paEl => {
                            questionData.possibleCorrectAnswersText.push(paEl.innerText.trim());
                        });
                    }
                    // Check correctness for short answer if not already set by points
                    if (questionData.isAnsweredCorrectly === null && qContainer.querySelector('.short-provided-answer-review-container i.correct-answer-color')) {
                        questionData.isAnsweredCorrectly = true;
                    } else if (questionData.isAnsweredCorrectly === null && qContainer.querySelector('.short-provided-answer-review-container i.wrong-answer-color')) {
                        questionData.isAnsweredCorrectly = false;
                    }
                }
            } catch (e) {
                console.warn(`Testportal Scraper: Error processing question ${index + 1}:`, e, qContainer);
            }
            analytics.questions.push(questionData);
        });

    } catch (error) {
        // console.log("Testportal Scraper: Critical error during data extraction:", error);
        // Optionally return partial data or a specific error object
        analytics.error = `Extraction failed: ${error.message}`;
    }

    // If totalQuestionsCountFromHeader is available and questions array length differs, log warning
    if (analytics.totalQuestionsCountFromHeader !== null && analytics.questions.length !== analytics.totalQuestionsCountFromHeader) {
        console.warn(`Testportal Scraper: Mismatch in question count. Header: ${analytics.totalQuestionsCountFromHeader}, Found: ${analytics.questions.length}`);
    }
    // Use the actual number of questions found if header count is not reliable or missing
    analytics.totalQuestionsFound = analytics.questions.length;


    // Calculate correctAnswersCount if not directly available but per-question data is
    if (analytics.pointsEarned === null && analytics.questions.length > 0) {
        let calculatedCorrect = 0;
        let calculatedPossiblePoints = 0;
        let calculatedEarnedPoints = 0;
        let allQuestionsHavePoints = true;

        analytics.questions.forEach(q => {
            if (q.isAnsweredCorrectly === true) {
                calculatedCorrect++;
            }
            if (typeof q.pointsEarnedForQuestion === 'number' && typeof q.pointsPossibleForQuestion === 'number') {
                calculatedEarnedPoints += q.pointsEarnedForQuestion;
                calculatedPossiblePoints += q.pointsPossibleForQuestion;
            } else {
                allQuestionsHavePoints = false; // If any question doesn't have points, overall score from questions is unreliable
            }
        });
        analytics.calculatedCorrectAnswersCount = calculatedCorrect;
        if (allQuestionsHavePoints && calculatedPossiblePoints > 0) { // Only set if all questions had point data
            analytics.pointsEarned = calculatedEarnedPoints;
            analytics.pointsPossible = calculatedPossiblePoints;
            if (calculatedPossiblePoints > 0) {
                analytics.scorePercentage = parseFloat(((calculatedEarnedPoints / calculatedPossiblePoints) * 100).toFixed(1));
            }
        }
    }


    return analytics;
}


// Do dodania w content_script.js
function extractVseosvitaAnalytics() {
    // console.log("Vseosvita Scraper: Starting extraction...");
    const analytics = {
        platform: "Vseosvita",
        userEmail: null, // Wypeniane przez gwn logik content_script
        scrapedAt: null, // Wypeniane przez gwn logik content_script
        pageUrl: window.location.href,

        testTitle: null,
        testAttemptId: null, // Specyficzne dla Vseosvita
        respondentIdentifier: null, // Na razie brak wyranego identyfikatora uytkownika w podsumowaniu Vseosvita

        dateTimeCompleted: null, // Sprbujemy sparsowa do ISO, jeli si da
        durationInSeconds: null, // Czas trwania w sekundach

        scoreEarned: null,          // np. 9 (z "9  12 ") - gwny wynik
        scorePossible: null,        // np. 12 (z "9  12 ") - gwny wynik
        scorePercentage: null,      // Obliczone z scoreEarned/scorePossible
        resultStatus: null,         // np. ""

        totalQuestions: null,
        correctAnswersCount: null,  // Liczba w peni poprawnych odpowiedzi
        incorrectAnswersCount: null,
        partiallyCorrectAnswersCount: 0,
        skippedAnswersCount: null,   // Obliczone, jeli to moliwe

        questions: [], // Pozostaje puste dla Vseosvita, bo nie ma szczegw pyta

        platformDetails: {
            vseosvita_dateTimeFullString: null,
            vseosvita_durationString: null,
            vseosvita_finalScoreString: null, // Oryginalny string wyniku, np. "9  12  ()"
            vseosvita_rawPointsEarned: null,  // np. 15.1 (   )
            vseosvita_rawPointsPossible: null,// np. 20 (     )
            vseosvita_pointsFromCorrect: null,// Punkty tylko z w peni poprawnych odpowiedzi
            vseosvita_partiallyCorrectDetails: [], // [{ count, pointsPerQuestion, earnedPoints, maxPointsPerQuestion }]
            vseosvita_pointsFromPartiallyCorrect: 0,
            vseosvita_gradingSystemDetails: null,
            vseosvita_finalGradeCalculationString: null,
        },
        error: null
    };

    try {
        const mainInfoContainer = document.querySelector('div.v-hello-container.type-container-vo[data-v-499098f8]');
        // console.log("Vseosvita Scraper: mainInfoContainer (with data-v) found?", !!mainInfoContainer);

        if (!mainInfoContainer) {
            // console.log("Vseosvita Scraper: Specific main info container (with data-v-499098f8) not found.");
            analytics.error = "Specific main info container not found.";
            return analytics;
        }

        // --- Test Attempt ID, Date, Time, Duration ---
        const infoParagraphs = mainInfoContainer.querySelectorAll('div.row_info-test.vertical-center p.text_info-test.silver');
        infoParagraphs.forEach(p => {
            const text = p.innerText.trim();
            if (text.toLowerCase().includes("id- :")) {
                analytics.testAttemptId = text.replace(/id- :/i, "").trim();
            } else if (text.match(/\d{1,2}\s+\S+\s+\d{4}\s+ \s+\d{2}:\d{2}/)) {
                analytics.platformDetails.vseosvita_dateTimeFullString = text.split('\n')[0].trim();
                const durationOuterSpan = p.querySelector('span[title^="  "]');
                if (durationOuterSpan) {
                    analytics.platformDetails.vseosvita_durationString = durationOuterSpan.querySelector('span')?.innerText.trim();
                } else {
                    const directDurationSpan = p.querySelector('span[data-v-499098f8] > span[data-v-499098f8]');
                    if (directDurationSpan) analytics.platformDetails.vseosvita_durationString = directDurationSpan.innerText.trim();
                }
            }
        });
        // Prba parsowania daty i czasu trwania
        if (analytics.platformDetails.vseosvita_dateTimeFullString) {
            // Prosta prba parsowania (moe wymaga bardziej zaawansowanej logiki dla ukraiskich miesicy)
            // "27  2025   22:00" - na razie nie parsujemy do ISO, bo miesice
            analytics.dateTimeCompleted = analytics.platformDetails.vseosvita_dateTimeFullString; // Store as string for now
        }
        if (analytics.platformDetails.vseosvita_durationString) {
            // "01  31 "
            const durationMatch = analytics.platformDetails.vseosvita_durationString.match(/(?:(\d+)\s*)?\s*(?:(\d+)\s*)?\s*(?:(\d+)\s*)?/);
            if (durationMatch) {
                const hours = parseInt(durationMatch[1] || 0);
                const minutes = parseInt(durationMatch[2] || 0);
                const seconds = parseInt(durationMatch[3] || 0);
                analytics.durationInSeconds = (hours * 3600) + (minutes * 60) + seconds;
            }
        }


        // --- Test Title ---
        const titlePElement = mainInfoContainer.querySelector('p.text_info-test.center.vo-mb1');
        if (titlePElement) {
            analytics.testTitle = titlePElement.querySelector('b[data-v-499098f8]')?.innerText.trim() || null;
        }

        // --- Final Score String and derived values ---
        const finalScorePElement = mainInfoContainer.querySelector('p.text_info-test.fs36.center.vo-mb2');
        if (finalScorePElement) {
            const finalScoreBElement = finalScorePElement.querySelector('b[data-v-499098f8]');
            if (finalScoreBElement) {
                analytics.platformDetails.vseosvita_finalScoreString = finalScoreBElement.innerText.trim();
                const scoreMatch = analytics.platformDetails.vseosvita_finalScoreString.match(/(\d+[\.,]?\d*)\s+\s+(\d+[\.,]?\d*)\s+(?:\s*\((.+)\))?/i);
                if (scoreMatch) {
                    analytics.scoreEarned = parseFloat(scoreMatch[1]?.replace(',', '.'));
                    analytics.scorePossible = parseFloat(scoreMatch[2]?.replace(',', '.'));
                    if (scoreMatch[3]) {
                        analytics.resultStatus = scoreMatch[3].trim();
                    }
                    if (analytics.scorePossible > 0) {
                        analytics.scorePercentage = parseFloat(((analytics.scoreEarned / analytics.scorePossible) * 100).toFixed(1));
                    }
                }
            }
        }

        // --- Table with detailed score breakdown ---
        const scoreTableContainer = mainInfoContainer.querySelector('div.table_answers-test[data-v-4b882196]');
        if (scoreTableContainer) {
            const scoreTable = scoreTableContainer.querySelector('table[data-v-4b882196]');
            if (scoreTable) {
                const rows = scoreTable.querySelectorAll('tr[data-v-4b882196]');
                rows.forEach(row => {
                    const cells = row.querySelectorAll('td[data-v-4b882196]');
                    if (cells.length < 2) return;
                    const labelElement = cells[0].querySelector('p.text_info-test > b[data-v-4b882196]');
                    const label = labelElement?.innerText.trim();
                    const valuePCell = cells[1].querySelector('p.text_info-test');

                    if (label && valuePCell) {
                        const valueText = valuePCell.innerText.trim();
                        if (label.startsWith(":")) {
                            const match = valueText.match(/(\d+)\s+/);
                            if (match) analytics.totalQuestions = parseInt(match[1]);
                        } else if (label.startsWith(":")) {
                            const match = valueText.match(/(\d+)\s+\s*x\s*[\d\.,]+\s*(?:|)?\s*=\s*([\d\.,]+)\s*/i);
                            if (match) {
                                analytics.correctAnswersCount = parseInt(match[1]);
                                analytics.platformDetails.vseosvita_pointsFromCorrect = parseFloat(match[2].replace(',', '.'));
                            }
                        } else if (label.startsWith(" :")) {
                            const partialEntries = cells[1].querySelectorAll('p.text_info-test');
                            analytics.platformDetails.vseosvita_pointsFromPartiallyCorrect = 0; // Reset
                            analytics.partiallyCorrectAnswersCount = 0; // Reset
                            partialEntries.forEach(entryP => {
                                const entryText = entryP.innerText.trim();
                                const match = entryText.match(/(\d+)\s+(?:|)\s*x\s*([\d\.,]+)\s*(?:|)?\s*=\s*([\d\.,]+)\s*(?:|)?\s*\(\s*([\d\.,]+)\s*/i);
                                if (match) {
                                    const detail = {
                                        count: parseInt(match[1]),
                                        pointsPerQuestion: parseFloat(match[2].replace(',', '.')),
                                        earnedPoints: parseFloat(match[3].replace(',', '.')),
                                        maxPointsPerQuestion: parseFloat(match[4].replace(',', '.'))
                                    };
                                    analytics.platformDetails.vseosvita_partiallyCorrectDetails.push(detail);
                                    analytics.platformDetails.vseosvita_pointsFromPartiallyCorrect += detail.earnedPoints;
                                    analytics.partiallyCorrectAnswersCount += detail.count;
                                }
                            });
                        } else if (label.startsWith(":")) {
                            const match = valueText.match(/(\d+)\s+/);
                            if (match) analytics.incorrectAnswersCount = parseInt(match[1]);
                        } else if (label.startsWith("   :")) {
                            const earnedB = valuePCell.querySelector('b[title="   "]');
                            if (earnedB) {
                                const match = earnedB.innerText.match(/([\d\.,]+)/);
                                if (match) analytics.platformDetails.vseosvita_rawPointsEarned = parseFloat(match[1].replace(',', '.'));
                            }
                            const possibleB = valuePCell.querySelector('b[title="     "]');
                            if (possibleB) {
                                const match = possibleB.innerText.match(/([\d\.,]+)/);
                                if (match) analytics.platformDetails.vseosvita_rawPointsPossible = parseFloat(match[1].replace(',', '.'));
                            }
                        } else if (label.startsWith(" :")) {
                            analytics.platformDetails.vseosvita_gradingSystemDetails = valueText;
                        } else if (label.startsWith(" :")) {
                            analytics.platformDetails.vseosvita_finalGradeCalculationString = valueText;
                        }
                    }
                });
            }
        }

        // Obliczanie skippedAnswersCount, jeli moliwe
        if (typeof analytics.totalQuestions === 'number' &&
            typeof analytics.correctAnswersCount === 'number' &&
            typeof analytics.incorrectAnswersCount === 'number' &&
            typeof analytics.partiallyCorrectAnswersCount === 'number') {
            analytics.skippedAnswersCount = analytics.totalQuestions -
                analytics.correctAnswersCount -
                analytics.incorrectAnswersCount -
                analytics.partiallyCorrectAnswersCount;
            if (analytics.skippedAnswersCount < 0) analytics.skippedAnswersCount = 0; // Sanity check
        }


    } catch (error) {
        // console.log("Vseosvita Scraper: Critical error during data extraction:", error);
        analytics.error = `Extraction failed: ${error.message}`;
    }
    // console.log("Vseosvita Scraper: Final standardized data:", JSON.parse(JSON.stringify(analytics)));
    return analytics;
}


function extractQuizizzAnalytics() {
    // console.log("Quizizz Scraper: Starting extraction...");
    const analytics = {
        platform: "Quizizz",
        userEmail: null, // Wypeniane przez gwn logik content_script
        scrapedAt: null, // Wypeniane przez gwn logik content_script
        pageUrl: window.location.href,

        testTitle: null, // Quizizz nie wywietla jednoznacznego tytuu quizu na tej stronie podsumowania; moe by w innym miejscu lub niedostpny
        quizMode: null, // np. "Recenzja solo"
        playerName: null,

        // Te pola odnosz si do "Dokadnoci" pokazywanej przez Quizizz
        accuracyPercentage: null,
        accuracyPointsEarned: null, // np. 9 z "9 / 19 pts"
        accuracyPointsPossible: null, // np. 19 z "9 / 19 pts"

        rankString: null, // np. "13 / 24"
        gameScore: null,  // Punkty w grze, np. 8960

        // Te pochodz z boksw statystyk
        totalQuestions: null, // Obliczone z correct + incorrect + partial
        correctAnswersCount: null,
        incorrectAnswersCount: null,
        partiallyCorrectAnswersCount: null, // Zazwyczaj 0 w Quizizz w tym widoku

        avgTimePerQuestionString: null, // np. "3 s"
        longestStreak: null,

        questions: [], // Bdzie zawiera ograniczon informacj o pytaniach

        platformDetails: { // Specyficzne dla Quizizz
            quizizz_gameTypeTitle: null,
            quizizz_accuracyRawString: null, // np. "9 / 19 pts"
            quizizz_motivationalMessage: null,
        },
        error: null
    };

    try {
        const mainContainer = document.querySelector('div[data-v-5e383631].screen-container');
        if (!mainContainer) {
            // console.log("Quizizz Scraper: Main screen container not found.");
            analytics.error = "Main screen container not found.";
            return analytics;
        }

        // --- Try to get Test Name if available from breadcrumbs or other sources (often not on summary screen directly)
        // This is an example if a breadcrumb existed. For now, it's often missing on the summary.
        // analytics.testTitle = document.querySelector('.breadcrumbs .quiz-title-breadcrumb')?.innerText.trim() || "Quizizz Test";


        // --- Quiz Mode ---
        analytics.platformDetails.quizizz_gameTypeTitle = mainContainer.querySelector('.game-type-title[data-cy="game-type-title"]')?.innerText.trim() || null;
        analytics.quizMode = analytics.platformDetails.quizizz_gameTypeTitle;


        // --- Player Name ---
        analytics.playerName = mainContainer.querySelector('span[data-testid="player-id-desktop"][data-cy="player-name"]')?.innerText.trim() || null;

        // --- Motivational Message ---
        analytics.platformDetails.quizizz_motivationalMessage = mainContainer.querySelector('div[data-v-b15e54fe].text-ds-light-300.font-bold.text-xl')?.innerText.trim() || null;

        // --- Accuracy Info ---
        const accuracySection = mainContainer.querySelector('div[data-v-b15e54fe].accuracy-info-section');
        if (accuracySection) {
            // Procent dokadnoci jest w tooltipie, ktry moe nie by atwy do pobrania bez interakcji.
            // Sprbujemy pobra z etykiety, jeli tam jest, lub z tooltipa, jeli jest renderowany.
            // Alternatywnie, obliczymy go, jeli mamy punkty.
            const accuracyLabelTooltip = accuracySection.querySelector('.accuracy-label-tooltip .tooltip-text[data-cy="tooltip-text"]');
            if (accuracyLabelTooltip) {
                const accText = accuracyLabelTooltip.innerText.trim().replace('%', '');
                if (!isNaN(parseFloat(accText))) {
                    analytics.accuracyPercentage = parseFloat(accText);
                }
            }

            // Punkty do dokadnoci (np. "9 / 19 pts")
            const accuracyPointsTooltipContent = accuracySection.querySelector('.accuracy-bar + .show-tooltip .content');
            if (accuracyPointsTooltipContent) { // To jest bardziej stabilny selektor dla punktw
                analytics.platformDetails.quizizz_accuracyRawString = accuracyPointsTooltipContent.innerText.trim();
                const pointsMatch = analytics.platformDetails.quizizz_accuracyRawString.match(/(\d+)\s*\/\s*(\d+)\s*pts/);
                if (pointsMatch) {
                    analytics.accuracyPointsEarned = parseInt(pointsMatch[1]);
                    analytics.accuracyPointsPossible = parseInt(pointsMatch[2]);
                    // Oblicz procent, jeli nie zosta pobrany z tooltipa
                    if (analytics.accuracyPercentage === null && analytics.accuracyPointsPossible > 0) {
                        analytics.accuracyPercentage = parseFloat(((analytics.accuracyPointsEarned / analytics.accuracyPointsPossible) * 100).toFixed(1));
                    }
                }
            }
        }


        // --- Rank and Game Score ---
        analytics.rankString = mainContainer.querySelector('.player-rank[data-cy="player-rank"]')?.innerText.trim() || null;
        const gameScoreEl = mainContainer.querySelector('.player-score[data-cy="player-score"]');
        if (gameScoreEl) {
            analytics.gameScore = parseInt(gameScoreEl.innerText.trim().replace(/\s/g, '')) || null;
        }

        // --- Stats Boxes ---
        const statsContainer = mainContainer.querySelector('.stats-container');
        if (statsContainer) {
            const correctEl = statsContainer.querySelector('.stat-box-big.correct-container .value');
            if (correctEl) analytics.correctAnswersCount = parseInt(correctEl.innerText.trim());

            const partialEl = statsContainer.querySelector('.stat-box-big.partial-container .value');
            if (partialEl) analytics.partiallyCorrectAnswersCount = parseInt(partialEl.innerText.trim());
            else analytics.partiallyCorrectAnswersCount = 0; // Quizizz rzadko uywa tego w typowych quizach

            const incorrectEl = statsContainer.querySelector('.stat-box-big.incorrect-container .value');
            if (incorrectEl) analytics.incorrectAnswersCount = parseInt(incorrectEl.innerText.trim());

            analytics.avgTimePerQuestionString = statsContainer.querySelector('.stat-box-big.avg-time-container .value')?.innerText.trim() || null;

            const streakEl = statsContainer.querySelector('.stat-box-big.streak-container .value');
            if (streakEl) analytics.longestStreak = parseInt(streakEl.innerText.trim());

            // Oblicz totalQuestions na podstawie zebranych statystyk
            if (typeof analytics.correctAnswersCount === 'number' &&
                typeof analytics.incorrectAnswersCount === 'number' &&
                typeof analytics.partiallyCorrectAnswersCount === 'number') {
                analytics.totalQuestions = analytics.correctAnswersCount + analytics.incorrectAnswersCount + analytics.partiallyCorrectAnswersCount;
            }
        }

        // --- Review Questions Section ---
        // Informacje s ograniczone w tym widoku
        const reviewList = mainContainer.querySelector('.question-review-list');
        if (reviewList) {
            const questionItems = reviewList.querySelectorAll('button.question-container');
            questionItems.forEach(item => {
                const questionData = {
                    questionNumber: null,
                    questionText: null,
                    isAnsweredCorrectlyByUser: null,
                    userTypedAnswer: null, // Jeli to pytanie otwarte
                    optionsDisplayed: [] // Jeli MCQ
                };

                const qTextElement = item.querySelector('.question-text');
                if (qTextElement) {
                    // Prba wycignicia numeru pytania z tekstu
                    const fullText = qTextElement.innerText.trim();
                    const numMatch = fullText.match(/^(\d+)\.\s*/);
                    if (numMatch) {
                        questionData.questionNumber = parseInt(numMatch[1]);
                        questionData.questionText = fullText.replace(numMatch[0], '').trim(); // Usu numer z tekstu
                    } else {
                        questionData.questionText = fullText;
                    }
                    // Mona te zapisa .innerHTML jeli potrzebne s tagi <p> wewntrz
                    // questionData.questionTextHTML = qTextElement.querySelector('p')?.innerHTML || qTextElement.innerHTML;
                }

                // Sprawd kolor ramki dla poprawnoci
                const borderColor = item.style.borderColor;
                if (borderColor === 'rgb(0, 201, 133)') { // Green
                    questionData.isAnsweredCorrectlyByUser = true;
                } else if (borderColor === 'rgb(239, 60, 105)') { // Red
                    questionData.isAnsweredCorrectlyByUser = false;
                }

                // Sprawd, czy to pytanie otwarte (typed answer)
                const typedAnswerEl = item.querySelector('.typed-answer-response .response-text span');
                if (typedAnswerEl) {
                    questionData.userTypedAnswer = typedAnswerEl.innerText.trim();
                } else {
                    // Jeli nie otwarte, to prawdopodobnie MCQ - zbierz opcje
                    const optionElements = item.querySelectorAll('.options-container .option-text p');
                    optionElements.forEach(optEl => {
                        questionData.optionsDisplayed.push(optEl.innerText.trim());
                    });
                }
                analytics.questions.push(questionData);
            });
        }
        // Jeli nie udao si wyliczy totalQuestions z boksw statystyk, a s pytania w review list:
        if (analytics.totalQuestions === null && analytics.questions.length > 0) {
            analytics.totalQuestions = analytics.questions.length;
        }


        // Ustawienie gwnych pl wyniku na podstawie "Dokadnoci" jeli dostpne
        if (analytics.accuracyPointsEarned !== null && analytics.accuracyPointsPossible !== null) {
            analytics.scoreEarned = analytics.accuracyPointsEarned;
            analytics.scorePossible = analytics.accuracyPointsPossible;
            // Procent ju powinien by ustawiony, ale dla pewnoci
            if (analytics.scorePossible > 0) {
                analytics.scorePercentage = parseFloat(((analytics.scoreEarned / analytics.scorePossible) * 100).toFixed(1));
            }
        } else if (analytics.correctAnswersCount !== null && analytics.totalQuestions !== null && analytics.totalQuestions > 0) {
            // Fallback, jeli "Dokadno" nie bya dostpna, ale mamy liczb poprawnych i wszystkich pyta
            // To jest mniej precyzyjne, bo nie uwzgldnia punktacji za pytanie
            analytics.scoreEarned = analytics.correctAnswersCount; // Zakadajc 1 pkt za pytanie
            analytics.scorePossible = analytics.totalQuestions;
            analytics.scorePercentage = parseFloat(((analytics.scoreEarned / analytics.scorePossible) * 100).toFixed(1));
        }


    } catch (error) {
        // console.log("Quizizz Scraper: Critical error during data extraction:", error);
        analytics.error = `Extraction failed: ${error.message}`;
    }
    // console.log("Quizizz Scraper: Final standardized data:", JSON.parse(JSON.stringify(analytics)));
    return analytics;
}

function extractMoodleAnalytics() {
    // console.log("Moodle Scraper: Starting extraction...");
    const analytics = {
        platform: "Moodle",
        userEmail: null, // Wypeniane przez gwn logik content_script
        scrapedAt: null, // Wypeniane przez gwn logik content_script
        pageUrl: window.location.href,

        testTitle: null,
        courseTitle: null,

        dateTimeStarted: null,
        dateTimeCompleted: null,
        duration: null, // Czas trwania jako string np. "1 day 23 hours" lub w sekundach

        scoreEarned: null,          // Z pola "Grade" / "Ocena"
        scorePossible: null,        // Z pola "Grade" / "Ocena"
        scorePercentage: null,      // Z pola "Grade" / "Ocena"
        resultStatus: null,         // np. "Finished", "Zakoczono"

        totalQuestions: null,       // Suma pyta w tecie
        correctAnswersCount: 0,     // Liczba w peni poprawnych odpowiedzi
        // incorrectAnswersCount, partiallyCorrectAnswersCount, skippedAnswersCount - mog by trudne do jednoznacznego okrelenia bez iteracji po pytaniach

        questions: [],

        platformDetails: {
            moodle_marksEarned: null, // Surowe punkty z pola "Marks" / "Punkty"
            moodle_marksPossible: null,
            moodle_rawGradeString: null, // np. "6.00 out of 10.00 (60%)"
            moodle_rawMarksString: null, // np. "3.00/5.00"
            moodle_statusString: null,
            moodle_durationString: null,
        },
        error: null
    };

    // --- Helper function to find row text by various label possibilities ---
    const MOODLE_LABELS = {
        status: ['Status', 'Stan'],
        started: ['Started', 'Rozpoczto', 'Date taken'],
        completed: ['Completed', 'Ukoczono', 'Submitted on', 'Data oddania'],
        duration: ['Duration', 'Wykorzystany czas', 'Time taken', 'Czas trwania'],
        marks: ['Marks', 'Points', 'Punkty'], // Raw score
        grade: ['Grade', 'Ocena', 'Note', 'Bewertung', 'Final grade'] // Scaled grade
    };

    function findRowTextInSummaryTable(tableBody, labelKeysOrExactLabels) {
        if (!tableBody) return null;
        const rows = Array.from(tableBody.querySelectorAll('tr'));
        for (const row of rows) {
            const th = row.querySelector('th.cell'); // Moodle often uses <th class="cell">
            if (th) {
                const headerText = th.innerText.trim();
                let found = false;
                if (Array.isArray(labelKeysOrExactLabels)) { // Array of keys from MOODLE_LABELS
                    for (const key of labelKeysOrExactLabels) {
                        if (MOODLE_LABELS[key] && MOODLE_LABELS[key].some(label => headerText.toLowerCase().startsWith(label.toLowerCase()))) {
                            found = true;
                            break;
                        }
                    }
                } else if (typeof labelKeysOrExactLabels === 'string') { // Single exact label (case insensitive start)
                    if (headerText.toLowerCase().startsWith(labelKeysOrExactLabels.toLowerCase())) {
                        found = true;
                    }
                }

                if (found) {
                    const td = row.querySelector('td.cell');
                    return td ? td.innerText.trim() : null;
                }
            }
        }
        return null;
    }

    try {
        // --- Page Title (often the quiz title) and Course Title (from breadcrumbs) ---
        analytics.testTitle = document.querySelector('#page-header h1.h2')?.innerText.trim() ||
            document.querySelector('h1#page-title')?.innerText.trim() || // Starsze motywy Moodle
            document.title.split(':')[0].trim(); // Fallback na tytu strony

        const breadcrumbItems = document.querySelectorAll('#page-navbar .breadcrumb-item a');
        if (breadcrumbItems.length > 0) {
            analytics.courseTitle = breadcrumbItems[0].innerText.trim(); // Pierwszy link w breadcrumbs to zazwyczaj kurs
            if (breadcrumbItems.length > 1 && analytics.testTitle === document.title.split(':')[0].trim()) {
                // Jeli tytu quizu nie zosta znaleziony w h1, sprbuj z breadcrumbs
                const potentialQuizTitleEl = breadcrumbItems[breadcrumbItems.length - 2]; // Przedostatni element przed "Preview" lub "Summary"
                if (potentialQuizTitleEl && !['Preview', 'Summary', 'Podgld', 'Podsumowanie'].includes(potentialQuizTitleEl.innerText.trim())) {
                    analytics.testTitle = potentialQuizTitleEl.innerText.trim();
                }
            }
        }
        // console.log("Moodle Scraper: Course Title:", analytics.courseTitle, "Test Title:", analytics.testTitle);

        // --- Summary Table ---
        const summaryTableBody = document.querySelector('table.generaltable.quizreviewsummary tbody, table.generaltable.generalbox.summarytable tbody'); // Rne klasy dla tabeli podsumowania
        // console.log("Moodle Scraper: Summary table body found?", !!summaryTableBody);

        if (summaryTableBody) {
            analytics.platformDetails.moodle_statusString = findRowTextInSummaryTable(summaryTableBody, ['status']);
            analytics.resultStatus = analytics.platformDetails.moodle_statusString; // np. "Finished"

            const startedStr = findRowTextInSummaryTable(summaryTableBody, ['started']);
            if (startedStr) analytics.dateTimeStarted = parseMoodleDate(startedStr);

            const completedStr = findRowTextInSummaryTable(summaryTableBody, ['completed']);
            if (completedStr) analytics.dateTimeCompleted = parseMoodleDate(completedStr);

            analytics.platformDetails.moodle_durationString = findRowTextInSummaryTable(summaryTableBody, ['duration']);
            analytics.duration = analytics.platformDetails.moodle_durationString; // Na razie string

            // Marks (surowe punkty) e.g., "3.00/5.00" or "3,00/5,00"
            analytics.platformDetails.moodle_rawMarksString = findRowTextInSummaryTable(summaryTableBody, ['marks']);
            if (analytics.platformDetails.moodle_rawMarksString) {
                const marksMatch = analytics.platformDetails.moodle_rawMarksString.match(/([\d\.,]+)\s*\/\s*([\d\.,]+)/);
                if (marksMatch) {
                    analytics.platformDetails.moodle_marksEarned = parseFloat(marksMatch[1].replace(',', '.'));
                    analytics.platformDetails.moodle_marksPossible = parseFloat(marksMatch[2].replace(',', '.'));
                }
            }

            // Grade (ocena skalowana) e.g., "6.00 out of 10.00 (60%)" or "6,00 na 10,00 (60%)"
            analytics.platformDetails.moodle_rawGradeString = findRowTextInSummaryTable(summaryTableBody, ['grade']);
            if (analytics.platformDetails.moodle_rawGradeString) {
                // Regex prbujcy zapa rne formaty: "X out of Y (Z%)" lub "X z Y (Z%)" lub "X na Y (Z%)"
                const gradeMatch = analytics.platformDetails.moodle_rawGradeString.match(/([\d\.,]+)\s*(?:out of|z|na)\s*([\d\.,]+)(?:\s*\(([\d\.,]+)%\))?/i);
                if (gradeMatch) {
                    analytics.scoreEarned = parseFloat(gradeMatch[1].replace(',', '.'));
                    analytics.scorePossible = parseFloat(gradeMatch[2].replace(',', '.'));
                    if (gradeMatch[3]) {
                        analytics.scorePercentage = parseFloat(gradeMatch[3].replace(',', '.'));
                    } else if (analytics.scorePossible > 0) {
                        analytics.scorePercentage = parseFloat(((analytics.scoreEarned / analytics.scorePossible) * 100).toFixed(1));
                    }
                }
            }
        } else {
            console.warn("Moodle Scraper: Summary table not found.");
        }

        // --- Individual Questions ---
        const questionElements = document.querySelectorAll('div.que'); // Gwny kontener pytania
        // console.log("Moodle Scraper: Found question elements count:", questionElements.length);
        analytics.totalQuestions = questionElements.length; // Wstpne ustawienie liczby pyta

        questionElements.forEach((qElement, index) => {
            const questionData = {
                questionNumber: index + 1,
                questionTextHTML: null,
                isAnsweredCorrectly: null,
                pointsEarned: null,
                pointsPossible: null,
                options: [],
                userAnswerText: [], // Moe by wiele odpowiedzi dla niektrych typw
                correctAnswerText: null, // Tekst z sekcji "The correct answer is:"
                platformDetails: {
                    moodle_state: null, // np. "Correct", "Incorrect", "Partially correct"
                    moodle_gradeString: null // np. "Mark 1.00 out of 1.00"
                }
            };

            try {
                const qNoElement = qElement.querySelector('.info .no .qno');
                if (qNoElement) {
                    questionData.questionNumber = parseInt(qNoElement.innerText.trim());
                }

                questionData.platformDetails.moodle_state = qElement.querySelector('.info .state')?.innerText.trim() || null;
                if (questionData.platformDetails.moodle_state) {
                    const stateLower = questionData.platformDetails.moodle_state.toLowerCase();
                    if (stateLower.includes('correct') || stateLower.includes('poprawna')) questionData.isAnsweredCorrectly = true;
                    else if (stateLower.includes('incorrect') || stateLower.includes('niepoprawna')) questionData.isAnsweredCorrectly = false;
                    else if (stateLower.includes('partially correct') || stateLower.includes('czciowo poprawna')) questionData.isAnsweredCorrectly = 'partial'; // Lub true, jeli czciowe liczymy jako "co zdoby"
                }


                questionData.platformDetails.moodle_gradeString = qElement.querySelector('.info .grade')?.innerText.trim() || null;
                if (questionData.platformDetails.moodle_gradeString) {
                    // "Mark 1.00 out of 1.00" or "Ocena 1,00 z 1,00"
                    const gradeMatch = questionData.platformDetails.moodle_gradeString.match(/([\d\.,]+)\s*(?:out of|z)\s*([\d\.,]+)/i);
                    if (gradeMatch) {
                        questionData.pointsEarned = parseFloat(gradeMatch[1].replace(',', '.'));
                        questionData.pointsPossible = parseFloat(gradeMatch[2].replace(',', '.'));
                        if (questionData.isAnsweredCorrectly === true && questionData.pointsEarned > 0) {
                            analytics.correctAnswersCount++;
                        }
                    }
                }

                questionData.questionTextHTML = qElement.querySelector('.content .formulation .qtext')?.innerHTML.trim() || null;

                // Odpowiedzi uytkownika i opcje (bardzo zaley od typu pytania)
                const answerSection = qElement.querySelector('.content .formulation .ablock, .content .formulation .answer');
                if (answerSection) {
                    // Przykadowa logika dla MCQ (radio/checkbox)
                    const choices = answerSection.querySelectorAll('input[type="radio"], input[type="checkbox"]');
                    choices.forEach(choice => {
                        const labelElement = choice.closest('div[class^="r"]'); // Kontener opcji, np. <div class="r0 incorrect">
                        let optionText = "";
                        const labelFor = document.querySelector(`label[for="${choice.id}"]`) || choice.nextElementSibling;
                        if (labelFor) {
                            // Pobierz tekst, omijajc <span class="answernumber">
                            const textContentDiv = labelFor.querySelector('.flex-fill');
                            optionText = textContentDiv ? textContentDiv.innerText.trim() : labelFor.innerText.trim().replace(/^[a-z]\.\s*/i, '').trim();
                        } else {
                            optionText = choice.value;
                        }

                        let isCorrectOption = false;
                        if (labelElement && (labelElement.classList.contains('correct') || labelElement.querySelector('i.text-success'))) {
                            isCorrectOption = true;
                        }

                        questionData.options.push({
                            text: optionText,
                            isUserSelected: choice.checked,
                            isCorrect: isCorrectOption // To jest poprawno danej opcji, niekoniecznie czy user j wybra i bya poprawna
                        });
                        if (choice.checked) {
                            questionData.userAnswerText.push(optionText);
                        }
                    });

                    // Przykadowa logika dla pyta typu "dopasuj" (select)
                    const selects = answerSection.querySelectorAll('select');
                    selects.forEach(select => {
                        const selectedOption = select.options[select.selectedIndex];
                        if (selectedOption && selectedOption.value !== "0") { // "Choose..." ma zwykle value 0
                            const questionPartText = select.closest('tr')?.querySelector('td.text')?.innerText.trim();
                            questionData.userAnswerText.push(`${questionPartText || 'Item'}: ${selectedOption.text.trim()}`);

                            const isCorrectIcon = select.nextElementSibling?.matches('i.text-success');
                            questionData.options.push({
                                text: `${questionPartText || 'Item'} -> ${selectedOption.text.trim()}`,
                                isUserSelected: true,
                                isCorrect: !!isCorrectIcon
                            });
                        }
                    });

                    // Pytania otwarte (input text/textarea)
                    const textInputs = answerSection.querySelectorAll('input[type="text"].form-control, textarea.form-control');
                    textInputs.forEach(input => {
                        questionData.userAnswerText.push(input.value.trim());
                        // Poprawno dla otwartych czsto jest tylko w 'rightanswer'
                    });

                }

                // Poprawna odpowied z sekcji feedback
                const rightAnswerElement = qElement.querySelector('.outcome .feedback .rightanswer');
                if (rightAnswerElement) {
                    // Usu pocztkowe "The correct answer is:" itp.
                    let clones = rightAnswerElement.cloneNode(true);
                    clones.querySelectorAll('p:first-child').forEach(p => {
                        if (p.innerText.toLowerCase().match(/^(the correct answer is:|poprawna odpowied to:|prawidowymi odpowiedziami s:)/i)) {
                            p.remove();
                        }
                    });
                    questionData.correctAnswerText = clones.innerText.trim().replace(/\n\s*\n/g, '\n').trim(); // Oczyszczony tekst
                }


            } catch (e) {
                console.warn(`Moodle Scraper: Error processing question ${questionData.questionNumber}:`, e, qElement);
            }
            analytics.questions.push(questionData);
        });

        // Uzupenienie `correctAnswersCount`, jeli nie zostao policzone z punktw za pytanie
        if (analytics.totalQuestions > 0 && analytics.correctAnswersCount === 0 && analytics.questions.some(q => q.isAnsweredCorrectly === true)) {
            analytics.correctAnswersCount = analytics.questions.filter(q => q.isAnsweredCorrectly === true).length;
        }


    } catch (error) {
        // console.log("Moodle Scraper: Critical error during data extraction:", error);
        analytics.error = `Extraction failed: ${error.message}`;
    }
    // console.log("Moodle Scraper: Final standardized data:", JSON.parse(JSON.stringify(analytics)));
    return analytics;
}

// Helper function to parse Moodle dates (this is a basic example, might need improvement for different locales)
function parseMoodleDate(dateString) {
    if (!dateString) return null;
    // Example: "Sunday, 25 May 2025, 10:28 PM" or "niedziela, 25 maja 2025, 22:28"
    // This is complex due to language. For now, return as string.
    // A robust solution would involve a date parsing library or more complex regex based on detected language.
    return dateString;
}

function generateQuizSummaryKey(data, platform) {
    let keyParts = [platform];

    // Try to use the most stable and unique identifiers for each platform
    switch (platform) {
        case "Naurok":
            if (data.quizTitle) keyParts.push(data.quizTitle);
            if (data.student) keyParts.push(data.student);
            if (data.dateTime) keyParts.push(data.dateTime); // e.g., ": 24.05.2024  16:39"
            if (data.score && data.score.earned !== undefined && data.score.total !== undefined) keyParts.push(`s:${data.score.earned}/${data.score.total}`);
            break;
        case "Testportal":
            if (data.testTitle) keyParts.push(data.testTitle);
            if (data.respondentIdentifier) keyParts.push(data.respondentIdentifier);
            if (data.endTime) keyParts.push(data.endTime); // e.g., "10:30"
            if (data.date) keyParts.push(data.date); // e.g., "25.05.2024"
            if (data.scorePercentage !== null) keyParts.push(`p:${data.scorePercentage}`);
            if (data.pointsEarned !== null && data.pointsPossible !== null) keyParts.push(`pts:${data.pointsEarned}/${data.pointsPossible}`);
            break;
        case "Vseosvita":
            if (data.testAttemptId) {
                keyParts.push(data.testAttemptId);
            } else {
                if (data.testTitle) keyParts.push(data.testTitle);
                if (data.dateTimeCompleted) keyParts.push(data.dateTimeCompleted); // e.g., "27  2025   22:00"
                if (data.scoreEarned !== null && data.scorePossible !== null) keyParts.push(`s:${data.scoreEarned}/${data.scorePossible}`);
            }
            break;
        case "Quizizz":
            if (data.playerName) keyParts.push(data.playerName);
            if (data.gameScore !== null) keyParts.push(`gs:${data.gameScore}`);
            if (data.quizMode) keyParts.push(data.quizMode);
            if (data.accuracyPointsEarned !== null && data.accuracyPointsPossible !== null) {
                keyParts.push(`acc:${data.accuracyPointsEarned}/${data.accuracyPointsPossible}`);
            }
            // Fallback to URL part if other identifiers are weak or missing
            if (keyParts.length < 3 && data.pageUrl) keyParts.push(data.pageUrl.split('?')[0].split('#')[0]);
            break;
        case "Moodle":
            const urlParams = new URLSearchParams(data.pageUrl.split('?')[1]);
            const attemptId = urlParams.get('attempt') || urlParams.get('cmid'); // Common Moodle params for attempt/quiz
            if (attemptId) {
                keyParts.push(`att:${attemptId}`);
            }
            // Even with attemptId, other fields help differentiate review phases
            if (data.testTitle) keyParts.push(data.testTitle);
            if (data.dateTimeCompleted) keyParts.push(data.dateTimeCompleted); // e.g., "Sunday, 25 May 2025, 10:28 PM"
            if (data.scoreEarned !== null && data.scorePossible !== null) keyParts.push(`s:${data.scoreEarned}/${data.scorePossible}`);
            break;
        default:
            // Generic fallback
            if (data.testTitle) keyParts.push(data.testTitle);
            if (data.pageUrl) keyParts.push(data.pageUrl.split('?')[0].split('#')[0]); // Base URL
            if (data.scoreEarned !== null && data.scorePossible !== null) keyParts.push(`s:${data.scoreEarned}/${data.scorePossible}`);
    }

    // If the key is still too short/generic, add more details like question count
    if (keyParts.length < 3) {
        if (data.totalQuestions !== null) keyParts.push(`tq:${data.totalQuestions}`);
        else if (data.questions && data.questions.length > 0) keyParts.push(`qcnt:${data.questions.length}`);

        if (data.questions && data.questions.length > 0 && (data.questions[0].questionText || data.questions[0].questionTextHTML)) {
            const qTextSample = String(data.questions[0].questionText || data.questions[0].questionTextHTML).substring(0, 30);
            keyParts.push(`fq:${qTextSample}`);
        }
    }

    // Normalize and sanitize the key
    return keyParts.join('|').replace(/\s+/g, '_').replace(/[^\w\|\.\-]/g, '');
}


// --- Main logic for content script ---
export async function handleUnviersalSummary(userEmail) {
    const currentUrl = window.location.href;
    let platform = null;
    let scraperFunction = null;

    // console.log("Quizonator Scraper: Content script evaluating page:", currentUrl);

    if (currentUrl.includes("naurok.com.ua/test/complete")) {
        // console.log("Quizonator Scraper: Sprawdzanie, czy URL pasuje do Naurok.");
        if (document.querySelector('.homework-result-page')) {
            platform = "Naurok";
            scraperFunction = extractNaurokAnalytics;
            // console.log("Quizonator Scraper: Wykryto platform Naurok.");
        } else {
            // console.log("Quizonator Scraper: URL pasuje do Naurok, ale brakujcy kluczowy element .homework-result-page.");
        }
    } else if (currentUrl.includes("testportal.pl/exam/test-result.html") || currentUrl.includes("testportal.net/test-result/")) {
        if (document.querySelector('.test-page-container')) {
            platform = "Testportal";
            scraperFunction = extractTestportalAnalytics;
            // console.log("Quizonator Scraper: Detected Testportal platform.");
        } else {
            // console.log("Quizonator Scraper: URL matches Testportal, but key element .test-page-container not found.");
        }
    } else if (currentUrl.includes("vseosvita.ua") && (document.querySelector('.vo-container') || document.querySelector('div.v-hello-container'))) { // Ulepszony selektor dla Vseosvita
        platform = "Vseosvita";
        scraperFunction = extractVseosvitaAnalytics;
        // console.log("Quizonator Scraper: Detected Vseosvita platform.");
    } else if (currentUrl.includes("quizizz.com") && currentUrl.includes("page=summary")) {
        if (document.querySelector('div[data-v-5e383631].screen-container') || document.querySelector('.screen-summary') || document.querySelector('.summary-container')) { // Ulepszony selektor dla Quizizz
            platform = "Quizizz";
            scraperFunction = extractQuizizzAnalytics;
            // console.log("Quizonator Scraper: Detected Quizizz platform.");
        } else {
            // console.log("Quizonator Scraper: URL matches Quizizz summary, but key elements missing.");
        }
    } else if (document.querySelector('body#page-mod-quiz-review') || (currentUrl.includes("/mod/quiz/review.php") && document.querySelector('.quizreviewsummary'))) { // Ulepszony selektor dla Moodle
        platform = "Moodle";
        scraperFunction = extractMoodleAnalytics;
        // console.log("Quizonator Scraper: Detected Moodle platform.");
    }


    if (platform && scraperFunction) {
        // console.log(`Quizonator Scraper: Attempting to extract data for ${platform} as user: ${userEmail}`);
        const extractedData = scraperFunction();

        if (extractedData && Object.keys(extractedData).length > (extractedData.platform ? 1 : 0)) {
            // pageUrl jest ju ustawiane wewntrz scraperw, ale dla pewnoci moemy je tu nadpisa/ustawi
            if (!extractedData.pageUrl) extractedData.pageUrl = currentUrl;
            extractedData.scrapedAt = new Date().toISOString();
            extractedData.userEmail = userEmail;
            // extractedData.platform powinno by ju ustawione przez funkcj scrapujc

            // *** DODANA LOGIKA DEDUPLIKACJI ***
            const summaryKey = generateQuizSummaryKey(extractedData, platform);
            const storageKey = `quizonator_sent_${summaryKey}`;

            if (sessionStorage.getItem(storageKey)) {
                // console.log(`Quizonator Scraper: Summary for key "${summaryKey}" (from ${platform}) already sent in this session. Skipping.`);
                return; // Zakocz, jeli ju wysano w tej sesji
            }
            // *** KONIEC LOGIKI DEDUPLIKACJI ***

            // console.log(`Quizonator Scraper: Data extracted for ${platform}:`, JSON.parse(JSON.stringify(extractedData))); // Logowanie kopii, aby unikn problemw z mutacj

            chrome.runtime.sendMessage({ type: "QUIZ_DATA", payload: extractedData }, (response) => {
                if (chrome.runtime.lastError) {
                    // console.log(`Quizonator Scraper: Error sending ${platform} data to background:`, chrome.runtime.lastError.message);
                } else {
                    // console.log(`Quizonator Scraper: ${platform} data sent to background, response:`, response);
                    // Oznacz jako wysane tylko po (prbie) wysania
                    sessionStorage.setItem(storageKey, 'true'); // Mona tu zapisa np. new Date().toISOString() dla wicej informacji
                    // console.log(`Quizonator Scraper: Marked summary with key "${summaryKey}" as sent in sessionStorage.`);
                }
            });
        } else {
            // console.log(`Quizonator Scraper: No data extracted for ${platform} or data was empty.`);
        }
    } else {
        // console.log("Quizonator Scraper: No known platform detected or required elements missing for URL:", currentUrl);
    }
}