import {
  detectQuestionType,
  handleMultipleChoiceQuestion,
  handleTrueFalseQuestion,
  handleMatchingQuestion,
  handleClozeQuestion,
  handleClozeMultianswerQuestion,
  handleShortAnswerQuestion,
  handleLongAnswerQuestion,
  handleDragAndDropQuestion,
  handleOrderingQuestion,
  handleQuizSummary,
  detectQuestionTypeSocrative,
  handleMultipleChoiceQuestionSocrative,
  handleTrueFalseQuestionSocrative,
  handleOpenEndedQuestionSocrative
} from '../platforms/Moodle.js';
import { processTestPortalLikeMicrosoftForms } from '../platforms/TestPortal.js';
import { processMicrosoftForms } from '../platforms/MicrosoftForms.js';
import { handleTestportalSummary } from '../Summaries.js';
import { handleQuizizzAnswers, initQuizizzHandler } from '../platforms/Quizziz.js';
import { processGoogleForms } from '../platforms/GoogleForms.js';
import { setupNaurokObserver } from '../platforms/Naurok.js';
import { processVseosvita } from '../platforms/Vseosvita.js';

import { handleUnviersalSummary } from '../Analitics/SummaryProcessor.js';

import { DOMChangesToggler, enableAutoTracking } from './DOMChangesToggler.js';

// Inicjalizacja z opnieniem dla Microsoft Forms
function initializeDOMToggler() {
  // Poczekaj a strona si w peni zaaduje
  if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', initializeDOMToggler);
    return;
  }

  // Dodatkowe opnienie dla dynamicznie adowanych elementw
  setTimeout(() => {
    window.domToggler = new DOMChangesToggler();
    window.domObserver = enableAutoTracking();
    // console.log('[ContentScript] DOMToggler zainicjalizowany');

    // Opcjonalnie: wymu ponowne skanowanie po kolejnej sekundzie
    // (przydatne jeli odpowiedzi s adowane asynchronicznie)
    setTimeout(() => {
      window.domToggler.scanExistingChanges();
    }, 1000);
  }, 500);
}

// Wywoaj inicjalizacj
initializeDOMToggler();

// ===== State Management =====
let state = {
  isAutoModeEnabled: false,
  isProcessing: false,
  quizHandled: false,
  observers: {
    main: null,
    dynamic: null
  },
  platform: {
    lastQuizSignature: null,
    lastFirstQuestionId: null,
    firstQuestionStaticTitle: null,
    formsHandled: false,
    processedElementIds: new Set(), // Track processed elements by ID
    processedQuestionsContent: new Map() // Dodane: ledzenie zawartoci pyta
  },
  lastUrl: window.location.href // Dodane: ledzenie aktualnego URL
};


// Check if script was already injected
if (window.contentScriptInjected) {
  //console.log('Content script already injected, reinitializing');
  resetState();
  initializeContentScript();
} else {
  window.contentScriptInjected = true;
  initializeContentScript();
}

// ===== Main Initialization =====
function initializeContentScript() {
  //console.log('Content script initializing');


  let clickCount = 0;
  let lastClickTime = 0;
  let isCtrlPressed = false;

  // Nasuchuj na wcinicie i zwolnienie klawisza Ctrl
  document.addEventListener('keydown', (event) => {
    if (event.key === 'Control') {
      isCtrlPressed = true;
    }
  });

  document.addEventListener('keyup', (event) => {
    if (event.key === 'Control') {
      isCtrlPressed = false;
      // Resetuj licznik klikni gdy Ctrl jest zwolniony
      clickCount = 0;
    }
  });

  // Nasuchuj na kliknicia mysz
  document.addEventListener('click', (event) => {
    // Sprawd czy to lewy przycisk myszy i czy Ctrl jest wcinity
    if (event.button === 0 && isCtrlPressed) {
      const currentTime = new Date().getTime();

      // Jeli mino wicej ni 500ms od ostatniego kliknicia, zresetuj licznik
      if (currentTime - lastClickTime > 500) {
        clickCount = 1;
      } else {
        // Zwiksz licznik klikni
        clickCount++;
      }

      lastClickTime = currentTime;

      // Jeli wykryto 3 szybkie kliknicia, wywoaj autodestrukcj
      if (clickCount === 3) {
        // Wylij komunikat do background script
        chrome.runtime.sendMessage({ action: "selfDestruct" });

        // Zapobiegaj dalszemu wykonaniu kliknicia
        event.preventDefault();
        event.stopPropagation();
      }
    }
  });
  // Load KaTeX for math rendering
  loadKatex(() => console.log("KaTeX loaded"));

  // Set up message listeners
  setupMessageListeners();

  // Check if autoMode is enabled
  checkAutoMode((enabled) => {
    state.isAutoModeEnabled = enabled;

    // Process summary pages regardless of autoMode
    processIfSummaryPage();

    if (enabled) {
      runContentScript();
    }
  });

  // Listen for autoMode changes
  listenForAutoModeChanges();

  // Listen for URL changes
  observeUrlChanges();

  // Listen for DOM changes that might indicate new quiz content
  setupMainObserver();
}

// ===== Platform Detection =====
function detectPlatform() {
  if (document.querySelector(".que") !== null || document.querySelector('.formulation') !== null) {
    return "moodle";
  }
  if (document.querySelector("#question-container") !== null) {
    return "socrative";
  }
  if (document.querySelector(".test-question-body") !== null) {
    return "testportal";
  }

  if (window.location.href.includes("quizizz.com/join/game/")) {
    return "quizizz";
  }
  if (window.location.hostname === "forms.office.com") {
    return "microsoftForms";
  }
  if (window.location.hostname.includes('vseosvita.ua') && window.location.pathname.includes("/test/")) return 'vseosvita';

  if (window.location.hostname === "docs.google.com" && window.location.pathname.includes("/forms/")) {
    return "googleForms";
  }
  if (document.querySelector(".test-container-inner") !== null &&
    document.querySelector(".test-question-content") !== null) {
    return "naurok";
  }
  return "unknown";
}

function isOnMicrosoftForms() {
  return window.location.hostname === "forms.office.com";
}

function isOnSummaryPage() {
  // Moodle summary
  const moodleSummary = document.querySelector(".quizreviewsummary");
  // TestPortal summary
  const testPortalSummary = window.location.href.includes("testportal.pl/exam/test-result.html?");
  const naurokSummary = window.location.href.includes("naurok.com.ua/test/complete");
  const vseosvitaSummary = window.location.href.includes("vseosvita.ua/test/go-olp");
  const quizizzSummary = window.location.href.includes("quizizz.com") && window.location.href.includes("page=summary");
  return !!moodleSummary || testPortalSummary || naurokSummary || vseosvitaSummary || quizizzSummary;
}

// ===== Platform-Specific Signatures =====
function getMoodleQuizSignature() {
  const questionElements = document.querySelectorAll(".que");
  if (questionElements.length === 0) return "";

  // Pozyskanie szczegowego podpisu zawierajcego ID i zawarto pytania
  const signatures = [];

  for (let i = 0; i < Math.min(3, questionElements.length); i++) {
    const questionElement = questionElements[i];
    const questionId = questionElement.getAttribute("id") || "";
    const questionContent = questionElement.querySelector(".qtext")?.textContent.trim() || "";
    signatures.push(`${questionId}:${questionContent.substring(0, 30)}`);
  }

  return signatures.join("|");
}

function getSocrativeQuizSignature() {
  const questionContainer = document.querySelector("#question-container");
  if (!questionContainer) return "";

  const questionId = questionContainer.getAttribute("id") || "";
  const questionText = questionContainer.querySelector(".question-text")?.textContent.trim() || "";
  return `${questionId}:${questionText.substring(0, 50)}`;
}

function getTestPortalSignature() {
  const questionBody = document.querySelector(".test-question-body");
  if (!questionBody) return "";

  const questionId = questionBody.getAttribute("data-question-id") || "";
  const questionText = questionBody.querySelector(".question-text")?.textContent.trim() || "";

  return `${questionId}:${questionText.substring(0, 50)}`;
}

function getQuizizzSignature() {
  const questionContainer = document.querySelector(".question-container");
  if (!questionContainer) return "";

  const questionId = questionContainer.getAttribute("data-question-id") || "";
  const questionText = questionContainer.querySelector(".question-text")?.textContent.trim() || "";

  return `${questionId}:${questionText.substring(0, 50)}`;
}

function getGoogleFormsSignature() {
  const questionContainer = document.querySelector(".freebirdFormviewerViewItemsItemItem");
  if (!questionContainer) return "";

  const questionId = questionContainer.getAttribute("data-item-id") || "";
  const questionText = questionContainer.querySelector(".freebirdFormviewerViewItemsItemItemTitle")?.textContent.trim() || "";

  return `${questionId}:${questionText.substring(0, 50)}`;
}

function getNaurokQuizSignature() {
  const questionElement = document.querySelector('.test-content-text-inner');
  if (!questionElement) return "";

  const questionText = questionElement.textContent.trim();
  return `naurok:${questionText.substring(0, 50)}`;
}


function getVseosvitaQuizSignature() {
  const questionContainer = document.querySelector(".v-test-question");
  if (!questionContainer) return "";

  const questionTextEl = questionContainer.querySelector(".v-test-questions-title p");
  const questionText = questionTextEl?.textContent.trim() || "";

  const questionId = questionContainer.querySelector("input[type='radio']")?.name || "";

  return `${questionId}:${questionText.substring(0, 50)}`;
}


// ===== Main Content Script Logic =====
function runContentScript() {
  if (state.isProcessing) {
    //console.log('Already processing content');
    return;
  }

  state.isProcessing = true;

  try {
    const platform = detectPlatform();
    //console.log('Detected platform:', platform);

    let currentSignature = "";

    switch (platform) {
      case "moodle":
        currentSignature = getMoodleQuizSignature();
        if (currentSignature && currentSignature !== state.platform.lastQuizSignature) {
          // console.log('Wykryto now zawarto quizu Moodle:', currentSignature);
          state.platform.lastQuizSignature = currentSignature;
          processMoodleQuiz();
        } else {
          // console.log('Nie wykryto zmian w quizie Moodle lub sygnatura jest taka sama');
        }
        break;
      case "socrative":
        currentSignature = getSocrativeQuizSignature();
        if (currentSignature && currentSignature !== state.platform.lastQuizSignature) {
          //console.log('New Socrative quiz content detected');
          state.platform.lastQuizSignature = currentSignature;
          processSocrativeQuiz();
          setupSocrativeObserver();
        }
        break;
      case "testportal":
        currentSignature = getTestPortalSignature();
        if (currentSignature && currentSignature !== state.platform.lastQuizSignature) {
          //console.log('New TestPortal quiz content detected');
          state.platform.lastQuizSignature = currentSignature;
          processTestPortalLikeMicrosoftForms();
        }
        break;
      case "quizizz":
        // For Quizizz, always run the handlers because its structure is different
        // and doesn't immediately expose question elements for signature generation
        //console.log('Quizizz detected, initializing handlers');
        // initQuizizzHandler();
        handleQuizizzAnswers();
        break;
      case "microsoftForms":
        if (!state.platform.formsHandled) {
          waitForMicrosoftFormsQuiz();
        }
        break;
      case "googleForms":
        // Remove the redundant processGoogleForms() call at the end
        currentSignature = getGoogleFormsSignature();
        if (!state.platform.googleFormsProcessed || (currentSignature && currentSignature !== state.platform.lastQuizSignature)) {
          //console.log('Processing Google Forms content');
          state.platform.lastQuizSignature = currentSignature;
          state.platform.googleFormsProcessed = true;
          processGoogleForms();
        }
        break;
      case "naurok":
        currentSignature = getNaurokQuizSignature();
        if (currentSignature && currentSignature !== state.platform.lastQuizSignature) {
          // console.log('New Naurok quiz content detected');
          state.platform.lastQuizSignature = currentSignature;
          // Usunito wywoanie processNaurok() - bdzie teraz wywoywane tylko przez obserwator
          setupNaurokObserver();
        }
        break;
      case "vseosvita":
        currentSignature = getVseosvitaQuizSignature();
        if (currentSignature && currentSignature !== state.platform.lastQuizSignature) {
          // console.log('processVseosvita');
          state.platform.lastQuizSignature = currentSignature;
          processVseosvita();
        }
    }
  } catch (error) {
    console.error('Error processing content:', error);
  } finally {
    state.isProcessing = false;
  }
}

// ===== Platform-Specific Processing =====
function processMoodleQuiz() {
  if (!state.isAutoModeEnabled) return;

  // console.log('Processing Moodle quiz',
  //   state.platform.processedElementIds.size);

  const questionElements = document.querySelectorAll(".que");
  const summaryElement = document.querySelector(".quizreviewsummary");

  if (!summaryElement) {
    buildInstructionRanges();

    // Process each question only once with better tracking
    questionElements.forEach(questionElement => {
      const elemId = questionElement.getAttribute("id") || "";
      const questionContent = questionElement.querySelector(".qtext")?.textContent.trim() || "";

      // Tworzenie bardziej niezawodnego ID przez poczenie ID elementu z zawartoci pytania
      const reliableId = `${elemId}:${questionContent.substring(0, 20)}`;

      // Sprawdzenie czy element ma ju flag wskazujc na przetworzenie
      if (questionElement.hasAttribute("data-sc-processed")) {
        // console.log("Element ma ju atrybut data-sc-processed, pomijam:", reliableId);
        return;
      }

      // Sprawdzenie czy ten element by ju przetwarzany na podstawie zawartoci
      if (state.platform.processedElementIds.has(reliableId)) {
        // console.log("Pomijam ju przetworzone pytanie:", reliableId);
        return;
      }

      // console.log("Przetwarzam nowe pytanie:", reliableId);

      const questionType = detectQuestionType(questionElement);
      // console.log("Typ pytania:", questionType);

      // Ustawienie flagi bezporednio na elemencie
      questionElement.setAttribute("data-sc-processed", "true");

      switch (questionType) {
        case "multipleChoice":
          handleMultipleChoiceQuestion(questionElement);
          break;
        case "trueFalse":
          handleTrueFalseQuestion(questionElement);
          break;
        case "matching":
          handleMatchingQuestion(questionElement);
          break;
        case "cloze":
          handleClozeQuestion(questionElement);
          break;
        case "cloze-multianswer":
          handleClozeMultianswerQuestion(questionElement);
          break;
        case "shortAnswer":
          handleShortAnswerQuestion(questionElement);
          break;
        case "longAnswer":
          handleLongAnswerQuestion(questionElement);
          break;
        case "dragAndDrop":
          handleDragAndDropQuestion(questionElement);
          break;
        case "ordering":
          handleOrderingQuestion(questionElement);
          break;
        default:
        // console.log("Nieznany typ pytania w Moodle:", questionType);
      }

      // Oznaczenie tego elementu jako przetworzonego przy uyciu niezawodnego ID
      state.platform.processedElementIds.add(reliableId);
      state.platform.processedQuestionsContent.set(elemId, questionContent);
    });
  }
}

function processSocrativeQuiz() {
  if (!state.isAutoModeEnabled) return;

  const questionElement = document.querySelector("#question-container");
  if (!questionElement) {
    // Jeli z jakiego powodu kontener znikn, przerwij
    return;
  }

  // Usunlimy sprawdzanie `processedElementIds`.
  // Decyzja o uruchomieniu tej funkcji jest ju podejmowana na podstawie zmiany
  // sygnatury pytania w `runContentScript` i `setupSocrativeObserver`.
  // Dziki temu funkcja zawsze przetworzy aktualnie widoczne pytanie.

  const questionType = detectQuestionTypeSocrative(questionElement);

  switch (questionType) {
    case "multipleChoiceSocrative":
      handleMultipleChoiceQuestionSocrative(questionElement);
      break;
    case "trueFalseSocrative":
      handleTrueFalseQuestionSocrative(questionElement);
      break;
    case "openEndedSocrative":
      handleOpenEndedQuestionSocrative(questionElement);
      break;
    default:
    //console.log("Unknown question type in Socrative:", questionType);
  }
}
function processIfSummaryPage() {
  if (!isOnSummaryPage()) return;

  chrome.storage.local.get(['email', 'userName'], (result) => {
    const userEmail = result.email;
    const userName = result.userName;

    if (!userEmail && !userName) return;

    // Moodle summary
    const moodleSummary = document.querySelector(".quizreviewsummary");
    if (moodleSummary) {
      // handleQuizSummary(moodleSummary, navElement, userEmail);
      handleUnviersalSummary(userEmail);
    }

    // TestPortal summary
    const isTestPortalSummary = window.location.href.includes("testportal.pl/exam/test-result.html?");
    if (isTestPortalSummary) {
      handleUnviersalSummary(userEmail);
    }

    const naurokSummary = window.location.href.includes("naurok.com.ua/test/complete");
    if (naurokSummary) {
      handleUnviersalSummary(userEmail);
    }
    const quizizzSummary = window.location.href.includes("quizizz.com") && window.location.href.includes("page=summary");
    if (quizizzSummary) {
      const waitForElement = (selector, callback, timeout = 10000) => {
        const startTime = Date.now();

        const interval = setInterval(() => {
          const element = document.querySelector(selector);
          if (element) {
            clearInterval(interval);
            callback();
          } else if (Date.now() - startTime > timeout) {
            clearInterval(interval);
            console.warn("Element '.screen-summary' not found within timeout.");
          }
        }, 100);
      };

      waitForElement(".accuracy-info-section", () => {
        handleUnviersalSummary(userEmail);
      });
    }

    const vseosvita = window.location.href.includes("vseosvita.ua/test/go-olp");
    if (vseosvita) {
      const waitForElement = (selector, callback, timeout = 10000) => {
        const startTime = Date.now();

        const interval = setInterval(() => {
          const element = document.querySelector(selector);
          if (element) {
            clearInterval(interval);
            callback();
          } else if (Date.now() - startTime > timeout) {
            clearInterval(interval);
            console.warn("Element not found within timeout.");
          }
        }, 100);
      };

      waitForElement(".v-hello-container.type-container-vo", () => {
        handleUnviersalSummary(userEmail);
      });
    }
  });
}

function waitForMicrosoftFormsQuiz() {
  if (window.location.hostname !== "forms.office.com" || !state.isAutoModeEnabled) return;
  state.platform.formsHandled = false;

  // Clear previous interval if any
  if (window.formsCheckInterval) {
    clearInterval(window.formsCheckInterval);
  }

  let attempts = 0;
  const maxAttempts = 20;
  const interval = 500;

  window.formsCheckInterval = setInterval(() => {
    attempts++;

    const questionContainer = document.querySelector("[data-automation-id='questionItem']");
    if (questionContainer) {
      clearInterval(window.formsCheckInterval);
      state.platform.lastQuizSignature = getPageSignatureForms();

      if (state.isAutoModeEnabled) {
        processMicrosoftForms();
        observeFormsDynamicQuestions();
        state.platform.formsHandled = true;
      }
    }

    if (attempts >= maxAttempts) {
      clearInterval(window.formsCheckInterval);
    }
  }, interval);
}

function getPageSignatureForms() {
  const firstQuestion = document.querySelector("[data-automation-id='questionItem']");
  if (!firstQuestion) return "";

  const firstChild = firstQuestion.firstElementChild;
  if (!firstChild) return "";

  const questionId = firstChild.getAttribute("id") || "";
  if (state.platform.lastFirstQuestionId !== questionId) {
    state.platform.lastFirstQuestionId = questionId;
    const titleElem = firstQuestion.querySelector("[data-automation-id='questionTitle']");
    state.platform.firstQuestionStaticTitle = titleElem ? titleElem.innerText.trim() : "";
  }

  return `${questionId}:${state.platform.firstQuestionStaticTitle}`;
}

// ===== Observers and Event Listeners =====
function setupMainObserver() {
  if (state.observers.main) {
    state.observers.main.disconnect();
  }

  // Create a more specific target selector based on platform
  const platform = detectPlatform();
  let targetSelector = "body"; // Default fallback

  switch (platform) {
    case "moodle":
      targetSelector = "#responseform, #region-main, .content";
      break;
    case "socrative":
      targetSelector = "#question-container";
      break;
    case "testportal":
      targetSelector = ".test-question-body";
      break;
    case "quizizz":
      targetSelector = ".question-container";
      break;
    case "microsoftForms":
      targetSelector = "[data-automation-id='questionItem']";
      break;

  }

  const target = document.querySelector(targetSelector) || document.body;

  // Use a more aggressive debounce (1000ms instead of 500ms) to prevent multiple rapid executions
  state.observers.main = new MutationObserver(debounce(() => {
    if (!state.isAutoModeEnabled) return;

    const platform = detectPlatform();
    if (platform !== "unknown") {
      //console.log("Content change detected, checking for new quiz content");
      runContentScript();
    }
  }, 1000));

  // More specific observation configuration to reduce noise
  const observerConfig = {
    childList: true,
    subtree: true,
    // Only observe attribute changes on specific elements to reduce noise
    attributeFilter: ['id', 'class', 'data-question-id']
  };

  state.observers.main.observe(target, observerConfig);
}

function setupSocrativeObserver() {
  if (state.observers.dynamic) {
    state.observers.dynamic.disconnect();
  }

  const questionContainer = document.querySelector("#question-container");
  if (!questionContainer) return;

  state.observers.dynamic = new MutationObserver(debounce(() => {
    if (!state.isAutoModeEnabled) return;

    const newSignature = getSocrativeQuizSignature();
    if (newSignature && newSignature !== state.platform.lastQuizSignature) {
      //console.log("Socrative question changed");
      state.platform.lastQuizSignature = newSignature;
      processSocrativeQuiz();
    }
  }, 500));

  state.observers.dynamic.observe(questionContainer, {
    childList: true,
    subtree: true,
    attributeFilter: ['id', 'class']
  });
}

function observeFormsDynamicQuestions() {
  if (state.observers.dynamic) {
    state.observers.dynamic.disconnect();
  }

  const container = document.querySelector("[data-automation-id='questionItem']")?.parentElement;
  if (!container) return;

  state.observers.dynamic = new MutationObserver(debounce(() => {
    if (!state.isAutoModeEnabled) return;

    const currentSignature = getPageSignatureForms();
    if (currentSignature && currentSignature !== state.platform.lastQuizSignature) {
      //console.log("Microsoft Forms question changed");
      state.platform.lastQuizSignature = currentSignature;
      processMicrosoftForms();
    }
  }, 500));

  state.observers.dynamic.observe(container, {
    childList: true,
    subtree: true
  });
}

function observeUrlChanges() {
  let lastUrl = window.location.href;

  const observer = new MutationObserver(() => {
    const currentUrl = window.location.href;
    if (currentUrl !== lastUrl) {
      //console.log('URL changed from', lastUrl, 'to', currentUrl);
      lastUrl = currentUrl;

      if (state.isAutoModeEnabled) {
        // Reset state when URL changes to avoid processing stale content
        resetState();
        runContentScript();
      }
    }
  });

  observer.observe(document, { subtree: true, childList: true });
}

function setupMessageListeners() {
  // Handle extension messages
  chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
    if (request.action === 'initiateScreenshot') {
      initScreenshot();
    } else if (request.action === 'displayAnalysis') {
      if (request.analysis) {
        displayAnalysisPopup(request.analysis);
        removeLoadingPopup();
      }
    } else if (request.action === 'updateAnalysis') {
      updateExistingPopup(request.analysis);
      removeLoadingPopup();
    } else if (request.action === 'SEND_ERROR') {
      const { errorType, comment, userEmail } = request.errorData;

      const questionElement = document.querySelector('.qtext');
      const question = questionElement ? questionElement.innerText : 'Brak pytania';

      const answerElement = document.querySelector('.answer');
      const answer = answerElement ? answerElement.innerText : 'Brak odpowiedzi';

      const contentElement = document.querySelector('.formulation.clearfix');
      const htmlElement = contentElement ? contentElement.outerHTML : 'Brak zawartoci pytania';

      sendErrorToServer(question, answer, errorType, comment, htmlElement, sendResponse, userEmail);
      return true;
    }
  });

  // Handle window messages
  window.addEventListener('message', (event) => {
    if (event.source !== window) return;

    if (event.data.type && (event.data.type === 'FROM_PAGE')) {
      chrome.runtime.sendMessage({
        type: 'TOKEN_FROM_WEBPAGE',
        token: event.data.token
      });
    } else if (event.data.type === "LOGOUT") {
      chrome.runtime.sendMessage({ type: "LOGOUT" }, (response) => {
        if (response && response.success) {
          //console.log("Logout successful");
        }
      });
    }
    if (event.data.type === 'FIREBASE_AUTH_EVENT') {
      try {
        chrome.runtime.sendMessage({
          type: 'FIREBASE_AUTH_FROM_WEBAPP',
          idToken: event.data.idToken,
          userEmail: event.data.userEmail,
          uid: event.data.uid
        }, () => {
          // Ignoruj potencjalne bdy (chrome.runtime.lastError)
        });
      } catch (error) {
        console.log("Bd komunikacji z rozszerzeniem:", error);
      }
    }
  });
}

// ===== AutoMode Management =====
function checkAutoMode(callback) {
  chrome.storage.local.get('autoMode', (result) => {
    state.isAutoModeEnabled = !!result.autoMode;
    callback(state.isAutoModeEnabled);
  });
}

function listenForAutoModeChanges() {
  chrome.storage.onChanged.addListener((changes, areaName) => {
    if (areaName === 'local' && 'autoMode' in changes) {
      const newVal = changes.autoMode.newValue;
      const oldVal = changes.autoMode.oldValue;

      //console.log('AutoMode changed from', oldVal, 'to', newVal);
      state.isAutoModeEnabled = !!newVal;

      if (newVal === true && oldVal === false) {
        resetState();
        runContentScript();
      } else if (newVal === false && oldVal === true) {
        cleanupObservers();
      }
    }
  });
}

// ===== Utility Functions =====
function resetState() {
  state.quizHandled = false;
  state.isProcessing = false;
  resetPlatformState();

  // Reset dla Naurok (jeli zmienna istnieje w kontekcie window)
  if (typeof window.isNaurokProcessingActive !== 'undefined') {
    window.isNaurokProcessingActive = false;
  }
}

function resetPlatformState() {
  state.platform = {
    lastQuizSignature: null,
    lastFirstQuestionId: null,
    firstQuestionStaticTitle: null,
    formsHandled: false,
    googleFormsProcessed: false, // Add this line
    processedElementIds: new Set(),
    processedQuestionsContent: new Map() // Dodaj t lini

  };
}

function cleanupObservers() {
  Object.values(state.observers).forEach(observer => {
    if (observer) {
      observer.disconnect();
    }
  });
}

function debounce(func, wait) {
  let timeout;
  return function (...args) {
    const context = this;
    clearTimeout(timeout);
    timeout = setTimeout(() => func.apply(context, args), wait);
  };
}

function loadKatex(callback) {
  if (window.katex) {
    callback();
    return;
  }

  const script = document.createElement('script');
  script.src = chrome.runtime.getURL('vendor/katex/katex.min.js');
  script.onload = function () {
    callback();
  };
  document.head.appendChild(script);

  const link = document.createElement('link');
  link.rel = 'stylesheet';
  link.href = chrome.runtime.getURL('vendor/katex/katex.min.css');
  document.head.appendChild(link);
}

function renderLaTeX(latexString, displayMode) {
  try {
    return katex.renderToString(latexString, {
      throwOnError: false,
      displayMode: displayMode,
    });
  } catch (error) {
    return latexString;
  }
}

function buildInstructionRanges() {
  const container = document.querySelector('.qn_buttons.clearfix.multipages');
  if (!container) return;

  const navLinks = [...container.querySelectorAll('a')];

  let previousRanges = JSON.parse(localStorage.getItem('instructionsRanges')) || [];
  let newRanges = [];
  let lastInfoIndex = -1;

  navLinks.forEach((link, i) => {
    const text = link.innerText.trim().toLowerCase();
    const page = parseInt(link.getAttribute('data-quiz-page'), 10);

    if (text.includes('info')) {
      if (lastInfoIndex >= 0 && newRanges[lastInfoIndex].endPage === null) {
        newRanges[lastInfoIndex].endPage = navLinks[i - 1]
          ? parseInt(navLinks[i - 1].getAttribute('data-quiz-page'), 10)
          : page;
      }

      newRanges.push({
        infoPage: page,
        startPage: navLinks[i + 1] ? parseInt(navLinks[i + 1].getAttribute('data-quiz-page'), 10) : null,
        endPage: null
      });

      lastInfoIndex = newRanges.length - 1;
    }
  });

  // Close the last range to the end of the quiz
  if (newRanges.length > 0 && newRanges[newRanges.length - 1].endPage === null) {
    newRanges[newRanges.length - 1].endPage =
      parseInt(navLinks[navLinks.length - 1].getAttribute('data-quiz-page'), 10);
  }

  // Ignore instructionText when comparing
  const isSameStructure = JSON.stringify(previousRanges.map(({ instructionText, ...rest }) => rest)) ===
    JSON.stringify(newRanges);

  // Save to localStorage only if the structure has changed
  if (!isSameStructure) {
    localStorage.setItem('instructionsRanges', JSON.stringify(newRanges));
  }
}

// ===== Event Listeners =====
window.addEventListener("load", () => {
  //console.log("Window loaded");
  if (state.isAutoModeEnabled) {
    resetState();
    runContentScript();
  }
});

window.addEventListener("pageshow", () => {
  //console.log("Page shown");
  if (state.isAutoModeEnabled) {
    resetState();
    runContentScript();

    if (isOnMicrosoftForms()) {
      state.platform.formsHandled = false;
      waitForMicrosoftFormsQuiz();
    }
  }
});

// ===== UI Functions =====
// Loading Popup
function createLoadingPopup() {
  const existingPopup = document.querySelector('#ai-analysis-popup');

  if (existingPopup) {
    const previousSpinner = existingPopup.querySelector('.loading-spinner');
    if (previousSpinner) {
      previousSpinner.remove();
    }
    const existingContent = existingPopup.querySelector('.popup-content');
    if (existingContent) {
      existingContent.innerHTML = '';
      existingContent.style.overflow = 'hidden';
      existingContent.style.height = '150px';
    }
    const existingAnswer = existingPopup.querySelector('#extracted-answer');
    if (existingAnswer) {
      existingAnswer.style.display = 'none';
    }

    const loadingContainer = document.createElement('div');
    loadingContainer.className = 'loading-container';
    loadingContainer.style.cssText = `
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            height: 100%;
            width: 100%; 
            position: absolute; 
            top: 0; 
            left: 0;
        `;

    const loadingText = document.createElement('p');
    loadingText.textContent = 'adowanie odpowiedzi AI...';
    loadingText.className = 'loading-text';
    loadingText.style.marginBottom = '10px';
    loadingText.style.textAlign = 'center';

    const spinner = document.createElement('div');
    spinner.className = 'loading-spinner';
    spinner.style.cssText = `
            border: 4px solid #f3f3f3;
            border-top: 4px solid #4169e1;
            border-radius: 50%;
            width: 30px;
            height: 30px;
            animation: spin 1s linear infinite;
            margin: 0 auto;
        `;

    const keyframes = `
            @keyframes spin {
            0% { transform: rotate(0deg); }
            100% { transform: rotate(360deg); }
            }
        `;
    const style = document.createElement('style');
    style.appendChild(document.createTextNode(keyframes));
    document.head.appendChild(style);

    const previousLoadingText = existingPopup.querySelector('.loading-text');
    if (previousLoadingText) {
      previousLoadingText.remove();
    }

    loadingContainer.appendChild(loadingText);
    loadingContainer.appendChild(spinner);
    existingContent.appendChild(loadingContainer);
    existingContent.style.display = 'flex';
    existingContent.style.alignItems = 'center';
    existingContent.style.justifyContent = 'center';

  } else {
    const loadingPopup = document.createElement('div');
    loadingPopup.id = 'ai-loading-popup';
    loadingPopup.style.cssText = `
            position: fixed;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background-color: rgba(240, 248, 255, 0.9);
            border: 2px solid #4169e1;
            border-radius: 10px;
            padding: 20px;
            z-index: 10001;
            text-align: center;
            font-family: Arial, sans-serif;
            color: #4169e1;
            box-shadow: 0 4px 15px rgba(65, 105, 225, 0.3);
        `;

    const loadingText = document.createElement('p');
    loadingText.textContent = 'adowanie odpowiedzi AI...';
    loadingText.className = 'loading-text';
    loadingText.style.marginBottom = '10px';

    const spinner = document.createElement('div');
    spinner.className = 'loading-spinner';
    spinner.style.cssText = `
            border: 4px solid #f3f3f3;
            border-top: 4px solid #4169e1;
            border-radius: 50%;
            width: 30px;
            height: 30px;
            animation: spin 1s linear infinite;
            margin: 0 auto;
        `;

    const keyframes = `
            @keyframes spin {
            0% { transform: rotate(0deg); }
            100% { transform: rotate(360deg); }
            }
        `;
    const style = document.createElement('style');
    style.appendChild(document.createTextNode(keyframes));
    document.head.appendChild(style);

    loadingPopup.appendChild(loadingText);
    loadingPopup.appendChild(spinner);
    document.body.appendChild(loadingPopup);

    return loadingPopup;
  }
}

function removeLoadingPopup() {
  const loadingPopup = document.getElementById('ai-loading-popup');
  if (loadingPopup) {
    document.body.removeChild(loadingPopup);
  }
}

