import { sendQuestionToChatGPT } from '../core/api.js';  // Import funkcji z api.js
function handleInformationQuestion(questionElement) {
    const currentPage = getCurrentPageNumber();

    // Odczytaj tekst
    const qtextElem = questionElement.querySelector(".qtext");
    const infoText = qtextElem ? qtextElem.innerText.trim() : "";

    // Wczytaj aktualny instructionsRanges z localStorage
    let instructionsRanges = JSON.parse(localStorage.getItem('instructionsRanges')) || [];

    // Znajd obiekt, ktry ma infoPage = currentPage
    const idx = instructionsRanges.findIndex(r => r.infoPage === currentPage);
    if (idx !== -1) {
        // Nadpisz/uzupenij pole instructionText
        instructionsRanges[idx].instructionText = infoText;
    }

    // Zapisz z powrotem do localStorage
    localStorage.setItem('instructionsRanges', JSON.stringify(instructionsRanges));
}

function getCurrentPageNumber() {
    // Pobieramy fragment "?attempt=...&cmid=...&page=1"
    const urlParams = new URLSearchParams(window.location.search);
    // Sprawdzamy, czy jest parametr 'page'
    const pageParam = urlParams.get('page');
    // Jeeli nie ma go w URL, przyjmujemy 0
    const pageNumber = pageParam !== null ? parseInt(pageParam, 10) : 0;
    // Dodatkowo, dla bezpieczestwa:
    return isNaN(pageNumber) ? 0 : pageNumber;
}

function getLocalizedText(textKey) {
    // Get browser language
    const browserLang = navigator.language || navigator.userLanguage;
    const lang = browserLang.split('-')[0]; // Get main language code (e.g., "pl" from "pl-PL")

    // Dictionary of translations for various text elements
    const translations = {
        'correctAnswer': {
            'pl': 'Poprawna odpowied:',
            'en': 'Correct answer:',
            'ru': ' :',
            'uk': ' :',
            // Default to English if language not found
            'default': 'Correct answer:'
        }
    };

    // Return translation for detected language or default to English
    const textDict = translations[textKey] || {};
    return textDict[lang] || textDict['default'] || textKey;
}

// Funkcja do rozpoznawania typu pytania
export function detectQuestionType(questionElement) {
    handleInformationQuestion(questionElement)

    if (questionElement.closest('.que.multichoice') || questionElement.closest('.que.calculatedmulti')) {
        return "multipleChoice";
    }
    if (questionElement.closest(".que.truefalse")) {
        return "trueFalse";
    }
    if (questionElement.closest(".que.match")) {
        return "matching";
    }
    if (questionElement.closest(".que.gapselect")) {
        return "cloze";
    }
    if (questionElement.closest(".que.multianswer")) {
        return "cloze-multianswer";
    }
    if (questionElement.closest('.que.shortanswer') || questionElement.closest('.que.numerical') || questionElement.closest('.que.calculated')) {
        return "shortAnswer";
    }
    if (questionElement.querySelector(".qtype_essay_editor") ||
        questionElement.querySelector("textarea.qtype_essay_plain") ||
        questionElement.querySelector("que.essay")) {
        return "longAnswer";
    }
    if (questionElement.querySelector(".sortablelist")) {
        return "ordering";
    }
    if (questionElement.querySelector(".answercontainer .draghome")) {
        return "dragAndDrop";
    }
    return "unknown";
}
// Funkcja rozpoznajca typ pytania dla Socrative
export function detectQuestionTypeSocrative(questionElement) {
    if (questionElement.querySelector('#question-container .mc-question')) {
        return "multipleChoiceSocrative";
    }
    if (questionElement.querySelector('#question-container .tf-question')) {
        return "trueFalseSocrative";
    }
    if (questionElement.querySelector('#question-container .fr-question')) {
        return "openEndedSocrative";
    }
    return "unknown";
}




