// Funkcja do przetwarzania pytania wielokrotnego wyboru na platformie Socrative
export async function handleMultipleChoiceQuestionSocrative(questionElement) {
    const questionText = await extractMultipleChoiceQuestionTextSocrative(questionElement);
    const possibleAnswers = await extractMultipleChoiceAnswersSocrative(questionElement);

    sendQuestionToChatGPT(questionText, possibleAnswers, "multipleChoice", 1, [], false, [])
        .then(response => {
            markCorrectAnswerForMultipleChoiceSocrative(questionElement, response);

        })
        .catch(error => {
            console.error("Error processing question with ChatGPT:", error);
        });
}

// Funkcja do wycigania tekstu pytania dla platformy Socrative
async function extractMultipleChoiceQuestionTextSocrative(questionElement) {
    const questionTextElement = questionElement.querySelector(".question-text pre");
    return questionTextElement ? questionTextElement.innerText.trim() : "";
}

// Funkcja do wycigania odpowiedzi dla platformy Socrative
async function extractMultipleChoiceAnswersSocrative(questionElement) {
    const answerElements = questionElement.querySelectorAll(".mc-answer-option");
    const answers = [];

    for (let el of answerElements) {
        const answerTextElement = el.querySelector(".mc-answer-option-text pre");
        if (answerTextElement) {
            const answerText = answerTextElement.innerText.trim();
            if (answerText && !answers.includes(answerText)) {
                answers.push(answerText);
            }
        }
    }
    return answers;
}


// Funkcja do zaznaczania poprawnej odpowiedzi na platformie Socrative
function markCorrectAnswerForMultipleChoiceSocrative(questionElement, correctAnswer) {
    const answerElements = questionElement.querySelectorAll(".mc-answer-option");
    const correctIndex = parseInt(correctAnswer.trim()) - 1;

    chrome.storage.local.get(['markingMode'], (result) => {
        const markingMode = result.markingMode || 'default';

        if (correctIndex >= 0 && correctIndex < answerElements.length) {
            const correctAnswerElement = answerElements[correctIndex];
            const answerContainer = correctAnswerElement.querySelector(".mc-answer-option-container");
            const answerTextElement = correctAnswerElement.querySelector(".mc-answer-option-text pre");

            if (markingMode === 'immediate') {
                answerTextElement.click();
            } else if (markingMode === 'saveMode') {
                if (answerTextElement) {
                    answerTextElement.style.fontWeight = 'normal';
                    answerTextElement.innerHTML = `<span style="font-weight: 600;">${answerTextElement.innerText[0]}</span>${answerTextElement.innerText.slice(1)}`;
                }
            } else {
                if (answerTextElement) {
                    answerTextElement.style.fontWeight = 'bold';
                    answerTextElement.style.color = '#4CAF50';
                }
            }
        } else {
            console.warn("Niepoprawny numer odpowiedzi:", correctAnswer);
        }
    });
}


