// Funkcja do wypeniania odpowiedzi dla pyta otwartych na platformie Socrative
export function markCorrectAnswerForOpenEndedSocrative(questionElement, answerText) {
    const answerTextarea = questionElement.querySelector(".fr-question-textarea");

    if (answerTextarea) {
        chrome.storage.local.get(['markingMode'], (result) => {
            const markingMode = result.markingMode || 'default';
            answerTextarea.value = answerText;
            if (markingMode === 'immediate') {
                answerTextarea.dispatchEvent(new Event('input', { bubbles: true }));
            } else if (markingMode === 'saveMode') {

                answerTextarea.dispatchEvent(new Event('input', { bubbles: true }));
            } else {
                answerTextarea.dispatchEvent(new Event('input', { bubbles: true }));
            }

            answerTextarea.addEventListener('focus', () => {
                answerTextarea.style.color = '';
                answerTextarea.style.fontWeight = '';
            });
        });
    } else {
        console.warn("Answer textarea not found for open-ended question.");
    }
}




function getInstructionForPage(page) {
    const instructionsRanges = JSON.parse(localStorage.getItem('instructionsRanges')) || [];
    const found = instructionsRanges.find(r =>
        r.startPage !== null &&
        r.endPage !== null &&
        page >= r.startPage &&
        page <= r.endPage
    );
    return found ? found.instructionText : "";
}

function formatPromptWithInstruction(questionText, instructionText, possibleAnswers) {
    return `
    Instructions: ${instructionText || '(brak)'}
    Question: ${questionText}
    Choices: ${possibleAnswers ? possibleAnswers.join(' | ') : '(brak)'}
    `;
}



function waitForMathJax() {
    return new Promise((resolve) => {
        if (typeof MathJax === 'undefined') {
            console.warn('MathJax is not defined, proceeding without waiting.');
            resolve();
            return;
        }

        MathJax.Hub.Queue(() => {
            resolve();
        });
    });
}

async function convertMathJaxToText(element) {
    // Pracujemy na kopii, aby nie modyfikowa oryginalnej strony
    const clone = element.cloneNode(true);

    // Krok 1: Znajd wszystkie skrypty z kodem LaTeX i zastp je czyteln dla czowieka form.
    // MathJax przechowuje wzory w takich skryptach.
    const scripts = clone.querySelectorAll('script[type="math/tex"], script[type="math/tex; mode=display"]');
    scripts.forEach(script => {
        const latex = script.textContent.trim();
        // Zastpujemy cay element-rodzic skryptu (np. <span class="MathJax_Preview">...</span>)
        // samym kodem LaTeX otoczonym w nawiasy, aby by atwy do odczytania przez AI.
        if (script.parentElement) {
            script.parentElement.replaceWith(document.createTextNode(` \\(${latex}\\) `));
        } else {
            script.remove();
        }
    });

    // Krok 2: Usu ewentualne pozostaoci po MathJax, ktre nie zawieraj treci
    const previews = clone.querySelectorAll('.MathJax_Preview, .MJX_Assistive_MathML');
    previews.forEach(el => el.remove());

    // Krok 3: Zwr czysty tekst z przetworzonego elementu.
    // .innerText jest inteligentniejszy ni .textContent i lepiej oddaje formatowanie.
    return clone.innerText.trim().replace(/\s+/g, ' ');
}

async function extractImageUrlsFromQuestion(questionElement) {
    const formulationElement = questionElement.querySelector('.formulation');
    const imageElements = formulationElement.querySelectorAll("img");
    const imageBase64 = [];

    for (const img of imageElements) {
        const base64 = await convertImageToBase64(img);
        imageBase64.push(base64);
    }

    return imageBase64;
}

function convertImageToBase64(img) {
    return new Promise((resolve, reject) => {
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');
        canvas.width = img.width;
        canvas.height = img.height;
        ctx.drawImage(img, 0, 0);

        canvas.toBlob((blob) => {
            const reader = new FileReader();
            reader.onloadend = () => {
                resolve(reader.result);
            };
            reader.onerror = () => {
                reject("Error converting image to base64.");
            };
            reader.readAsDataURL(blob);
        }, 'image/png');
    });
}

export async function handleMultipleChoiceQuestion(questionElement) {

    const questionText = await extractMultipleChoiceQuestionText(questionElement);
    const possibleAnswers = await extractMultipleChoiceAnswers(questionElement);
    const imageBase64Strings = await extractImageUrlsFromQuestion(questionElement);
    const isMultipleCorrect = questionElement.querySelectorAll("input[type='checkbox']").length > 1;

    // 1) pobierz numer aktualnej strony z URL lub w inny sposb
    const currentPage = getCurrentPageNumber();

    // 2) znajd instrukcj w localStorage
    const instructionText = getInstructionForPage(currentPage);

    // 3) zbuduj prompt (np. scalamy instructionText i questionText)
    // Moe by w formie: "Instrukcja:\n${instructionText}\nPytanie:\n${questionText}"
    const combinedPrompt = formatPromptWithInstruction(questionText, instructionText, possibleAnswers);

    sendQuestionToChatGPT(combinedPrompt, possibleAnswers, "multipleChoice", 1, [], isMultipleCorrect, imageBase64Strings)
        .then(response => {
            markCorrectAnswerForMultipleChoice(questionElement, response);
        })
        .catch(error => {
            console.error("Error processing question with ChatGPT:", error);
        });
}

async function extractMultipleChoiceQuestionText(questionElement) {
    const questionTextElement = questionElement.querySelector(".qtext");
    return questionTextElement ? await convertMathJaxToText(questionTextElement) : "";
}

async function extractMultipleChoiceAnswers(questionElement) {
    const answerElements = questionElement.querySelectorAll(".answer > div, .answer .r0, .answer .r1");
    const answers = [];

    for (let el of answerElements) {
        let processedText = "";

        const answerLabelElement = el.querySelector("div[data-region='answer-label'], label");

        if (answerLabelElement) {
            const pElements = answerLabelElement.querySelectorAll("p");
            const preElements = answerLabelElement.querySelectorAll("pre");
            const divElements = answerLabelElement.querySelectorAll("div");

            if (pElements.length > 0) {
                processedText = Array.from(pElements)
                    .map(node => node.textContent.trim())
                    .join(" ");
            } else if (preElements.length > 0) {
                processedText = Array.from(preElements)
                    .map(node => node.textContent.trim())
                    .join(" ");
            } else if (divElements.length > 0) {
                processedText = Array.from(divElements)
                    .map(node => node.textContent.trim())
                    .join(" ");
            }

            processedText = processedText.trim();

            if (processedText && !answers.includes(processedText)) {
                answers.push(processedText);
            }
        }
    }
    return answers;
}

function markCorrectAnswerForMultipleChoice(questionElement, correctAnswer) {
    const answerElements = questionElement.querySelectorAll(".answer > div, .answer .r0, .answer .r1, .radio-custom");
    const correctAnswersArray = correctAnswer.split(',').map(a => a.trim());
    chrome.storage.local.get(['markingMode'], (result) => {
        const markingMode = result.markingMode || 'default';

        answerElements.forEach((el, index) => {
            const inputElement = el.querySelector("input[type='checkbox'], input[type='radio']");
            let answerLabelElement = el.querySelector("div[data-region='answer-label']");

            if (!answerLabelElement) {
                answerLabelElement = el.querySelector("label");
            }

            let answerText = "";
            if (answerLabelElement) {
                answerText = Array.from(answerLabelElement.querySelectorAll("p, pre, div"))
                    .map(node => node.innerText.trim())
                    .filter(text => text.length > 0)
                    .join(" ")
                    .trim();
            }

            const answerNumber = (index + 1).toString();

            const isNumberMatch = correctAnswersArray.includes(answerNumber);

            const isTextMatch = correctAnswersArray.some(correctText => {
                return correctText === answerText;
            });

            if (isNumberMatch || isTextMatch) {
                if (markingMode === 'immediate') {
                    if (inputElement) {
                        inputElement.checked = true;
                        // console.debug(`Zaznaczono odpowied: ${answerText}`);
                    } else {
                        console.warn(`Input element for answer "${answerText}" not found.`);
                    }
                } else if (markingMode === 'saveMode') {
                    Array.from(answerLabelElement.querySelectorAll("p, pre, div")).forEach(label => {
                        const text = label.innerText.trim();
                        if (text) {
                            label.innerHTML = `<span style="font-weight: 600;">${text[0]}</span>${text.slice(1)}`;
                            // console.debug(`Zmieniono formatowanie dla odpowiedzi: ${answerText}`);
                        }
                    });
                } else {
                    Array.from(answerLabelElement.querySelectorAll("p, pre, div")).forEach(label => {
                        label.style.fontWeight = 'bold';
                        label.style.color = '#4CAF50';
                        // console.debug(`Zmieniono formatowanie dla odpowiedzi: ${answerText}`);
                    });
                }
            }
        });
    });
}



