function markCorrectAnswerForTrueFalse(questionElement, correctAnswer) {
    const answerElements = questionElement.querySelectorAll(".answer div");
    const correctAnswerText = correctAnswer.trim().toLowerCase();

    chrome.storage.local.get(['markingMode'], (result) => {
        const markingMode = result.markingMode || 'default';

        answerElements.forEach((el, index) => {
            const labelElement = el.querySelector("label");
            const inputElement = el.querySelector("input[type='radio']");

            if (labelElement && inputElement) {
                const answerText = labelElement.innerText.trim().toLowerCase();
                const answerNumber = (index + 1).toString();

                if (correctAnswerText.includes(answerText) || correctAnswerText.includes(answerNumber)) {
                    if (markingMode === 'immediate') {
                        inputElement.checked = true;
                    } else if (markingMode === 'saveMode') {
                        labelElement.innerHTML = `<span style="font-weight: 600;">${answerText[0]}</span>${answerText.slice(1)}`;
                    } else {
                        labelElement.style.fontWeight = 'bold';
                        labelElement.style.color = '#4CAF50';
                    }
                }
            }
        });
    });
}





export async function handleMatchingQuestion(questionElement) {

    const questionText = await extractMatchingQuestionText(questionElement);

    const matchingPairs = await extractMatchingPairs(questionElement);
    if (!Array.isArray(matchingPairs)) {
        return;
    }

    sendQuestionToChatGPT(questionText, matchingPairs, "matching")
        .then(response => {
            markCorrectAnswersForMatching(questionElement, response);
        })
        .catch(error => {
            console.error("Error processing matching question with ChatGPT:", error);
        });
}


async function extractMatchingQuestionText(questionElement) {
    const questionTextElement = questionElement.querySelector(".qtext");
    if (!questionTextElement) {
        console.error("Nie znaleziono elementu .qtext.");
        return "";
    }

    let questionText = questionTextElement.textContent.trim();
    return questionText;
}

async function extractMatchingPairs(questionElement) {
    const answerTable = questionElement.querySelector(".ablock .answer tbody");
    if (!answerTable) {
        console.error("Nie znaleziono tabeli odpowiedzi.");
        return [];
    }

    const pairs = [];
    const rows = answerTable.querySelectorAll("tr");

    rows.forEach(row => {
        let termElement = row.querySelector("td.text pre");
        const selectElement = row.querySelector("td.control select");
        termElement = row.querySelector("td.text p");
        if (!termElement) {
            termElement = row.querySelectorAll("td.text pre");
        }
        if (termElement && selectElement) {
            const term = termElement.textContent.trim();
            const options = [];

            selectElement.querySelectorAll("option").forEach(option => {
                if (option.value !== "0") {
                    options.push(option.textContent.trim());
                }
            });

            pairs.push({ term, options });
        } else {
            console.error("Nie znaleziono elementw termElement lub selectElement w wierszu:", row);
        }
    });

    return pairs;
}


function markCorrectAnswersForMatching(questionElement, rawResponse) {
    const response = rawResponse.replace(/[^0-9,]/g, '').trim();
    const rows = questionElement.querySelectorAll(".ablock .answer tbody tr");

    if (!rows.length) {
        console.error("Nie znaleziono adnych wierszy w tabeli odpowiedzi.");
        return;
    }

    chrome.storage.local.get(['markingMode'], (result) => {
        const markingMode = result.markingMode || 'default';
        const correctAnswerNumbers = response.split(',').map(answer => answer.trim());

        if (correctAnswerNumbers.length !== rows.length) {
            console.error("Mismatch between number of terms and answers. Check the response format.");
            return;
        }

        rows.forEach((row, index) => {
            const selectElement = row.querySelector("select");
            const correctAnswerNumber = correctAnswerNumbers[index];
            const termElement = row.querySelector("td.text p");

            if (!termElement) {
                console.error("Nie znaleziono elementu termElement w wierszu:", row);
                return;
            }

            const optionToHighlight = selectElement ? selectElement.querySelector(`option[value="${correctAnswerNumber}"]`) : null;

            if (markingMode === 'immediate') {
                if (optionToHighlight) {
                    selectElement.value = optionToHighlight.value;
                }
            } else if (markingMode === 'saveMode') {
                if (optionToHighlight) {
                    optionToHighlight.style.color = '#999797';
                }
            } else {
                if (optionToHighlight) {
                    optionToHighlight.style.fontWeight = 'bold';
                    optionToHighlight.style.color = '#4CAF50';
                }
            }
        });
    });
}


export async function handleClozeQuestion(questionElement) {

    const questionText = await extractClozeQuestionText(questionElement);

    const clozeOptions = await extractClozeOptions(questionElement);

    if (!Array.isArray(clozeOptions)) {
        console.error("clozeOptions is not an array:", clozeOptions);
        return;
    }

    sendQuestionToChatGPT(questionText, clozeOptions, "cloze")
        .then(response => {
            markCorrectAnswersForCloze(questionElement, response);
        })
        .catch(error => {
            console.error("Error processing cloze question with ChatGPT:", error);
        });
}


async function extractClozeQuestionText(questionElement) {
    const questionTextElement = questionElement.querySelector(".qtext");
    return questionTextElement ? await convertMathJaxToText(questionTextElement) : "";
}


async function extractClozeOptions(questionElement) {
    const selects = questionElement.querySelectorAll(".qtext select");
    let options = [];

    for (const select of selects) {
        const selectOptions = [];
        for (const option of select.options) {
            if (option.value !== "") {
                const processedText = await convertMathJaxToText(option);
                selectOptions.push(processedText.trim());
            }
        }
        options.push(selectOptions);
    }

    return options;
}


function markCorrectAnswersForCloze(questionElement, correctAnswers) {
    const selects = questionElement.querySelectorAll(".qtext select");

    chrome.storage.local.get(['markingMode'], (result) => {
        const markingMode = result.markingMode || 'default';

        correctAnswers.split(',').forEach((answer, index) => {
            const selectElement = selects[index];
            if (!selectElement) {
                console.warn(`No select element found for cloze part ${index + 1}`);
                return;
            }

            const correctAnswerNumber = answer.trim();
            const optionToHighlight = selectElement.querySelector(`option[value="${correctAnswerNumber}"]`);

            if (!optionToHighlight) {
                console.warn(`No option found for value '${correctAnswerNumber}' in select element for cloze part ${index + 1}`);
                return;
            }

            if (markingMode === 'immediate') {
                selectElement.value = optionToHighlight.value;
            } else if (markingMode === 'saveMode') {
                optionToHighlight.style.color = '#D3D3D3';
            } else {
                optionToHighlight.style.fontWeight = 'bold';
                optionToHighlight.style.color = '#4CAF50';
            }
        });
    });
}

export async function handleClozeMultianswerQuestion(questionElement) {

    // Pobieramy wszystkie kontenery pyta
    const questionContainers = document.querySelectorAll(".formulation");
    let extractedText = [];

    /**
     * Funkcja rekurencyjnie wycigajca tre z danego wza, 
     * wstawiajca [opcja] w miejscu subquestion w zalenoci od tego,
     * czy w subquestion jest <select>, czy <input type="text">
     */
    function extractTextWithSubquestions(node) {
        let textResult = "";

        node.childNodes.forEach(child => {
            // Jeli to zwyky tekst
            if (child.nodeType === Node.TEXT_NODE) {
                textResult += child.textContent;

                // Jeli to element HTML
            } else if (child.nodeType === Node.ELEMENT_NODE) {
                const element = child;

                // Sprawdzamy, czy to .subquestion
                if (element.classList.contains("subquestion")) {
                    const selectElement = element.querySelector("select");
                    const inputElement = element.querySelector("input[type='text']");

                    if (selectElement) {
                        // Mamy list rozwijan
                        let options = [];
                        // Zbieramy wszystkie niepuste opcje
                        const allOptions = selectElement.querySelectorAll("option");
                        allOptions.forEach((option, index) => {
                            if (option.value !== "") {
                                // Uywamy indeksu jako wskanika odpowiedzi
                                options.push(`${index}: ${option.textContent.trim()}`);
                            }
                        });
                        // Wstawiamy list w nawiasach kwadratowych
                        textResult += ` [${options.join(", ")}] `;
                    } else if (inputElement) {
                        // Mamy pole tekstowe
                        // Dla czytelnoci wstawiamy np. [___] jako placeholder
                        textResult += ` [___] `;
                    }
                } else {
                    // Jeli to inny element, to wywoujemy rekurencj
                    textResult += extractTextWithSubquestions(element);
                }
            }
        });

        return textResult;
    }

    // Przetwarzamy kady kontener pytania z osobna
    questionContainers.forEach((questionContainer, qIndex) => {
        let questionText = extractTextWithSubquestions(questionContainer);

        questionText = questionText
            .replace(/\s+/g, " ")
            .trim();

        extractedText.push(`Pytanie ${qIndex + 1}: ${questionText}`);
    });

    const currentPage = getCurrentPageNumber();


    const instructionText = getInstructionForPage(currentPage);


    const combinedPrompt = formatPromptWithInstruction(extractedText, instructionText);

    sendQuestionToChatGPT(combinedPrompt, [], "cloze-multianswer")
        .then(response => {

            // Kada linia z odpowiedzi to kolejna warto do wstawienia
            // (np. "1", "0", "wealthy", itp.)
            const answersArray = response
                .split("\n")
                .map(answer => answer.trim())
                .filter(a => a !== ""); // usuwamy ewentualne puste linie


            markCorrectAnswersForMultianswer(questionElement, answersArray);
        })
        .catch(error => {
            console.error("Error processing multi-answer question with ChatGPT:", error);
        });
}

/****************************************************
 * Funkcja do oznaczania odpowiedzi 
 ****************************************************/
function markCorrectAnswersForMultianswer(questionElement, correctAnswers) {

    // Znajdujemy wszystkie .subquestion w obrbie pytania
    const subQuestions = questionElement.querySelectorAll(".subquestion");

    // Pobieramy tryb oznaczania z pamici
    chrome.storage.local.get(["markingMode"], (result) => {
        const markingMode = result.markingMode || "default";


        const joinedAnswers = correctAnswers.join(",");


        const allAnswers = joinedAnswers
            .split(",")
            .map(item => item.trim())   // usuwamy spacje
            .filter(item => item !== ""); // usuwamy puste wejcia


        subQuestions.forEach((subq, index) => {
            const selectElement = subq.querySelector("select");
            const inputElement = subq.querySelector("input[type='text']");

            if (index >= allAnswers.length) {
                return;
            }

            // userAnswer to np. "1" dla <select>, "wealthy" dla <input>
            const userAnswer = allAnswers[index];

            if (selectElement) {
                // ------ Przypadek: <select> ------
                const correctIndex = parseInt(userAnswer, 10);
                if (isNaN(correctIndex)) {
                    return;
                }

                const correctOption = selectElement.options[correctIndex];
                if (!correctOption) {
                    return;
                }

                // Oznaczanie wg trybu
                if (markingMode === "default") {
                    correctOption.style.color = "#4CAF50";
                } else if (markingMode === "immediate") {
                    selectElement.value = correctOption.value;
                } else if (markingMode === "saveMode") {
                    const answerText = correctOption.textContent.trim();
                    if (answerText.length > 0) {
                        correctOption.innerHTML = `<strong>${answerText[0]}</strong>${answerText.slice(1)}`;
                    }
                    correctOption.style.color = "#A9A9A9";
                }
            } else if (inputElement) {
                // ------ Przypadek: <input type="text"> ------
                // Tutaj userAnswer to sowo/wyraenie np. "wealthy"
                if (markingMode === "default") {
                    // Wywietlamy tekst poprawnej odpowiedzi na zielono POD polem
                    // lub inna forma oznaczenia. Tutaj przykadowo tworzymy <div>:
                    const answerDiv = document.createElement("div");
                    answerDiv.style.color = "#4CAF50";
                    answerDiv.style.fontWeight = "bold";
                    answerDiv.innerText = userAnswer;
                    // wstawiamy po polu input
                    inputElement.insertAdjacentElement("afterend", answerDiv);

                } else if (markingMode === "immediate") {
                    // Wpisujemy poprawn odpowied do inputa
                    inputElement.value = userAnswer;

                } else if (markingMode === "saveMode") {
                    // Ustawiamy placeholder z odpowiedzi, ale tylko widoczny przy klikniciu
                    inputElement.addEventListener("focus", function () {
                        inputElement.placeholder = userAnswer;
                    });
                    inputElement.addEventListener("blur", function () {
                        inputElement.placeholder = "";
                    });
                    // Przyciemniamy tekst, dopki uytkownik nie wpisze niczego
                    inputElement.style.color = "#A9A9A9";

                    inputElement.addEventListener("input", function handleUserInput() {
                        inputElement.style.color = "";
                    });
                }
            } else {
                // Ani <select>, ani <input type="text">
                console.warn(` subquestion nr ${index + 1} nie zawiera <select> ani <input>.`);
            }
        });
    });
}


