async function extractShortAnswerQuestionText(questionElement) {
    let questionText = "";

    const qtextElement = questionElement.querySelector(".qtext");
    if (qtextElement) {
        questionText = qtextElement.textContent.trim();
    } else {
        const elements = questionElement.querySelectorAll("p, pre");

        elements.forEach(element => {
            if (!element.querySelector(".subquestion")) {
                questionText += element.textContent.trim() + "\n";
            }
        });
    }

    return questionText.trim();
}
function markCorrectAnswerForShortAnswer(questionElement, correctAnswers) {
    const inputElements = questionElement.querySelectorAll("input[type='text']");
    const answersArray = correctAnswers.split("\n").map(answer => answer.trim().toLowerCase());

    chrome.storage.local.get(['markingMode'], (result) => {
        const markingMode = result.markingMode || 'default';

        inputElements.forEach((inputElement) => {
            const matchingAnswer = answersArray.find(answer => answer === inputElement.value.trim().toLowerCase()) || answersArray[0];

            if (markingMode === 'immediate') {
                if (inputElement) {
                    inputElement.value = matchingAnswer;
                }
            } else if (markingMode === 'saveMode') {
                if (inputElement) {
                    inputElement.addEventListener('focus', function () {
                        inputElement.placeholder = matchingAnswer;
                    });

                    inputElement.addEventListener('blur', function () {
                        inputElement.placeholder = '';
                        inputElement.style.color = '';
                    });
                }
            } else {
                if (inputElement) {
                    const correctAnswerDisplay = document.createElement('div');
                    correctAnswerDisplay.style.color = '#4CAF50';
                    correctAnswerDisplay.style.fontWeight = 'bold';
                    correctAnswerDisplay.innerText = `${getLocalizedText('correctAnswer')} ${matchingAnswer}`;
                    inputElement.parentNode.appendChild(correctAnswerDisplay);
                }
            }
        });
    });
}


