function applyLongAnswerResponse(questionElement, response) {
    const textAreaElement = questionElement.querySelector("textarea.qtype_essay_plain");
    const iframe = questionElement.querySelector("iframe");
    let editorBody;

    if (iframe) {
        const editorDocument = iframe.contentDocument || iframe.contentWindow.document;
        editorBody = editorDocument.querySelector("body");
    } else {
        editorBody = questionElement.querySelector("textarea");
    }

    chrome.storage.local.get(['markingMode'], (result) => {
        const markingMode = result.markingMode || 'default';

        if (markingMode === 'immediate') {
            if (textAreaElement) {
                textAreaElement.value = response.trim();
            } else if (iframe) {
                editorBody.innerHTML = `<p>${response.trim()}</p>`;
            }
        } else if (markingMode === 'saveMode') {
            const words = response.trim().split(' ');
            let currentWordIndex = 0;

            const createOverlay = () => {
                const overlay = document.createElement('div');
                overlay.style.position = 'absolute';
                overlay.style.color = '#999797';
                overlay.style.pointerEvents = 'none';
                overlay.style.whiteSpace = 'pre-wrap';
                overlay.style.opacity = '0.5';
                overlay.style.zIndex = '1';
                return overlay;
            };

            const updateOverlayPosition = (inputElement, overlayElement) => {
                const rect = inputElement.getBoundingClientRect();
                overlayElement.style.top = `${rect.top + window.scrollY}px`;
                overlayElement.style.left = `${rect.left + window.scrollX}px`;
                overlayElement.style.width = `${rect.width}px`;
                overlayElement.style.height = `${rect.height}px`;
                overlayElement.style.padding = getComputedStyle(inputElement).padding;
                overlayElement.style.fontSize = getComputedStyle(inputElement).fontSize;
                overlayElement.style.lineHeight = getComputedStyle(inputElement).lineHeight;
                overlayElement.style.fontFamily = getComputedStyle(inputElement).fontFamily;
            };

            const updateOverlayContent = (inputElement, overlayElement) => {
                const currentText = inputElement.value;
                let remainingWords = words.slice(currentWordIndex).join(' ');

                if (currentText.trim()) {
                    const typedWords = currentText.trim().split(' ');
                    for (let i = 0; i < typedWords.length; i++) {
                        if (words[i] && words[i].startsWith(typedWords[i])) {
                            currentWordIndex = i + 1;
                        } else {
                            currentWordIndex = i;
                            remainingWords = words.slice(currentWordIndex).join(' ');
                            break;
                        }
                    }
                }

                overlayElement.innerHTML = `${currentText}${remainingWords ? ' ' + remainingWords : ''}`;
            };

            const handleKeyDown = (event, inputElement, overlayElement) => {
                if (event.key === 'Tab') {
                    event.preventDefault();
                    if (currentWordIndex < words.length) {
                        const currentWord = words[currentWordIndex] + ' ';
                        inputElement.value += currentWord;
                        currentWordIndex++;
                        updateOverlayContent(inputElement, overlayElement);
                    }
                } else {
                    updateOverlayContent(inputElement, overlayElement);
                }
            };

            const setupOverlayForTextarea = (textarea) => {
                const overlay = createOverlay();
                document.body.appendChild(overlay);

                updateOverlayPosition(textarea, overlay);
                updateOverlayContent(textarea, overlay);

                textarea.addEventListener('keydown', (event) => handleKeyDown(event, textarea, overlay));
                textarea.addEventListener('input', () => updateOverlayContent(textarea, overlay));
                window.addEventListener('resize', () => updateOverlayPosition(textarea, overlay));
            };

            if (textAreaElement) {
                textAreaElement.style.position = 'relative';
                setupOverlayForTextarea(textAreaElement);
            } else if (iframe) {
                const hintElement = document.createElement('div');
                hintElement.style.color = '#999797';
                hintElement.style.fontStyle = 'italic';
                hintElement.innerText = `Sugestia: ${response.trim()}`;
                iframe.closest('.qtype_essay_editor').appendChild(hintElement);

                const overlay = createOverlay();
                document.body.appendChild(overlay);

                updateOverlayPosition(editorBody, overlay);
                updateOverlayContent(editorBody, overlay);

                editorBody.addEventListener('keydown', (event) => handleKeyDown(event, editorBody, overlay));
                editorBody.addEventListener('input', () => updateOverlayContent(editorBody, overlay));
                window.addEventListener('resize', () => updateOverlayPosition(editorBody, overlay));
            }
        } else {
            if (textAreaElement) {
                const correctAnswerDisplay = document.createElement('div');
                correctAnswerDisplay.style.color = '#4CAF50';
                correctAnswerDisplay.style.fontWeight = 'bold';
                correctAnswerDisplay.innerText = `${getLocalizedText('correctAnswer')} ${response.trim()}`;
                textAreaElement.parentNode.appendChild(correctAnswerDisplay);
            } else if (iframe) {
                const correctAnswerDisplay = document.createElement('div');
                correctAnswerDisplay.style.color = '#4CAF50';
                correctAnswerDisplay.style.fontWeight = 'bold';
                correctAnswerDisplay.innerText = `${getLocalizedText('correctAnswer')} ${response.trim()}`;
                iframe.closest('.qtype_essay_editor').appendChild(correctAnswerDisplay);

            }
        }
    });
}




export async function handleDragAndDropQuestion(questionElement) {

    const questionText = await extractDragAndDropQuestionText(questionElement);

    const possibleAnswers = await extractDragAndDropAnswers(questionElement);

    if (!Array.isArray(possibleAnswers)) {
        console.error("Possible answers are not an array:", possibleAnswers);
        return;
    }

    sendQuestionToChatGPT(questionText, possibleAnswers, "dragAndDrop")
        .then(response => {
            markCorrectAnswerForDragAndDrop(questionElement, response);
        })
        .catch(error => {
            console.error("Error processing drag-and-drop question with ChatGPT:", error);
        });
}

async function extractDragAndDropQuestionText(questionElement) {
    const questionTextElement = questionElement.querySelector(".qtext");
    return questionTextElement ? await convertMathJaxToText(questionTextElement) : "";
}

async function extractDragAndDropAnswers(questionElement) {
    const answerElements = questionElement.querySelectorAll(".draghome.unplaced");
    const answers = [];

    for (const el of answerElements) {
        const processedText = await convertMathJaxToText(el);
        answers.push(processedText.trim());
    }
    return answers;
}

function markCorrectAnswerForDragAndDrop(questionElement, correctAnswer) {
    const correctAnswerIndexes = correctAnswer.split(',').map(num => parseInt(num.trim()) - 1);
    const answerElements = questionElement.querySelectorAll(".draghome.unplaced");
    const dropZones = questionElement.querySelectorAll(".place1.drop, .place2.drop, .place3.drop, .place4.drop");

    chrome.storage.local.get(['markingMode'], (result) => {
        const markingMode = result.markingMode || 'default';

        if (dropZones.length > 1 && answerElements.length > 1) {
            if (markingMode === 'immediate') {
                correctAnswerIndexes.forEach((answerIndex, i) => {
                    const correctAnswerElement = answerElements[answerIndex];
                    const dropZone = dropZones[i];

                    const startPos = correctAnswerElement.getBoundingClientRect();
                    const endPos = dropZone.getBoundingClientRect();

                    correctAnswerElement.dispatchEvent(new MouseEvent('mousedown', {
                        bubbles: true,
                        cancelable: true,
                        clientX: startPos.left,
                        clientY: startPos.top
                    }));

                    correctAnswerElement.dispatchEvent(new MouseEvent('mousemove', {
                        bubbles: true,
                        cancelable: true,
                        clientX: endPos.left + endPos.width / 2,
                        clientY: endPos.top + endPos.height / 2
                    }));

                    correctAnswerElement.dispatchEvent(new MouseEvent('mouseup', {
                        bubbles: true,
                        cancelable: true,
                        clientX: endPos.left + endPos.width / 2,
                        clientY: endPos.top + endPos.height / 2
                    }));
                });
            } else if (markingMode === 'saveMode') {
                correctAnswerIndexes.forEach((answerIndex) => {
                    const correctAnswerElement = answerElements[answerIndex];
                    const correctAnswerText = correctAnswerElement.innerText.trim();
                    correctAnswerElement.innerHTML = `<span style="font-weight: 600;">${correctAnswerText[0]}</span>${correctAnswerText.slice(1)}`;
                });
            } else {
                correctAnswerIndexes.forEach((answerIndex, i) => {
                    const correctAnswerElement = answerElements[answerIndex];
                    const correctAnswerText = correctAnswerElement.innerText.trim();
                    const correctAnswerDisplay = document.createElement('div');
                    correctAnswerDisplay.style.color = '#4CAF50';
                    correctAnswerDisplay.style.fontWeight = 'bold';
                    correctAnswerDisplay.innerText = `${getLocalizedText('correctAnswer')} ${i + 1}: ${correctAnswerText}`;
                    questionElement.appendChild(correctAnswerDisplay);
                });
            }
        }
    });
}



