async function extractCurrentOrder(questionElement) {
    const listItems = questionElement.querySelectorAll(".sortablelist .sortableitem");
    const currentOrder = [];

    listItems.forEach(item => {
        const textElement = item.querySelector(".flex-grow-1");
        if (textElement) {
            currentOrder.push(textElement.textContent.trim());
        }
    });

    return currentOrder;
}
function markCorrectOrder(questionElement, correctOrder) {
    const correctItems = correctOrder.split(',').map(item => item.trim());
    const listItems = Array.from(questionElement.querySelectorAll("li.sortableitem"));

    function updateArrowColors() {
        listItems.forEach((itemElement, currentIndex) => {
            const text = itemElement.querySelector(".d-flex.align-items-center.flex-grow-1.px-2").textContent.trim();
            const correctIndex = correctItems.indexOf(text);

            const upArrow = itemElement.querySelector("button[data-action='move-backward'] i");
            const downArrow = itemElement.querySelector("button[data-action='move-forward'] i");

            upArrow.style.color = '';
            downArrow.style.color = '';

            if (correctIndex !== currentIndex) {
                if (correctIndex < currentIndex) {
                    upArrow.style.color = '#999999';
                } else if (correctIndex > currentIndex) {
                    downArrow.style.color = '#999999';
                }
            }
        });
    }

    chrome.storage.local.get(['markingMode'], (result) => {
        const markingMode = result.markingMode || 'default';

        if (markingMode === 'immediate') {
            listItems.sort((a, b) => {
                const aText = a.querySelector(".d-flex.align-items-center.flex-grow-1.px-2").textContent.trim();
                const bText = b.querySelector(".d-flex.align-items-center.flex-grow-1.px-2").textContent.trim();
                return correctItems.indexOf(aText) - correctItems.indexOf(bText);
            });
            const list = questionElement.querySelector("ul.sortablelist");
            listItems.forEach(item => list.appendChild(item));
            updateArrowColors();
        } else if (markingMode === 'saveMode') {
            updateArrowColors();

            listItems.forEach(itemElement => {
                const upArrow = itemElement.querySelector("button[data-action='move-backward']");
                const downArrow = itemElement.querySelector("button[data-action='move-forward']");

                upArrow.addEventListener('click', () => {
                    setTimeout(() => {
                        updateArrowColors();
                    }, 100);
                });

                downArrow.addEventListener('click', () => {
                    setTimeout(() => {
                        updateArrowColors();
                    }, 100);
                });
            });
        } else {
            listItems.forEach((itemElement, index) => {
                const text = itemElement.querySelector(".d-flex.align-items-center.flex-grow-1.px-2").textContent.trim();
                const correctIndex = correctItems.indexOf(text);
                if (correctIndex !== -1) {
                    itemElement.querySelector(".d-flex.align-items-center.flex-grow-1.px-2").innerHTML = `<span style="color: #4CAF50; font-weight: bold;">${correctIndex + 1}. </span>${text}`;
                } else {
                    console.warn(`No match found for: ${text}`);
                }
            });
        }
    });
}



function waitForStableDOMAndMathJax(containerSelector, quietPeriod = 2000) {
    return new Promise((resolve, reject) => {
        const container = document.querySelector(containerSelector);
        if (!container) {
            return reject(new Error(`Nie znaleziono kontenera: ${containerSelector}`));
        }

        let lastMutationTime = Date.now();

        const observer = new MutationObserver(() => {
            lastMutationTime = Date.now();
        });

        observer.observe(container, {
            childList: true,
            subtree: true,
            attributes: true,
            characterData: true
        });


        const checkInterval = setInterval(() => {
            const now = Date.now();

            if (now - lastMutationTime > quietPeriod) {
                clearInterval(checkInterval);
                observer.disconnect();


                if (typeof MathJax !== 'undefined' && MathJax.typesetPromise) {
                    MathJax.typesetPromise().then(() => {
                        resolve();
                    }).catch(err => reject(err));
                } else {

                    resolve();
                }
            }
        }, 500);
    });
}

export async function handleQuizSummary(summaryElement, navElement, userEmail) {
    const quizData = extractQuizDataFromSummary(summaryElement);

    await waitForStableDOMAndMathJax('.questionflagsaveform');

    if (typeof MathJax !== 'undefined' && MathJax.typesetPromise) {
        await MathJax.typesetPromise();
    } else {
        await new Promise(res => setTimeout(res, 2000));
    }

    const quizQuestions = extractQuestionsFromSummary();

    let courseName = "NA";
    let testName = "NA";

    if (navElement) {
        const breadcrumbItems = navElement.querySelectorAll(".breadcrumb-item");
        if (breadcrumbItems.length > 0) {
            const courseElement = breadcrumbItems[0].querySelector("a");
            courseName = courseElement ? courseElement.innerText.trim() : "NA";
        }
        if (breadcrumbItems.length > 1) {
            const testElement = breadcrumbItems[1].querySelector("span");
            testName = testElement ? testElement.innerText.trim() : "NA";
        }
    }

    const currentUrl = window.location.href;
    const universityUrl = new URL(currentUrl).origin;
    const startTime = new Date().toISOString();

    const quizIdentifierBase = `${testName}-${quizData.map(item => item.value).join('-')}`;
    const existingIdentifier = localStorage.getItem('quizIdentifier');
    if (existingIdentifier === quizIdentifierBase) {
        return;
    }
    localStorage.setItem('quizIdentifier', quizIdentifierBase);

    const payload = {
        userEmail: userEmail,
        courseName: courseName,
        testName: testName,
        universityUrl: universityUrl,
        quizData: quizData,
        startTime: startTime,
        quizQuestions: quizQuestions
    };

    try {
        const response = await fetch('https://us-central1-quizonator-a4513.cloudfunctions.net/api/save-quiz-summary-new', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(payload)
        });

        if (!response.ok) {
            throw new Error(`Failed to save quiz summary: ${response.statusText}`);
        }

    } catch (error) {
        console.error("Error saving quiz summary:", error);
    }
}

function extractQuizDataFromSummary(summaryElement) {
    const rows = summaryElement.querySelectorAll("tr");
    const quizData = [];
    rows.forEach(row => {
        const keyEl = row.querySelector("th");
        const valEl = row.querySelector("td");
        if (!keyEl || !valEl) return;
        const key = keyEl.innerText.trim();
        const value = valEl.innerText.trim();
        quizData.push({ key, value });
    });
    return quizData;
}

function extractQuestionsFromSummary() {
    const questionElements = document.querySelectorAll('.que');
    const questions = [];
    questionElements.forEach(el => {
        const questionType = getQuestionType(el);
        if (questionType === 'multichoice') {
            questions.push(extractMultipleChoice(el));
        } else if (questionType === 'truefalse') {
            questions.push(extractTrueFalse(el));
        } else if (questionType === 'shortanswer') {
            questions.push(extractShortAnswer(el));
        }
    });
    return questions;
}
function getQuestionType(questionElement) {
    const classes = questionElement.className.split(' ');
    if (classes.includes('multichoice')) {
        return 'multichoice';
    } else if (classes.includes('truefalse')) {
        return 'truefalse';
    } else if (classes.includes('shortanswer')) {
        return 'shortanswer';
    }
    return null;
}

function extractMultipleChoice(questionElement) {
    const answerRows = questionElement.querySelectorAll('.answer > div[class^="r"]');
    const finalAnswers = [];
    const seenAnswers = new Set();

    const questionTextHTML = extractQuestionHTML(questionElement);
    const questionText = extractQuestionText(questionElement);


    let globalCorrectness = getGlobalQuestionCorrectness(questionElement);

    answerRows.forEach(row => {
        const input = row.querySelector('input[type="radio"], input[type="checkbox"]');
        if (!input) return;

        const labelContainer = row.querySelector('[data-region="answer-label"]');
        if (!labelContainer) return;

        const tempContainer = labelContainer.cloneNode(true);
        tempContainer.querySelectorAll('.MathJax_Preview, .MJX_Assistive_MathML').forEach(el => el.remove());
        tempContainer.querySelectorAll('script[type="math/tex"]').forEach(el => el.remove());

        const answerHTML = tempContainer.innerHTML.trim();
        if (!answerHTML) return;
        if (seenAnswers.has(answerHTML)) return;
        seenAnswers.add(answerHTML);

        const textContainer = document.createElement('div');
        textContainer.innerHTML = answerHTML;
        let answerText = cleanText(textContainer.innerText.trim());

        const selected = input.checked;

        let correct = null;
        if (row.classList.contains('correct')) {
            correct = true;
        } else if (row.classList.contains('incorrect')) {
            correct = false;
        } else {

            const rightAnswerEl = questionElement.querySelector('.rightanswer');
            if (rightAnswerEl) {
                const correctText = rightAnswerEl.innerText
                    .replace("The correct answer is:", "")
                    .replace("Poprawna odpowied to:", "")
                    .trim();
                correct = (answerText === correctText);
            }

        }

        finalAnswers.push({
            html: answerHTML,
            text: answerText,
            selected: selected,
            correct: correct
        });
    });

    return {
        questionHTML: questionTextHTML,
        questionText: questionText,
        answers: finalAnswers
    };
}

function extractTrueFalse(questionElement) {
    const questionTextHTML = extractQuestionHTML(questionElement);
    const questionText = extractQuestionText(questionElement);

    const answerRows = questionElement.querySelectorAll('.answer > div');
    const finalAnswers = [];
    const seenAnswers = new Set();

    let globalCorrectness = getGlobalQuestionCorrectness(questionElement);

    answerRows.forEach(row => {
        const input = row.querySelector('input[type="radio"]');
        if (!input) return;

        const label = row.querySelector('label');
        if (!label) return;

        const answerText = cleanText(label.innerText.trim());
        if (seenAnswers.has(answerText)) return;
        seenAnswers.add(answerText);

        const selected = input.checked;
        let correct = null;

        const rightAnswerEl = questionElement.querySelector('.rightanswer');
        if (rightAnswerEl) {
            const text = rightAnswerEl.innerText.trim();
            // Create regex patterns for each language
            const patterns = [
                /The correct answer is:/i,
                /Poprawna odpowied to:/i,
                / :/i,
                / :/i
            ];

            // Try each pattern
            let cleanedText = text;
            for (const pattern of patterns) {
                cleanedText = cleanedText.replace(pattern, '').trim();
                if (cleanedText !== text) break; // Found a match
            }

            const correctText = cleanText(cleanedText);
            correct = (answerText === correctText);
        } else if (globalCorrectness !== null) {
            if (selected && globalCorrectness === true) {
                correct = true;
            } else if (selected && globalCorrectness === false) {
                correct = false;
            }
        }

        finalAnswers.push({
            text: answerText,
            selected: selected,
            correct: correct
        });
    });

    return {
        type: 'truefalse',
        questionHTML: questionTextHTML,
        questionText: questionText,
        answers: finalAnswers
    };
}
function extractShortAnswer(questionElement) {
    const questionTextHTML = extractQuestionHTML(questionElement);
    const questionText = extractQuestionText(questionElement);

    const answerInput = questionElement.querySelector('.ablock input[type="text"]');
    const correctAnswerEl = questionElement.querySelector('.rightanswer');

    const userAnswer = answerInput ? cleanText(answerInput.value.trim()) : null;
    let correctAnswer = null;
    if (correctAnswerEl) {
        const text = correctAnswerEl.innerText.trim();
        // Create regex patterns for each language
        const patterns = [
            /The correct answer is:/i,
            /Poprawna odpowied to:/i,
            / :/i,
            / :/i
        ];

        // Try each pattern
        let cleanedText = text;
        for (const pattern of patterns) {
            cleanedText = cleanedText.replace(pattern, '').trim();
            if (cleanedText !== text) break; // Found a match
        }

        correctAnswer = cleanText(cleanedText);
    };

    let globalCorrectness = getGlobalQuestionCorrectness(questionElement);

    let isCorrect = null;
    if (correctAnswer && userAnswer) {
        isCorrect = (userAnswer === correctAnswer);
    } else {
        if (globalCorrectness !== null) {
            isCorrect = (globalCorrectness === true);
        } else {
            const gradeElement = questionElement.querySelector('.grade');
            if (gradeElement) {
                isCorrect = interpretGradeAsCorrectness(gradeElement.innerText);
            }

            if (isCorrect === null) {
                const iconSuccess = questionElement.querySelector('.fa-circle-check.text-success');
                if (iconSuccess) {
                    isCorrect = true;
                } else if (answerInput && answerInput.classList.contains('correct')) {
                    isCorrect = true;
                }
            }
        }

        if (isCorrect === true && correctAnswer === null && userAnswer) {
            correctAnswer = userAnswer;
        }
    }

    return {
        type: 'shortanswer',
        questionHTML: questionTextHTML,
        questionText: questionText,
        userAnswer: userAnswer,
        correctAnswer: correctAnswer,
        isCorrect: isCorrect
    };
}


function getGlobalQuestionCorrectness(questionElement) {
    const classes = questionElement.className.split(' ');
    if (classes.includes('correct')) {
        return true;
    } else if (classes.includes('incorrect')) {
        return false;
    } else if (classes.includes('partiallycorrect')) {
        return 'partial';
    }
    return null;
}

function interpretGradeAsCorrectness(gradeText) {
    const match = gradeText.match(/Mark\s+([\d.,]+)\s+out of\s+([\d.,]+)/i);
    if (match) {
        const obtained = parseFloat(match[1].replace(',', '.'));
        const total = parseFloat(match[2].replace(',', '.'));
        if (!isNaN(obtained) && !isNaN(total) && total > 0) {
            if (obtained === total) {
                return true;
            } else if (obtained === 0) {
                return false;
            } else {
                return 'partial';
            }
        }
    }
    return null;
}

function extractQuestionHTML(questionElement) {
    const qTextEl = questionElement.querySelector('.qtext');
    if (!qTextEl) return '';
    const tempContainer = qTextEl.cloneNode(true);
    tempContainer.querySelectorAll('.MathJax_Preview, .MJX_Assistive_MathML').forEach(el => el.remove());
    return tempContainer.innerHTML.trim();
}

function extractQuestionText(questionElement) {
    const qTextEl = questionElement.querySelector('.qtext');
    if (!qTextEl) return '';
    const tempContainer = qTextEl.cloneNode(true);
    tempContainer.querySelectorAll('.MathJax_Preview, .MJX_Assistive_MathML').forEach(el => el.remove());
    tempContainer.querySelectorAll('script[type="math/tex"]').forEach(el => el.remove());
    return tempContainer.innerText.trim();
}
function cleanText(text) {
    return text.replace(/^[a-zA-Z]\.|^[a-zA-Z]\)|^\d+\./, '').trim();
}




