import { sendQuestionToBackendNaurok } from '../core/api.js';

/*************************************************
 * STAN GLOBALNY
 *************************************************/
// Flaga do ledzenia czy przetwarzanie jest aktywne
let isNaurokProcessingActive = false;
// ledzenie ostatniego pytania
let lastQuestionContent = '';

/*************************************************
 * HELPER FUNCTIONS
 *************************************************/

// Funkcja do czyszczenia stylw zastosowanych we wczeniejszych pytaniach
function resetStyles(container) {
    // Resetujemy style dla elementw odpowiedzi
    const optionElements = container.querySelectorAll('.question-option-inner');
    optionElements.forEach(element => {
        element.style.border = '';
        element.style.backgroundColor = '';
        element.style.outline = '';
    });

    // Resetujemy style dla obrazkw
    const imageElements = container.querySelectorAll('.question-option-image');
    imageElements.forEach(element => {
        element.style.border = '';
        element.style.boxShadow = '';
        element.style.outline = '';
    });
}

// Function to extract background-image URL from style attribute
function extractBackgroundImageUrl(element) {
    if (!element) return null;

    const style = element.getAttribute('style');
    if (!style) return null;

    const match = style.match(/background-image\s*:\s*url\s*\(\s*["']?([^"')]+)["']?\s*\)/i);
    return match ? match[1] : null;
}

/*************************************************
 * 1. QUESTION TYPE DETECTION
 *************************************************/
function detectQuestionTypeNaurok(questionContainer) {
    const questionType = questionContainer.querySelector('.question-option-inner[ng-if="test.question.type == \'quiz\'"]')
        ? 'singleChoice'
        : questionContainer.querySelector('.question-option-inner[ng-if="test.question.type == \'multiquiz\'"]')
            ? 'multipleChoice'
            : 'unknown';
    return questionType;
}

/*************************************************
 * 2. QUESTION TEXT EXTRACTION
 *************************************************/
async function extractQuestionTextNaurok(questionContainer) {
    const contentElement = questionContainer.querySelector('.test-content-text-inner');
    let questionText = "";

    if (contentElement) {
        questionText = contentElement.innerText.trim();
    }

    const imageContainer = questionContainer.querySelector('.test-content-image');
    let images = [];

    if (imageContainer && !imageContainer.classList.contains('ng-hide')) {
        const img = imageContainer.querySelector('img');
        if (img && !img.classList.contains('ng-hide') && img.src) {
            images.push({
                url: img.src,
                alt: img.alt || ""
            });
        }
    }
    return { text: questionText, images };
}

/*************************************************
 * 3. EXTRACT ANSWERS FOR CHOICE QUESTIONS
 *************************************************/
async function extractChoiceAnswersNaurok(questionContainer) {
    const optionElements = questionContainer.querySelectorAll('.test-option');
    const options = [];
    const letters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";

    for (let i = 0; i < optionElements.length; i++) {
        const optionElement = optionElements[i];
        let answerText = "";
        let images = [];

        const contentElement = optionElement.querySelector('.question-option-inner-content');
        if (contentElement) {
            answerText = contentElement.innerText.trim();
        }

        const imageElement = optionElement.querySelector('.question-option-image');
        if (imageElement) {
            const imageUrl = extractBackgroundImageUrl(imageElement);
            if (imageUrl) {
                images.push({
                    url: imageUrl,
                    alt: `Option ${letters[i]} image`
                });
            }
        }

        const id = letters[i] || String(i + 1);
        options.push({
            id,
            label: answerText || `Option ${id}`,
            images
        });
    }
    return options;
}

/*************************************************
 * 4. BUILD UNIFIED QUESTION
 *************************************************/
async function buildUnifiedQuestionNaurok(questionContainer) {
    const detectedType = detectQuestionTypeNaurok(questionContainer);
    const { text, images } = await extractQuestionTextNaurok(questionContainer);

    if (!text && images.length === 0) { // Zmodyfikowano warunek - puste pytanie tekstowe jest OK jeli jest obrazek
        console.log("Brak tekstu pytania i obrazkw, pomijam budowanie.");
        return null;
    }

    let unified = {
        id: Date.now().toString(),
        text,
        images,
        metadata: { difficulty: "unknown" }
    };

    switch (detectedType) {
        case "singleChoice":
            unified.type = "singlechoice";
            unified.options = await extractChoiceAnswersNaurok(questionContainer);
            unified.format = {
                responseType: "optionId",
                description: "Odpowiedz jako pojedyncza litera odpowiadajca waciwej opcji."
            };
            break;
        case "multipleChoice":
            unified.type = "multiple";
            unified.options = await extractChoiceAnswersNaurok(questionContainer);
            unified.format = {
                responseType: "optionIds",
                description: "Odpowiedz jako tablica liter odpowiadajcych waciwym opcjom (np. [\"A\", \"C\"])."
            };
            break;
        default:
            console.log("Nieznany typ pytania:", detectedType);
            return null;
    }
    return unified;
}

/*************************************************
 * 5. MARK CORRECT ANSWER
 *************************************************/
function markCorrectAnswerNaurok(questionContainer, correctAnswerObj, unifiedQuestion, mode) {
    const qType = unifiedQuestion.type;
    mode = mode.toLowerCase();

    if (qType === "singlechoice") {
        let correctIndex = -1;
        if (typeof correctAnswerObj === "string") {
            const correctLetter = correctAnswerObj.trim().toUpperCase();
            const optionIds = unifiedQuestion.options.map(opt => opt.id.toUpperCase());
            correctIndex = optionIds.indexOf(correctLetter);
        }

        const optionElements = questionContainer.querySelectorAll('.test-option');
        if (correctIndex >= 0 && correctIndex < optionElements.length) {
            const correctElement = optionElements[correctIndex];
            const innerElement = correctElement.querySelector('.question-option-inner');

            if (innerElement) {
                if (mode === "default") {
                    innerElement.style.border = "3px solid green";
                    innerElement.style.backgroundColor = "rgba(0, 128, 0, 0.1)";
                    const imageElement = innerElement.querySelector('.question-option-image');
                    if (imageElement) {
                        imageElement.style.boxShadow = "0 0 8px 2px rgba(0, 128, 0, 0.5)";
                    }
                } else if (mode === "immediate") {
                    innerElement.click();
                } else if (mode === "savemode") {
                    innerElement.style.border = "1px solid rgba(0, 128, 0, 0.3)";
                    const imageElement = innerElement.querySelector('.question-option-image');
                    if (imageElement) {
                        imageElement.style.border = "1px solid rgba(0, 128, 0, 0.2)";
                    }
                }
            }
        }
    } else if (qType === "multiple") {
        let correctIndices = [];
        if (Array.isArray(correctAnswerObj)) {
            const correctLetters = correctAnswerObj.map(letter => letter.toUpperCase());
            const optionIds = unifiedQuestion.options.map(opt => opt.id.toUpperCase());
            correctLetters.forEach(letter => {
                const index = optionIds.indexOf(letter);
                if (index >= 0) {
                    correctIndices.push(index);
                }
            });
        } else if (typeof correctAnswerObj === "string") {
            const parts = correctAnswerObj.split(',');
            for (const part of parts) {
                const letter = part.trim().toUpperCase();
                const index = unifiedQuestion.options.findIndex(opt => opt.id.toUpperCase() === letter);
                if (index >= 0) {
                    correctIndices.push(index);
                }
            }
        }

        const optionElements = questionContainer.querySelectorAll('.test-option');
        correctIndices.forEach(index => {
            if (index >= 0 && index < optionElements.length) {
                const correctElement = optionElements[index];
                const innerElement = correctElement.querySelector('.question-option-inner');
                if (innerElement) {
                    if (mode === "default") {
                        innerElement.style.border = "3px solid green";
                        innerElement.style.backgroundColor = "rgba(0, 128, 0, 0.1)";
                        const imageElement = innerElement.querySelector('.question-option-image');
                        if (imageElement) {
                            imageElement.style.boxShadow = "0 0 8px 2px rgba(0, 128, 0, 0.5)";
                        }
                    } else if (mode === "immediate") {
                        innerElement.click();
                    } else if (mode === "savemode") {
                        innerElement.style.border = "1px solid rgba(0, 128, 0, 0.3)";
                        const imageElement = innerElement.querySelector('.question-option-image');
                        if (imageElement) {
                            imageElement.style.border = "1px solid rgba(0, 128, 0, 0.2)";
                        }
                    }
                }
            }
        });
    }
}

/*************************************************
 * 6. PROCESS INDIVIDUAL QUESTION
 *************************************************/
async function handleQuestionNaurok(questionContainer, mode) {
    // resetStyles jest teraz wywoywane wczeniej w processNaurok
    // ale jeli handleQuestionNaurok byoby kiedy wywoane bezporednio,
    // mona by tu zostawi resetStyles(questionContainer); jako zabezpieczenie.
    // Na razie, dla spjnoci z optymalizacj, zakadamy, e reset jest ju zrobiony.

    const unifiedQuestion = await buildUnifiedQuestionNaurok(questionContainer);
    if (!unifiedQuestion) {
        return null;
    }

    const isMultipleCorrect = unifiedQuestion.type === "multiple";

    try {
        const response = await sendQuestionToBackendNaurok(unifiedQuestion, isMultipleCorrect);
        if (response && response.correctAnswer) {
            markCorrectAnswerNaurok(questionContainer, response.correctAnswer, unifiedQuestion, mode);
            return unifiedQuestion;
        } else {
            console.log("Brak poprawnej odpowiedzi z backendu lub odpowied niekompletna.");
            return null;
        }
    } catch (error) {
        console.error("Bd wysyania pytania do backendu:", error);
        return null;
    }
}

/*************************************************
 * 7. MAIN PROCESSING FUNCTION
 *************************************************/
export async function processNaurok() {
    if (isNaurokProcessingActive) {
        console.log("Przetwarzanie Naurok ju aktywne, pomijam.");
        return [];
    }
    isNaurokProcessingActive = true;

    try {
        const { markingMode } = await new Promise((resolve) => {
            chrome.storage.local.get(['markingMode'], (result) => {
                resolve({ markingMode: result.markingMode || 'default' });
            });
        });

        const questionContainer = document.querySelector('.test-container-inner[ng-show="test.scene == 1"]');
        if (!questionContainer) {
            console.log("Nie znaleziono kontenera pytania Naurok.");
            // Flaga isNaurokProcessingActive zostanie zresetowana w bloku finally
            return [];
        }

        // OPTYMALIZACJA 1: Natychmiastowe resetowanie stylw
        // Wywoaj resetStyles zaraz po znalezieniu nowego kontenera pytania,
        // aby usun podwietlenie ze starego pytania tak szybko, jak to moliwe.
        resetStyles(questionContainer);

        // OPTYMALIZACJA 2: Znaczce zredukowanie lub usunicie staego opnienia
        // To opnienie byo gwnym wskim gardem po stronie frontendu.
        // Zmniejszono z 1000ms do 100ms. Testuj, czy mniejsza warto (lub 0ms)
        // nadal pozwala na poprawne zaadowanie i ekstrakcj danych pytania.
        // Jeli pytania aduj si bardzo szybko, to opnienie moe nie by potrzebne.
        const formLoadDelay = 100; // ms
        if (formLoadDelay > 0) {
             console.log(`Czekam ${formLoadDelay}ms na zaadowanie formularza (mona testowa mniejsze wartoci).`);
             await new Promise(resolve => setTimeout(resolve, formLoadDelay));
        }


        try {
            const qData = await handleQuestionNaurok(questionContainer, markingMode);
            document.body.style.cursor = 'default';
            return qData ? [qData] : [];
        } catch (err) {
            console.error(`Bd przetwarzania pytania: ${err.message}`);
            document.body.style.cursor = 'default';
            return [];
        }
    } catch (error) {
        document.body.style.cursor = 'default';
        console.error(`Krytyczny bd w processNaurok: ${error.message}`);
        return [];
    } finally {
        // Opnij resetowanie flagi, aby zapobiec natychmiastowemu ponownemu wywoaniu
        // i da czas na ustabilizowanie si DOM po przetworzeniu.
        setTimeout(() => {
            isNaurokProcessingActive = false;
        }, 300); // Zmniejszono z 500ms, mona dalej testowa
    }
}

function debounce(func, wait) {
    let timeout;
    return function (...args) {
        clearTimeout(timeout);
        timeout = setTimeout(() => func.apply(this, args), wait);
    };
}

export function setupNaurokObserver() {
    function getCurrentQuestionSignature() {
        const questionElement = document.querySelector('.test-content-text-inner');
        const optionsElements = document.querySelectorAll('.test-option');
        let signature = questionElement ? questionElement.textContent.trim() : '';

        if (optionsElements.length > 0) {
            signature += '|OPTIONS|';
            optionsElements.forEach(option => {
                const content = option.querySelector('.question-option-inner-content');
                if (content) {
                    signature += content.textContent.trim() + ';';
                }
            });
        }
        return signature;
    }

    let isProcessing = false;

    const debouncedProcess = debounce(async () => {
        // Sprawdzamy obie flagi: isProcessing (lokalna dla observera)
        // oraz isNaurokProcessingActive (globalna dla gwnej logiki przetwarzania)
        if (isProcessing || isNaurokProcessingActive) {
            // console.log("DebouncedProcess: Pomijam, przetwarzanie ju aktywne.");
            return;
        }

        const currentSignature = getCurrentQuestionSignature();

        if (currentSignature && currentSignature !== lastQuestionContent) {
            // console.log("DebouncedProcess: Nowa sygnatura pytania, rozpoczynam przetwarzanie.");
            isProcessing = true;
            lastQuestionContent = currentSignature;

            try {
                await processNaurok();
            } finally {
                // Resetowanie flagi isProcessing z opnieniem, aby unikn
                // natychmiastowego ponownego przetworzenia przy szybkich zmianach DOM.
                setTimeout(() => {
                    isProcessing = false;
                    // console.log("DebouncedProcess: Flaga isProcessing zresetowana.");
                }, 300); // Zmniejszono z 500ms, mona dalej testowa
            }
        } else if (!currentSignature) {
            // console.log("DebouncedProcess: Brak sygnatury pytania.");
        } else {
            // console.log("DebouncedProcess: Sygnatura pytania bez zmian.");
        }
    }, 250); // Zmniejszono debounce z 300ms, dostosuj w zalenoci od szybkoci zmian na stronie

    const observer = new MutationObserver(() => {
        // console.log("MutationObserver: Wykryto mutacj.");
        debouncedProcess();
    });

    const container = document.querySelector('.test-screen');
    if (container) {
        observer.observe(container, {
            childList: true,
            subtree: true,
            attributeFilter: ['class', 'style'], // Obserwuj tylko zmiany klas i stylw
            characterData: false // Nie obserwuj bezporednich zmian tekstu
        });
        // console.log("Obserwator Naurok zosta pomylnie skonfigurowany.");
    } else {
        console.error("Nie znaleziono kontenera .test-screen do obserwacji.");
    }

    return observer;
}