import { sendQuestionToChatGPTMicrosoftForms } from '../core/api.js';

/*************************************************
 * 1. DETEKCJA RODZAJU PYTANIA DLA TESTPORTAL
 *************************************************/
function detectQuestionTypeTestPortal(questionElement) {
    // W TestPortalu rodzaj pytania zwykle siedzi w polu hidden: input[name="givenAnswer.questionType"]
    const questionTypeInput = questionElement.querySelector('input[name="givenAnswer.questionType"]');
    if (!questionTypeInput) {
        console.warn("Nie znaleziono typu pytania w:", questionElement);
        return "unknown";
    }

    const questionTypeValue = questionTypeInput.value;
    switch (questionTypeValue) {
        case "TRUE_FALSE":
            return "trueFalse";
        case "SINGLE_ANSWER":
        case "SURVEY":
            return "singleChoice";
        case "MULTI_ANSWER":
            return "multipleChoice";
        case "SHORT_ANSWER":
            return "shortAnswer";
        case "DESCRIPTIVE":
            return "openText";
        default:
            return "unknown";
    }
}

/*************************************************
 * 2. FUNKCJE POMOCNICZE DO OBRAZKW
 *************************************************/

async function isImagePublic(url) {
    try {
        const response = await fetch(url, { method: "HEAD" });
        return response.ok;
    } catch (error) {
        return false;
    }
}

async function convertUrlToImageDataUrl(url, forceMime = "image/jpeg") {
    const response = await fetch(url);
    if (!response.ok) {
        throw new Error(`Bd fetch: ${response.status} - ${response.statusText}`);
    }

    let contentType = response.headers.get("content-type") || "";
    if (!contentType.startsWith("image/")) {
        contentType = forceMime;
    }

    let blob = await response.blob();
    blob = new Blob([blob], { type: contentType });

    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = () => resolve(reader.result);
        reader.onerror = err => reject(err);
        reader.readAsDataURL(blob);
    });
}


async function ensureImageString(url) {
    const publicAvailable = await isImagePublic(url);
    return publicAvailable ? url : await convertUrlToImageDataUrl(url);
}


async function extractImagesFromTestPortal(element) {
    if (!element) return [];
    const imgs = element.querySelectorAll("img");
    const promises = Array.from(imgs).map(async (img) => {
        const src = img.getAttribute("src");
        if (!src) return null;
        return {
            url: await ensureImageString(src),
            alt: img.alt || ""
        };
    });
    const results = await Promise.all(promises);
    return results.filter(Boolean);
}

/*************************************************
 * 3. BUDOWANIE "UNIFIED" OBIEKTU PYTANIA 
 *************************************************/

async function buildUnifiedQuestionTestPortal(questionElement) {
    const detectedType = detectQuestionTypeTestPortal(questionElement);

    const questionTextElement = questionElement.querySelector(".question_essence");
    let questionText = questionTextElement ? questionTextElement.innerText.trim() : "";
    const questionImages = await extractImagesFromTestPortal(questionTextElement);

    const unified = {
        id: questionElement.getAttribute('id') || `q-${Date.now()}`,
        text: questionText,
        images: questionImages,
        metadata: { difficulty: "unknown" },
    };

    switch (detectedType) {
        case "trueFalse":
        case "singleChoice":
            unified.type = "singlechoice";
            unified.options = await extractMultipleChoiceAnswersTestPortal(questionElement);
            unified.format = {
                responseType: "optionId",
                description: "Odpowiedz jako pojedyncza litera lub numer odpowiadajcy waciwej opcji."
            };
            break;

        case "multipleChoice":
            unified.type = "multiple";
            unified.options = await extractMultipleChoiceAnswersTestPortal(questionElement);
            unified.format = {
                responseType: "optionIds",
                description: 'Odpowiedz jako tablica numerw/oznacze [np. ["1", "3"]].'
            };
            break;

        case "shortAnswer":
            unified.type = "shortanswer";
            unified.options = [];
            unified.placeholder = "Wpisz krtk odpowied";

            const inputShort = questionElement.querySelector(".all_short_answers input[type='text']");
            if (inputShort) {
                unified.answerLength = "short";
            } else {
                unified.answerLength = "long";
            }
            if (unified.answerLength === "long") {
                unified.format = {
                    responseType: "text",
                    description: "Podaj wyczerpujc odpowied."
                };
            } else {
                unified.format = {
                    responseType: "text",
                    description: "Podaj krtk odpowied."
                };
            }
            break;

        case "openText":
            unified.type = "shortanswer";
            unified.options = [];
            unified.placeholder = "Wpisz odpowied opisow";
            unified.answerLength = "long";
            unified.format = {
                responseType: "text",
                description: "Podaj rozwinit odpowied tekstow (descriptive)."
            };
            break;

        default:
            return null;
    }

    return unified;
}

/*************************************************
 * 4. EKSTRAKCJA OPCJI (multiple/single/trueFalse)
 *************************************************/
async function extractMultipleChoiceAnswersTestPortal(questionElement) {
    const answerElements = questionElement.querySelectorAll(".question_answers .answer_container");
    const options = [];
    const letters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";

    for (let i = 0; i < answerElements.length; i++) {
        const el = answerElements[i];
        const answerBody = el.querySelector(".answer_body");
        if (!answerBody) continue;

        const answerText = answerBody.innerText.trim();
        const images = await extractImagesFromTestPortal(answerBody);

        options.push({
            id: letters[i],
            label: answerText,
            images
        });
    }

    return options;
}

/*************************************************
 * 5. OZNACZANIE POPRAWNEJ ODPOWIEDZI W TESTPORTAL
 *************************************************/
function markCorrectAnswerTestPortal(questionElement, correctAnswerObj, unifiedQuestion, mode) {
    mode = (mode || "default").toLowerCase();
    const qType = unifiedQuestion.type;
    if (questionElement.querySelector(".correct-answer-info")) {
        return;
    }
    if (questionElement.dataset.alreadyMarked) {
        return;
    }
    questionElement.dataset.alreadyMarked = 'true';
    /******************************************************
     * 1) SINGLECHOICE / MULTIPLE
     ******************************************************/
    if (qType === "singlechoice" || qType === "multiple") {
        const answerContainers = questionElement.querySelectorAll(".question_answers .answer_container");
        if (!answerContainers || answerContainers.length === 0) return;

        let correctIds = [];
        if (Array.isArray(correctAnswerObj)) {
            correctIds = correctAnswerObj.map((item) => item.trim().toUpperCase());
        } else if (typeof correctAnswerObj === "string") {
            if (correctAnswerObj.includes(",")) {
                correctIds = correctAnswerObj.split(",").map(s => s.trim().toUpperCase());
            } else {
                correctIds = [correctAnswerObj.trim().toUpperCase()];
            }
        }

        answerContainers.forEach((container, i) => {
            const optionLetter = unifiedQuestion.options[i]?.id.toUpperCase();
            if (!optionLetter) return;

            const isCorrect = correctIds.includes(optionLetter);
            if (isCorrect) {
                const inputElement = container.querySelector("input[type='checkbox'], input[type='radio']");
                const answerBody = container.querySelector(".answer_body");
                if (!answerBody) return;

                const textContent = answerBody.innerText
                    .replace(/zoom-in\s*Powiksz obraz/i, "")
                    .trim();
                const isTextEmpty = textContent.length === 0;

                if (mode === 'immediate' || (isTextEmpty && (mode === 'default' || mode === 'savemode'))) {
                    if (inputElement) {
                        inputElement.checked = true;
                        inputElement.dispatchEvent(new Event('change', { bubbles: true }));
                    }
                }
                else if (mode === 'savemode') {
                    const originalText = answerBody.innerText;
                    if (originalText.length > 0) {
                        answerBody.innerHTML = `<span style="font-weight: 600;">${originalText[0]}</span>${originalText.slice(1)}`;
                    }
                }
                else {
                    answerBody.style.color = '#4CAF50';
                    answerBody.style.fontWeight = '700';
                }

            }
        });
    }

    /******************************************************
     * 2) SHORTANSWER (w tym openText)
     ******************************************************/
    else if (qType === "shortanswer") {
        let inputEl = questionElement.querySelector(".all_short_answers input[type='text']");
        if (!inputEl) {
            inputEl = questionElement.querySelector(".rich-text-answer-area");
        }
        if (!inputEl) return;

        let correctAnswerString = "";
        if (typeof correctAnswerObj === "string") {
            correctAnswerString = correctAnswerObj.trim();
        } else if (correctAnswerObj && correctAnswerObj.value) {
            correctAnswerString = String(correctAnswerObj.value).trim();
        }

        if (mode === 'immediate') {
            if (inputEl.tagName.toLowerCase() === "input") {
                inputEl.value = correctAnswerString;
                inputEl.dispatchEvent(new Event('input', { bubbles: true }));
                inputEl.dispatchEvent(new Event('change', { bubbles: true }));
            } else {
                const hiddenField = document.getElementById("givenAnswer");
                if (hiddenField) {
                    hiddenField.value = correctAnswerString;
                    hiddenField.dispatchEvent(new Event("input", { bubbles: true }));
                    hiddenField.dispatchEvent(new Event("change", { bubbles: true }));
                }
                const iframe = document.getElementById("givenAnswer_ifr");
                if (iframe) {
                    const iframeDoc = iframe.contentDocument || iframe.contentWindow.document;
                    const edytorBody = iframeDoc.querySelector(".mce-content-body") || iframeDoc.body;
                    edytorBody.innerHTML = `<p>${correctAnswerString}</p>`;
                }
            }
        }
        else if (mode === 'savemode') {
            if (inputEl.tagName.toLowerCase() === "input") {
                inputEl.value = "";
                inputEl.placeholder = "";
                inputEl.addEventListener('focus', () => {
                    inputEl.placeholder = correctAnswerString;
                });
                inputEl.addEventListener('blur', () => {
                    inputEl.placeholder = "";
                });
            } else {
                const hint = document.createElement("div");
                hint.textContent = `Podpowied: ${correctAnswerString}`;

                // Inline style
                hint.style.color = "#ccc";
                hint.style.fontSize = "0.8em";
                hint.style.opacity = "0.1";
                hint.style.transition = "opacity 0.2s ease";
                hint.style.marginTop = "4px";
                hint.style.userSelect = "text"; // Zmieniono z "none" na "text" aby mona byo kopiowa

                // Poka hint w penej widocznoci przy najechaniu
                hint.addEventListener('mouseenter', () => {
                    hint.style.opacity = "1";
                });
                hint.addEventListener('mouseleave', () => {
                    hint.style.opacity = "0.1";
                });

                // Wstawiamy zaraz po elemencie
                inputEl.insertAdjacentElement("afterend", hint);
            }
        }
        else {
            // default  wywietlamy zielony tekst POD polem
            const hint = document.createElement("div");
            hint.classList.add("correct-answer-info");

            // Style dla kontenera odpowiedzi
            hint.style.background = "#f0fff0";
            hint.style.border = "1px solid #ddeedd";
            hint.style.borderRadius = "4px";
            hint.style.marginTop = "10px";
            hint.style.marginBottom = "10px";
            hint.style.padding = "8px 12px";
            hint.style.width = "100%";
            hint.style.boxSizing = "border-box";
            hint.style.fontSize = "14px";
            hint.style.color = "green";

            // Struktura: nagwek + tre
            hint.innerHTML = `
                <div style="font-weight: bold; margin-bottom: 4px;">Poprawna odpowied:</div>
                <div>${correctAnswerString}</div>
            `;

            if (inputEl.tagName.toLowerCase() === "input") {
                const parentContainer = inputEl.closest(".all_short_answers");
                if (parentContainer) {
                    parentContainer.appendChild(hint);
                } else {
                    inputEl.insertAdjacentElement("afterend", hint);
                }
            } else {
                // Dla .rich-text-answer-area
                inputEl.insertAdjacentElement("afterend", hint);
            }
        }
    }
}


/*************************************************
 * 6. OBSUGA POJEDYNCZEGO PYTANIA
 *************************************************/
async function handleQuestionTestPortal(questionElement, mode, userEmail) {
    // === POCZTEK SUGEROWANEJ ZMIANY ===

    // Sprawd, czy pytanie nie zostao ju przetworzone, aby unikn duplikatw zapyta API
    if (questionElement.dataset.alreadyHandled === 'true') {
        // console.log("Pytanie ju obsuone, pomijam:", questionElement.id);
        return null;
    }
    // Ustaw flag natychmiast, aby zablokowa kolejne prby przetworzenia tego samego pytania
    questionElement.dataset.alreadyHandled = 'true';

    // === KONIEC SUGEROWANEJ ZMIANY ===

    const unifiedQuestion = await buildUnifiedQuestionTestPortal(questionElement);
    if (!unifiedQuestion) {
        // Jeli pytanie jest nieprawidowe, usu flag, aby umoliwi ewentualn ponown prb
        delete questionElement.dataset.alreadyHandled;
        return null;
    }

    return sendQuestionToChatGPTMicrosoftForms(unifiedQuestion, userEmail, mode)
        .then(response => {
            if (response && response.correctAnswer) {
                markCorrectAnswerTestPortal(
                    questionElement,
                    response.correctAnswer,
                    unifiedQuestion,
                    mode
                );
                // Flaga alreadyHandled pozostaje 'true' po sukcesie
                return response.correctAnswer;
            } else {
                // Jeli nie ma odpowiedzi, usu flag, aby pozwoli na ponown prb
                delete questionElement.dataset.alreadyHandled;
            }
            return null;
        })
        .catch(err => {
            console.error("Bd zapytania do endpointu /query-chatgpt-mf (TestPortal):", err);
            // W przypadku bdu sieciowego rwnie usu flag
            delete questionElement.dataset.alreadyHandled;
            return null;
        });
}



/*************************************************
 * 7. OBSUGA WSZYSTKICH PYTA NA STRONIE
 *************************************************/
export async function processTestPortalLikeMicrosoftForms(userEmail) {

    const { markingMode } = await new Promise((resolve) => {
        chrome.storage.local.get(['markingMode'], (result) => {
            resolve({ markingMode: result.markingMode || 'default' });
        });
    });

    const questions = document.querySelectorAll(".test-question-body");
    if (!questions.length) {
        return;
    }

    // === POCZTEK SUGEROWANEJ ZMIANY ===

    // Przetwarzaj wszystkie znalezione pytania rwnolegle, zamiast jedno po drugim
    const processingPromises = Array.from(questions).map(questionElement =>
        handleQuestionTestPortal(questionElement, markingMode, userEmail)
    );

    // Poczekaj na zakoczenie wszystkich operacji
    const results = await Promise.all(processingPromises);
}
