import { sendQuestionToChatGPTMicrosoftForms } from '../core/api.js';

/*************************************************
 * FUNKCJA POBIERAJCA OBRAZY
 *************************************************/
// Dodaj funkcj pomocnicz, ktra czeka na zaadowanie obrazu
async function waitForImageLoad(img, timeout = 5000) {
    return new Promise(resolve => {
        if (img.complete && img.naturalWidth !== 0) {
            return resolve();
        }
        const timer = setTimeout(resolve, timeout);
        img.addEventListener("load", () => {
            clearTimeout(timer);
            resolve();
        });
        img.addEventListener("error", () => {
            clearTimeout(timer);
            resolve();
        });
    });
}

async function extractImages(element) {
    const imageElements = element.querySelectorAll('img');
    const images = [];
    for (const img of imageElements) {
        await waitForImageLoad(img);  // czekamy na zaadowanie obrazu
        images.push({
            url: img.src,
            alt: img.alt || ""
        });
    }
    return images;
}


/*************************************************
 * 0. FUNKCJA POMOCNICZA DO EKSTRAKCJI ZAPISU MATEMATYCZNEGO
 *************************************************/
/**
 * Funkcja do ekstrakcji zapisu matematycznego z danego elementu.
 * Jeli elementy MathJax lub te z atrybutem data-mathml nie s od razu dostpne,
 * funkcja wykonuje synchroniczne oczekiwanie (busy-waiting) przez okrelony czas.
 *
 * @param {HTMLElement} element - Element, z ktrego pobieramy zapis matematyczny.
 * @param {number} timeout - Maksymalny czas oczekiwania (w ms), domylnie 300 ms.
 * @returns {string} - Wyekstrahowany zapis matematyczny.
 */
function extractMathContent(element, timeout = 300) {
    const pollInterval = 10;
    const startTime = Date.now();
    return new Promise((resolve) => {
        function poll() {
            // Jeli istnieje element <script type="math/tex">, pobieramy jego tre
            const scriptEl = element.querySelector('script[type="math/tex"]');
            if (scriptEl) {
                return resolve(scriptEl.textContent.trim());
            }
            // Inaczej szukamy elementw MathJax lub [data-mathml]
            const mathElements = element.querySelectorAll('.MathJax, [data-mathml]');
            if (mathElements.length > 0 || (Date.now() - startTime) >= timeout) {
                let mathContent = "";
                mathElements.forEach(mathEl => {
                    if (mathEl.hasAttribute('data-mathml')) {
                        mathContent += " " + mathEl.getAttribute('data-mathml').trim();
                    } else if (mathEl.innerText && mathEl.innerText.trim().length > 0) {
                        mathContent += " " + mathEl.innerText.trim();
                    }
                });
                // Dodatkowo przeszukujemy HTML w poszukiwaniu LaTeX otoczonego dolarami
                const rawHTML = element.innerHTML;
                const latexRegex = /\$(.*?)\$/g;
                let match;
                while ((match = latexRegex.exec(rawHTML)) !== null) {
                    if (match[1] && match[1].trim().length > 0) {
                        mathContent += " " + match[1].trim();
                    }
                }
                resolve(mathContent.trim());
            } else {
                setTimeout(poll, pollInterval);
            }
        }
        poll();
    });
}


/*************************************************
 * 1. DETEKCJA RODZAJU PYTANIA
 *************************************************/
export function detectQuestionTypeMicrosoftForms(questionElement) {
    // Jeli widoczna jest struktura Likerta (np. tabele z nagwkami lub wierszami Likerta)
    if (
        questionElement.querySelector('[data-automation-id="likerTableTh"]') ||
        questionElement.querySelector('[data-automation-id="likerTableTr"]')
    ) {
        return "likert";
    }
    // Mobile: Likert  struktura z subquestion
    if (questionElement.querySelector('[data-automation-id="likerSubQuestion"]')) {
        return "likert";
    }

    // Sprbuj wykry typ pytania na podstawie tekstu tytuu
    const questionInfo = questionElement.querySelector('[data-automation-id="questionTitle"] [aria-hidden="true"]');
    if (questionInfo) {
        const questionType = questionInfo.innerText.toLowerCase().trim();
        // Dla pyta jednokrotnego wyboru
        if (questionType.includes("pojedynczy wybr") || questionType.includes("jednokrotny wybr")) {
            return "singleChoice";
        }
        // Dla pyta wielokrotnego wyboru
        else if (questionType.includes("wielokrotny wybr") || questionType.includes("multi wybr")) {
            return "multipleChoice";
        }
        // Dla pyta zwizanych z dat
        else if (questionType.includes("data")) {
            return "date";
        }
        // Dla pyta otwartych (tekstowych)
        else if (questionType.includes("tekst")) {
            return "openText";
        }
        // Dla pyta typu ranking lub klasyfikacja
        else if (questionType.includes("klasyfikacja")) {
            return "ranking";
        }
    }

    // Alternatywne podejcie - wykrywanie po strukturze DOM
    if (questionElement.querySelector('input[type="radio"]')) {
        return "singleChoice";
    }
    if (questionElement.querySelector('input[type="checkbox"]')) {
        return "multipleChoice";
    }
    if (questionElement.querySelector('[data-automation-id="textInput"]') || questionElement.querySelector('textarea')) {
        return "openText";
    }
    if (questionElement.querySelector('[data-automation-id="dateContainer"]')) {
        return "date";
    }
    if (questionElement.querySelector('[data-automation-id="rankingItemContent"]')) {
        return "ranking";
    }

    return "unknown";
}


/*************************************************
 * 2. EKSTRAKCJA TEKSTU PYTANIA
 *************************************************/
async function extractQuestionTextMicrosoftForms(questionElement) {
    const mainTitleEl = questionElement.querySelector('[data-automation-id="questionTitle"] .text-format-content');
    const subTitleEl = questionElement.querySelector('[data-automation-id="questionSubTitle"] .text-format-content');
    let questionText = "";
    if (mainTitleEl) {
        questionText += mainTitleEl.innerText.trim();
        // Dodajemy zapis matematyczny z gwnego tytuu
        const mathFromMain = await extractMathContent(mainTitleEl);
        if (mathFromMain) {
            questionText += "\n" + mathFromMain;
        }
    }
    if (subTitleEl) {
        questionText += "\n" + subTitleEl.innerText.trim();
        // Dodajemy zapis matematyczny z podtytuu
        const mathFromSub = await extractMathContent(subTitleEl);
        if (mathFromSub) {
            questionText += "\n" + mathFromSub;
        }
    }

    const questionContent = questionElement.querySelector(':not([data-automation-id="choiceItem"])');
    const mathFromQuestion = questionContent ? await extractMathContent(questionContent) : "";
    if (mathFromQuestion && !questionText.includes(mathFromQuestion)) {
        questionText += "\n" + mathFromQuestion;
    }
    const images = await extractImages(questionElement);
    return { text: questionText.trim(), images };
}

/*************************************************
 * 3. EKSTRAKCJA ODPOWIEDZI DLA PYTA SINGLE/MULTIPLE CHOICE
 *************************************************/
async function extractMultipleChoiceAnswersMicrosoftForms(questionElement) {
    let answerElements = questionElement.querySelectorAll('[data-automation-id="choiceItem"]');
    const options = [];
    const letters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
    let idx = 0;

    // Jeli nie znaleziono adnych elementw opcji (np. dla pyta, gdzie opcje s obrazami),
    // sprbuj znale <img> w kontenerze odpowiedzi (np. w div.-bT-49)
    if (!answerElements || answerElements.length === 0) {
        const container = questionElement.querySelector('div.-bT-49');
        if (container) {
            const imgs = container.querySelectorAll('img');
            imgs.forEach((img, index) => {
                const id = letters[index] || ("" + (index + 1));
                options.push({
                    id,
                    // Jeli nie mamy sensownego labela, uywamy domylnego "Opcja X"
                    label: `Opcja ${index + 1}`,
                    images: [{ url: img.src, alt: img.alt || "" }]
                });
            });
            return options;
        }
    }

    // Jeli znaleziono elementy opcji  postpuj jak dotychczas
    for (let answerElement of answerElements) {
        let answerText = "";
        // Najpierw sprawdzamy, czy istnieje atrybut data-automation-value
        const autoVal = answerElement.querySelector('[data-automation-value]');
        if (autoVal) {
            answerText = autoVal.getAttribute('data-automation-value').trim();
        } else {
            const scriptEl = answerElement.querySelector('script[type="math/tex"]');
            if (scriptEl) {
                answerText = scriptEl.textContent.trim();
            } else {
                const mathContent = await extractMathContent(answerElement);
                const textContent = answerElement.querySelector('.text-format-content')?.innerText.trim() || "";
                answerText = textContent + (mathContent ? ("\n" + mathContent) : "");
            }
        }
        const images = await extractImages(answerElement);
        const id = letters[idx] || ("" + (idx + 1));
        options.push({
            id,
            label: answerText,
            images
        });
        idx++;
    }
    return options;
}



/*************************************************
 * 4. EKSTRAKCJA DLA PYTA OPEN TEXT
 *************************************************/
async function extractOpenTextQuestionMicrosoftForms(questionElement) {
    // Dla pyta openText nie mamy opcji  przekazujemy jedynie placeholder oraz ewentualne obrazy
    const inputElement = questionElement.querySelector('input[type="text"], textarea');
    const images = await extractImages(questionElement);
    return {
        placeholder: inputElement ? inputElement.placeholder || "Wprowad odpowied" : "Wprowad odpowied",
        images
    };
}

/*************************************************
 * 5. EKSTRAKCJA DLA PYTA RANKING
 *************************************************/
async function extractRankingQuestionMicrosoftForms(questionElement) {
    // Zakadamy, e ranking itemy posiadaj atrybut data-automation-id="rankingItemContent"
    const rankingItemElements = questionElement.querySelectorAll('[data-automation-id="rankingItemContent"]');
    // Tworzymy tablic obiektw  przyjmujemy, e kolejno w DOM-ie to oryginalna kolejno
    const ranking = Array.from(rankingItemElements).map((item, index) => ({
        id: index + 1,  // oryginalna pozycja
        label: item.innerText.trim()
    }));
    return { ranking };
}

/*************************************************
 * 5. EKSTRAKCJA DLA PYTA DATE
 *************************************************/
async function extractDateQuestionMicrosoftForms(questionElement) {
    // Szukamy kontenera, ktry zawiera pole daty  w Twojej strukturze atrybut data-automation-id="dateContainer"
    const dateContainer = questionElement.querySelector('[data-automation-id="dateContainer"]');
    let placeholder = "Wprowad dat (dd.MM.yyyy)";
    if (dateContainer) {
        // Pobieramy pole input typu text w obrbie kontenera
        const inputElement = dateContainer.querySelector('input[type="text"]');
        if (inputElement) {
            placeholder = inputElement.placeholder || placeholder;
        }
    }
    const images = await extractImages(questionElement);
    return { placeholder, images };
}


/*************************************************
 * 5. EKSTRAKCJA DLA PYTA LIKERT
 *************************************************/
async function extractLikertQuestionMicrosoftForms(questionElement) {
    // Jeli mamy struktur desktopow (tabela)
    if (questionElement.querySelector('[data-automation-id="likerTableTr"]')) {
        const statementElements = questionElement.querySelectorAll('[data-automation-id="likerStatementTd"] .text-format-content');
        const optionElements = questionElement.querySelectorAll('[data-automation-id="likerTableTh"] .text-format-content');

        const statements = await Promise.all(
            Array.from(statementElements).map(async (el, idx) => {
                const images = await extractImages(el);
                return { id: `s${idx + 1}`, text: el.innerText.trim(), images };
            })
        );

        const letters = ["P", "F"];
        const commonOptions = await Promise.all(
            Array.from(optionElements).map(async (el, idx) => {
                const images = await extractImages(el);
                let id = letters[idx] || String.fromCharCode(65 + idx);
                return { id, label: el.innerText.trim(), images };
            })
        );

        return { commonOptions, statements };
    }
    // Jeli mamy struktur mobiln (subquestion)
    else if (questionElement.querySelector('[data-automation-id="likerSubQuestion"]')) {
        const subQuestionElements = questionElement.querySelectorAll('[data-automation-id="likerSubQuestion"]');
        const statements = await Promise.all(
            Array.from(subQuestionElements).map(async (subQ, idx) => {
                const stmtEl = subQ.querySelector('.-q--329 .text-format-content');
                const images = stmtEl ? await extractImages(stmtEl) : [];
                return { id: `s${idx + 1}`, text: stmtEl ? stmtEl.innerText.trim() : "", images };
            })
        );
        // Zakadamy, e opcje s takie same we wszystkich subquestion  pobieramy z pierwszego
        const firstSubQ = questionElement.querySelector('[data-automation-id="likerSubQuestion"]');
        let optionElements = [];
        if (firstSubQ) {
            optionElements = firstSubQ.querySelectorAll('[data-automation-id="likerOption"]');
        }
        const letters = ["P", "F"];
        const commonOptions = await Promise.all(
            Array.from(optionElements).map(async (optEl, idx) => {
                const labelEl = optEl.querySelector('.-jt-333 .text-format-content');
                const images = labelEl ? await extractImages(labelEl) : [];
                let id = letters[idx] || String.fromCharCode(65 + idx);
                const label = labelEl ? labelEl.innerText.trim() : "";
                return { id, label, images };
            })
        );
        return { commonOptions, statements };
    }

    // Fallback  jeli struktura nie jest rozpoznana
    return { commonOptions: [], statements: [] };
}

/*************************************************
 * 6. BUDOWANIE UNIFIKOWANEJ STRUKTURY PYTANIA
 *************************************************/
async function buildUnifiedQuestion(questionElement) {
    const detectedType = detectQuestionTypeMicrosoftForms(questionElement);
    const { text, images } = await extractQuestionTextMicrosoftForms(questionElement);
    let unified = {
        id: questionElement.getAttribute('id') || `q-${Date.now()}`, // unikalny identyfikator
        text,
        images,
        metadata: { difficulty: "unknown" } // Moesz to rozszerzy
    };

    // Mapowanie typu: singleChoice -> singlechoice, multipleChoice -> multiple, openText -> shortanswer, likert -> likert
    switch (detectedType) {
        case "singleChoice":
            unified.type = "singlechoice";
            unified.options = await extractMultipleChoiceAnswersMicrosoftForms(questionElement);
            unified.format = {
                responseType: "optionId",
                description: "Odpowiedz jako pojedyncza litera odpowiadajca waciwej opcji."
            };
            break;
        case "multipleChoice":
            unified.type = "multiple";
            unified.options = await extractMultipleChoiceAnswersMicrosoftForms(questionElement);
            unified.format = {
                responseType: "optionIds",
                description: "Odpowiedz jako tablica liter odpowiadajcych waciwym opcjom (np. [\"A\", \"C\"])."
            };
            break;
        case "date":
            unified.type = "date";
            const dateData = await extractDateQuestionMicrosoftForms(questionElement);
            unified.options = [];  // Dla dat nie ma opcji wyboru
            unified.placeholder = dateData.placeholder;
            unified.format = {
                responseType: "date",
                description: "Podaj dat w formacie dd.MM.yyyy."
            };
            break;

        case "openText":
            unified.type = "shortanswer";
            const openData = await extractOpenTextQuestionMicrosoftForms(questionElement);
            unified.options = [];
            unified.placeholder = openData.placeholder;

            // Sprawdzamy, czy odpowied jest "krtka" (input) czy "duga" (textarea)
            const inputElement = questionElement.querySelector('input[type="text"], textarea');
            if (inputElement) {
                if (inputElement.tagName.toLowerCase() === "textarea") {
                    unified.answerLength = "long";
                } else {
                    unified.answerLength = "short";
                }
            } else {
                unified.answerLength = "short";
            }

            // Ustawiamy format zalenie od dugoci odpowiedzi
            if (unified.answerLength === "long") {
                unified.format = {
                    responseType: "text",
                    description: "Podaj wyczerpujc, szczegow odpowied tekstow."
                };
            } else {
                unified.format = {
                    responseType: "text",
                    description: "Podaj krtk odpowied tekstow."
                };
            }
            break;
        case "likert":
            unified.type = "likert";
            const likertData = await extractLikertQuestionMicrosoftForms(questionElement);
            unified.commonOptions = likertData.commonOptions;
            unified.statements = likertData.statements;
            unified.format = {
                responseType: "matrix",
                description: "Odpowiedz jako obiekt JSON, gdzie kady klucz to id stwierdzenia (np. 's1'), a warto to identyfikator wybranej opcji ('P' dla Prawda lub 'F' dla Fasz)."
            };
            break;
        case "ranking":
            unified.type = "ranking";
            const rankingData = await extractRankingQuestionMicrosoftForms(questionElement);
            unified.ranking = rankingData.ranking; // Tablica ranking itemw
            unified.format = {
                responseType: "ranking",
                description: "Podaj poprawn kolejno elementw jako uporzdkowan list numerw, np. [2, 1, 3, 4]."
            };
            break;



        default:
            // console.warn("Nieobsugiwany typ pytania:", detectedType);
            return null;
    }
    return unified;
}

function getCorrectAnswerText() {
    // Pobierz jzyk przegldarki
    const browserLang = navigator.language || navigator.userLanguage;
    const lang = browserLang.split('-')[0];

    const translations = {
        'pl': 'Poprawna odpowied:',
        'en': 'Correct answer:',
        'de': 'Richtige Antwort:',
        'es': 'Respuesta correcta:',
        'fr': 'Rponse correcte:',
        'it': 'Risposta corretta:',
        'ru': ' :',
        'cs': 'Sprvn odpov:',
        'sk': 'Sprvna odpove:',
        'uk': ' :'
    };

    return translations[lang] || translations['en'];
}


/*************************************************
 * 7. ZAZNACZANIE POPRAWNEJ ODPOWIEDZI  UNIFIKOWANE
 *************************************************/
function markCorrectAnswerUnified(questionElement, correctAnswerObj, unifiedQuestion, mode) {
    mode = mode.toLowerCase()
    // console.log("Markowanie odpowiedzi dla pytania:", unifiedQuestion.id, "tryb:", mode);
    const qType = unifiedQuestion.type;

    if (qType === "singlechoice" || qType === "multiple") {
        let answerElements = questionElement.querySelectorAll('[data-automation-id="choiceItem"]');
        if (!answerElements || answerElements.length === 0) {
            answerElements = questionElement.querySelectorAll('div.-mf-91');
        }

        const isIndexMode = typeof correctAnswerObj === "string" && /^[\d,\s]+$/.test(correctAnswerObj.trim());
        let correctIndexes = [];
        let correctLetters = [];
        if (isIndexMode) {
            correctIndexes = correctAnswerObj
                .split(",")
                .map(str => parseInt(str.trim(), 10) - 1)
                .filter(n => !isNaN(n) && n >= 0);
        } else if (Array.isArray(correctAnswerObj)) {
            correctLetters = correctAnswerObj.map(letter => letter.toUpperCase());
        } else if (typeof correctAnswerObj === "string") {
            correctLetters = [correctAnswerObj.trim().toUpperCase()];
        }

        // console.log("Oczekiwane indeksy:", correctIndexes, "lub litery:", correctLetters);
        // console.log(answerElements)
        answerElements.forEach((answerElement, idx) => {
            // Pobieramy element zawierajcy tre (MathJax lub tekst)
            const answerTextEl = answerElement.querySelector('.text-format-content') || answerElement;
            // Ustalamy identyfikator opcji  jeli nie ma, uywamy domylnego: A, B, C, 
            const optionLetter = unifiedQuestion.options[idx]?.id || String.fromCharCode(65 + idx);
            let isCorrect = false;
            if (isIndexMode) {
                if (correctIndexes.includes(idx)) {
                    isCorrect = true;
                }
            } else {
                if (optionLetter && correctLetters.includes(optionLetter.toUpperCase())) {
                    isCorrect = true;
                }
            }

            // console.log(`Opcja ${idx}: identyfikator = ${optionLetter}, jest poprawna?`, isCorrect);

            if (isCorrect) {
                // console.log(answerElement)
                if (mode === "default") {
                    // Najpierw prbujemy oznaczy zapis matematyczny, jeli istnieje
                    const mathEl = answerTextEl.querySelector('.MathJax');
                    // console.log(mathEl)

                    if (mathEl) {
                        if (mathEl.getBoundingClientRect().height < 5) {
                            setTimeout(() => {
                                mathEl.style.boxShadow = "0 0 10px rgba(0, 128, 0, 0.7)";
                                mathEl.style.border = "2px solid rgba(0, 128, 0, 0.7)";
                                mathEl.style.backgroundColor = "rgba(0, 128, 0, 0.1)";
                                mathEl.style.transition = "all 0.3s ease";
                                mathEl.style.padding = "10px";
                                mathEl.style.borderRadius = "5px";
                            }, 200);
                        } else {
                            mathEl.style.boxShadow = "0 0 10px rgba(0, 128, 0, 0.7)";
                            mathEl.style.border = "2px solid rgba(0, 128, 0, 0.7)";
                            mathEl.style.backgroundColor = "rgba(0, 128, 0, 0.1)";
                            mathEl.style.transition = "all 0.3s ease";
                            mathEl.style.padding = "10px";
                            mathEl.style.borderRadius = "5px";
                        }
                    } else {
                        answerTextEl.style.color = "green";
                        answerTextEl.style.fontWeight = "700";
                    }

                    // Teraz obsuga obrazkw
                    const imgEl = answerElement.querySelector('img');
                    // console.log(imgEl)
                    if (imgEl) {
                        if (imgEl.naturalWidth < 20) { // obraz nie jest jeszcze zaadowany
                            setTimeout(() => {
                                answerTextEl.style.boxShadow = "0 0 10px rgba(0, 128, 0, 0.7)";
                                answerTextEl.style.border = "2px solid rgba(0, 128, 0, 0.7)";
                                answerTextEl.style.backgroundColor = "rgba(0, 128, 0, 0.1)";
                                answerTextEl.style.transition = "all 0.3s ease";
                                answerTextEl.style.padding = "10px";
                                answerTextEl.style.borderRadius = "5px";
                            }, 300);
                        } else {
                            answerTextEl.style.boxShadow = "0 0 10px rgba(0, 128, 0, 0.7)";
                            answerTextEl.style.border = "2px solid rgba(0, 128, 0, 0.7)";
                            answerTextEl.style.backgroundColor = "rgba(0, 128, 0, 0.1)";
                            answerTextEl.style.transition = "all 0.3s ease";
                            answerTextEl.style.padding = "10px";
                            answerTextEl.style.borderRadius = "5px";
                        }
                    } else {
                        // Jeli nie ma obrazka, zmieniamy styl tekstu
                        answerTextEl.style.color = "green";
                        answerTextEl.style.fontWeight = "700";
                    }
                } else if (mode === "immediate") {
                    // W trybie immediate sprbuj symulowa kliknicie na input
                    const inputEl = answerElement.querySelector('input[type="radio"], input[type="checkbox"]');
                    if (inputEl) {
                        if (inputEl.type === "checkbox") {
                            if (!inputEl.checked) {
                                inputEl.checked = true;
                            }
                        } else {
                            inputEl.click();
                        }
                        inputEl.dispatchEvent(new Event('input', { bubbles: true }));
                        inputEl.dispatchEvent(new Event('change', { bubbles: true }));
                    }
                } else if (mode === "savemode") {
                    const mathEl = answerTextEl.querySelector('.MathJax');
                    if (mathEl) {
                        mathEl.style.boxShadow = "0 0 0 1px rgba(0, 128, 0, 0.03)";
                        mathEl.style.border = "1px solid rgba(0, 128, 0, 0.03)";
                        mathEl.style.backgroundColor = "rgba(0, 128, 0, 0.01)";
                        mathEl.style.padding = "2px";
                        mathEl.style.borderRadius = "3px";
                    } else {
                        const text = answerTextEl.textContent;
                        if (text.length > 0) {
                            answerTextEl.innerHTML = `<span style="font-weight:600; opacity:0.8;">${text.charAt(0)}</span>${text.slice(1)}`;
                        }
                    }
                }
            }
        });
    }

    else if (qType === "shortanswer") {
        // Pobieramy element input lub textarea
        const inputElement = questionElement.querySelector('[data-automation-id="textInput"]');
        if (!inputElement) return;

        if (!inputElement.classList.contains('savemode-input')) {
            inputElement.classList.add('savemode-input');
        }

        if (mode === "default") {
            // Dla dugiej odpowiedzi (textarea) ustawiamy placeholder i dodajemy tre odpowiedzi
            const inputEl = questionElement.querySelector('[data-automation-id="textInput"]');
            if (!inputEl) return;

            // Upewniamy si, e kontener inputa (rodzic) ma styl block,
            // dziki czemu element wstawiony za nim pojawi si w nowej linii.
            const container = inputEl.parentElement;
            container.style.display = "block";

            // Szukamy istniejcego elementu z informacj lub tworzymy nowy
            let info = container.querySelector('.correct-answer-info');
            if (!info) {
                info = document.createElement("div");
                info.classList.add("correct-answer-info");
                info.style.display = "block";
                info.style.marginTop = "10px";
                info.style.marginBottom = "5px";
                info.style.color = "green";
                info.style.fontWeight = "500";
                info.style.whiteSpace = "normal";
                info.style.overflow = "auto";
                info.style.width = "100%";
                info.style.boxSizing = "border-box";
                info.style.position = "relative";
                info.style.zIndex = "10";
                info.style.padding = "8px";
                info.style.border = "1px solid #e0e0e0";
                info.style.borderRadius = "4px";
                info.style.backgroundColor = "#f0f8f0";
                info.style.fontSize = "14px";
                info.style.lineHeight = "1.5";
                info.style.wordWrap = "break-word";
                // Ustawiamy tre odpowiedzi z wykorzystaniem tumaczenia nagwka
                info.innerText = `${getCorrectAnswerText()} ${correctAnswerObj}`;
                // Wstawiamy element informacyjny po kontenerze inputa
                container.insertAdjacentElement("afterend", info);
            }
        } else if (mode === "immediate") {
            // Dla obu rodzajw elementw ustawiamy warto i wywoujemy eventy
            inputElement.value = String(correctAnswerObj);
            inputElement.dispatchEvent(new Event('input', { bubbles: true }));
            inputElement.dispatchEvent(new Event('change', { bubbles: true }));
            inputElement.focus();
            inputElement.blur();
        } else if (mode === "savemode") {
            // Uywamy selektora opartego na data-automation-id, aby znale element input lub textarea
            const inputEl = questionElement.querySelector('[data-automation-id="textInput"]');
            if (!inputEl) return;

            if (!inputEl.classList.contains('savemode-input')) {
                inputEl.classList.add('savemode-input');
            }

            // Zapisujemy oryginalny placeholder, aby mc go pniej przywrci
            const originalPlaceholder = inputEl.getAttribute('placeholder') || '';
            inputEl.dataset.originalPlaceholder = originalPlaceholder;

            // Pobieramy domylny element placeholder generowany przez stron (np. Google)
            const googlePlaceholder = questionElement.querySelector('div[jsname="LwH6nd"]');

            // Dodajemy event listenery dla trybu savemode
            inputEl.addEventListener('focus', function () {
                if (this.value === '') {
                    this.placeholder = String(correctAnswerObj);
                }
                // Ukrywamy dodatkowy element z placeholderem, jeli istnieje
                if (googlePlaceholder) {
                    googlePlaceholder.style.display = "none";
                }
            });

            inputEl.addEventListener('blur', function () {
                // Przywracamy oryginalny placeholder zapisany w dataset
                this.placeholder = this.dataset.originalPlaceholder;
                // Przywracamy domyln widoczno dodatkowego placeholdera
                if (googlePlaceholder) {
                    googlePlaceholder.style.display = "";
                }
            });

            inputEl.addEventListener('input', function () {
                if (this.value !== '') {
                    this.placeholder = '';
                } else if (document.activeElement === this) {
                    this.placeholder = String(correctAnswerObj);
                }
            });

            // Dodajemy reguy CSS dla pseudo-elementu ::placeholder tylko raz
            if (!document.getElementById('savemode-placeholder-style')) {
                const style = document.createElement('style');
                style.id = 'savemode-placeholder-style';
                style.textContent = `
                    /* Gdy input ma fokus, placeholder w kolorze jasno szarym */
                    .savemode-input:focus::placeholder {
                        color: #ccc !important;
                    }
                    /* Gdy input nie ma fokusu, placeholder w kolorze czarnym */
                    .savemode-input:not(:focus)::placeholder {
                        color: #000 !important;
                    }
                `;
                document.head.appendChild(style);
            }
        }

    }
    else if (qType === "ranking") {
        // Zakadamy, e backend zwraca correctAnswerObj jako tablic liczb, np. [1, 3, 2, 4]
        if (typeof correctAnswerObj === "string") {
            try {
                correctAnswerObj = JSON.parse(correctAnswerObj);
            } catch (e) {
                // console.error("Nie udao si sparsowa correctAnswerObj:", e);
            }
        }
        if (mode === "default") {
            // Default mode: pod kontenerem rankingowym wywietlamy czytelny komunikat
            const rankingContainer = questionElement.querySelector('.lrp-ranking-container');
            if (!rankingContainer) {
                // console.warn("Nie znaleziono kontenera rankingowego.");
                return;
            }
            // Funkcja sprawdzajca kolejno rankingowych elementw
            function checkRankingOrder() {
                const rankItems = Array.from(rankingContainer.querySelectorAll('.rank-item'));
                // Upewniamy si, e kady rank-item ma ustawiony atrybut data-original-id (raz, przy pierwszym uruchomieniu)
                rankItems.forEach((item, index) => {
                    if (!item.getAttribute('data-original-id')) {
                        item.setAttribute('data-original-id', index + 1);
                    }
                });
                // Obliczamy aktualn kolejno  jako tablic liczb pobranych z atrybutu data-original-id
                const currentOrder = rankItems.map(item => parseInt(item.getAttribute('data-original-id')));
                // Dla kadego elementu porwnujemy, czy jego warto w currentOrder odpowiada wartoci z correctAnswerObj
                rankItems.forEach((item, idx) => {
                    if (currentOrder[idx] !== correctAnswerObj[idx]) {
                        // Jeli element jest w niewaciwej kolejnoci  czerwone obramowanie
                        item.style.border = "2px solid red";
                    } else {
                        // Jeli element jest poprawnie ustawiony  zielone obramowanie
                        item.style.border = "2px solid green";
                    }
                });
            }
            // Wywoujemy sprawdzenie kolejnoci od razu
            checkRankingOrder();
            // Ustawiamy MutationObserver, aby przy kadej zmianie (np. przesuwaniu elementw przez uytkownika)
            // wywoa funkcj sprawdzajc kolejno i aktualizowa obramowania
            const observer = new MutationObserver(() => {
                checkRankingOrder();
            });
            observer.observe(rankingContainer, { childList: true, subtree: true });
            // console.log("Tryb default aktywny  elementy s podwietlane: bdne na czerwono, poprawne na zielono.");
        }

        if (mode === "immediate") {
            // TRYB IMMEDIATE  automatyczne ustawienie kolejnoci rankingowych elementw
            const rankingContainer = questionElement.querySelector('.lrp-ranking-container');
            if (!rankingContainer) {
                // console.warn("Nie znaleziono kontenera rankingowego.");
                return;
            }
            // Pobieramy wszystkie rank-itemy i ustawiamy atrybut data-original-id, jeli jeszcze nie jest ustawiony
            const rankItems = Array.from(rankingContainer.querySelectorAll('.rank-item'));
            rankItems.forEach((item, index) => {
                if (!item.getAttribute('data-original-id')) {
                    item.setAttribute('data-original-id', index + 1);
                }
            });
            // Zakadamy, e correctAnswerObj to tablica liczb, np. [1, 3, 2, 4]
            // Konwertujemy liczby na stringi, aby porwnania atrybutw dziaay poprawnie
            const correctOrder = correctAnswerObj.map(String);
            // Tworzymy now kolejno: dla kadej pozycji z correctOrder szukamy rank-itemu,
            // ktrego data-original-id odpowiada tej wartoci
            const newOrder = correctOrder.map(id =>
                rankItems.find(item => item.getAttribute('data-original-id') === id)
            );
            if (newOrder.includes(undefined)) {
                // console.warn("Nie udao si przestawi rankingowych elementw  sprawd struktur DOM.");
                return;
            }
            // Czycimy kontener i wstawiamy elementy w nowej kolejnoci
            rankingContainer.innerHTML = '';
            newOrder.forEach(item => rankingContainer.appendChild(item));
            // console.log("Kolejno zostaa ustawiona automatycznie:", newOrder.map(item => item.getAttribute('data-original-id')));
        }
        else if (mode === "savemode") {
            // TRYB SAVEMODE  podwietlanie elementw, ktre s w zej kolejnoci
            const rankingContainer = questionElement.querySelector('.lrp-ranking-container');
            if (!rankingContainer) {
                // console.warn("Nie znaleziono kontenera rankingowego.");
                return;
            }
            // Funkcja sprawdzajca kolejno rankingowych elementw
            function checkRankingOrder() {
                const rankItems = Array.from(rankingContainer.querySelectorAll('.rank-item'));
                // Upewnij si, e kady rank-item ma ustawiony atrybut data-original-id (raz, przy pierwszym uruchomieniu)
                rankItems.forEach((item, index) => {
                    if (!item.getAttribute('data-original-id')) {
                        item.setAttribute('data-original-id', index + 1);
                    }
                });
                // Obliczamy aktualn kolejno  jako tablic liczb pobranych z atrybutu data-original-id
                const currentOrder = rankItems.map(item => parseInt(item.getAttribute('data-original-id')));
                // Dla kadego elementu porwnujemy, czy jego warto w currentOrder odpowiada wartoci z correctAnswerObj
                rankItems.forEach((item, idx) => {
                    if (currentOrder[idx] !== correctAnswerObj[idx]) {
                        // Podwietlamy element, ktry jest w niewaciwym miejscu
                        item.style.border = "1px solid #ebebeb";
                    } else {
                        // Jeli element jest ju poprawnie ustawiony  usuwamy podwietlenie
                        item.style.border = "";
                    }
                });
            }
            // Wywoujemy sprawdzenie kolejnoci od razu
            checkRankingOrder();
            // Ustawiamy MutationObserver, aby przy kadej zmianie (np. przesuwaniu elementw przez uytkownika)
            // wywoa funkcj sprawdzajc kolejno i odpowiednio aktualizowa podwietlenia.
            const observer = new MutationObserver(() => {
                checkRankingOrder();
            });
            observer.observe(rankingContainer, { childList: true, subtree: true });
            // console.log("Tryb savemode aktywny  niewaciwie ustawione elementy bd podwietlone.");
        }

    }
    else if (qType === "date") {
        // Szukamy kontenera daty
        const dateContainer = questionElement.querySelector('[data-automation-id="dateContainer"]');
        if (!dateContainer) {
            // console.warn("Nie znaleziono kontenera daty dla pytania:", unifiedQuestion.id);
            return;
        }
        // Pobieramy pole input typu text w kontenerze daty
        const inputElement = dateContainer.querySelector('input[type="text"]');
        if (!inputElement) {
            // console.warn("Nie znaleziono pola daty dla pytania:", unifiedQuestion.id);
            return;
        }

        if (mode === "default") {
            // Wstawiamy komunikat z poprawn dat pod kontenerem daty
            let info = dateContainer.parentElement.querySelector('.correct-answer-date-info');
            if (!info) {
                info = document.createElement("div");
                info.classList.add("correct-answer-date-info");
                info.style.display = "block";
                info.style.marginTop = "5px";
                info.style.color = "green";
                info.style.fontWeight = "700";
                dateContainer.parentElement.insertAdjacentElement("afterend", info);
            }
            info.innerText = `Poprawna data: ${correctAnswerObj}`;
        } else if (mode === "immediate") {
            // Automatycznie wpisujemy poprawn dat do pola
            inputElement.value = String(correctAnswerObj);
            inputElement.dispatchEvent(new Event('input', { bubbles: true }));
            inputElement.dispatchEvent(new Event('change', { bubbles: true }));
            inputElement.focus();
            inputElement.blur();
        } else if (mode === "savemode") {
            // Dodajemy event listenery, aby przy focusie ustawi placeholder na poprawn dat,
            // a przy blur przywrci domylny placeholder.
            inputElement.addEventListener('focus', function () {
                inputElement.placeholder = String(correctAnswerObj);
            });
            inputElement.addEventListener('blur', function () {
                inputElement.placeholder = unified.placeholder;  // domylny placeholder z obiektu unified
            });
        }
    }



    else if (qType === "likert") {

        // Obsuga przypadku, gdy correctAnswerObj.value to string
        if (typeof correctAnswerObj.value === "string") {
            const numericStr = correctAnswerObj.value;
            const numericValues = numericStr.split(",").filter(v => v !== "");
            let mapping = {};
            if (unifiedQuestion.commonOptions && unifiedQuestion.commonOptions.length > 0) {
                unifiedQuestion.commonOptions.forEach((opt, idx) => {
                    mapping[idx + 1] = opt.id.toUpperCase();
                });
            }
            // Szukamy desktopowych wierszy, a jeli ich nie ma  mobilnych subquestion
            let statementRows = questionElement.querySelectorAll('[data-automation-id="likerTableTr"]');
            if (statementRows.length === 0) {
                statementRows = questionElement.querySelectorAll('[data-automation-id="likerSubQuestion"]');
            }
            // Ustalamy, czy mamy do czynienia z mobiln wersj
            const isMobile = statementRows.length > 0 &&
                statementRows[0].getAttribute('data-automation-id') === 'likerSubQuestion';
            statementRows.forEach((row, idx) => {
                const desiredLetter = mapping[numericValues[idx]];
                if (!desiredLetter) return;
                const inputSelector = `input[data-choice-id="${numericValues[idx]}"]`;
                const inputEl = row.querySelector(inputSelector);
                if (inputEl) {
                    if (mode === "immediate") {
                        // Dla mobilnej wersji klikamy w label, co lepiej symuluje dotyk
                        if (isMobile) {
                            const labelEl = inputEl.closest('label');
                            if (labelEl) {
                                labelEl.click();
                            }
                        } else {
                            inputEl.click();
                        }
                    }
                    // Aktualizacja stylw  najpierw prbujemy znale element graficzny (SVG)
                    const svgPath = inputEl.parentElement.querySelector("svg path");
                    if (svgPath) {
                        if (mode === "savemode") {
                            svgPath.style.stroke = "#ccc";
                            svgPath.style.strokeWidth = "2";
                        } else if (mode === "default") {
                            svgPath.style.stroke = "green";
                            svgPath.style.strokeWidth = "2";
                        }
                    } else {
                        // Jeli nie znaleziono SVG, zmieniamy styl tekstu
                        const answerTextEl = row.querySelector('.text-format-content');
                        if (answerTextEl) {
                            if (mode === "savemode") {
                                answerTextEl.style.color = "gray";
                            } else {
                                answerTextEl.style.color = "green";
                                answerTextEl.style.fontWeight = "700";
                            }
                        }
                    }
                    // Dla mobilnej wersji  zmieniamy styl waciwego kontenera odpowiedzi,
                    // szukajc od inputa najbliszego elementu z data-automation-id="likerOption"
                    if (isMobile) {
                        const mobileTarget = inputEl.closest('[data-automation-id="likerOption"]');
                        if (mobileTarget) {
                            if (mode === "savemode") {
                                mobileTarget.style.border = "2px solid lightgray";
                            } else if (mode === "default") {
                                mobileTarget.style.border = "2px solid green";
                                mobileTarget.style.fontWeight = "700";
                            }
                        }
                    }
                }
            });
        }
        // Obsuga przypadku, gdy correctAnswerObj.value to obiekt (np. { s1: 'P', s2: 'F', ... })
        else if (typeof correctAnswerObj.value === "object" && correctAnswerObj.value !== null) {
            let inverseMapping = {};
            if (unifiedQuestion.commonOptions && unifiedQuestion.commonOptions.length > 0) {
                unifiedQuestion.commonOptions.forEach((opt, idx) => {
                    inverseMapping[opt.id.toUpperCase()] = idx + 1;
                });
            }
            let numericAnswers = [];
            if (unifiedQuestion.statements && unifiedQuestion.statements.length > 0) {
                unifiedQuestion.statements.forEach((stmt) => {
                    let letter = correctAnswerObj.value[stmt.id];
                    if (letter) {
                        let numeric = inverseMapping[letter.toUpperCase()];
                        numericAnswers.push(numeric !== undefined ? numeric : letter);
                    } else {
                        numericAnswers.push("");
                    }
                });
            }
            const numericAnswerString = numericAnswers.join(",") + ",";
            correctAnswerObj = { value: numericAnswerString, answerRepresentation: "numeric" };

            const numericStr = correctAnswerObj.value;
            const numericValues = numericStr.split(",").filter(v => v !== "");
            let mapping = {};
            if (unifiedQuestion.commonOptions && unifiedQuestion.commonOptions.length > 0) {
                unifiedQuestion.commonOptions.forEach((opt, idx) => {
                    mapping[idx + 1] = opt.id.toUpperCase();
                });
            }
            let statementRows = questionElement.querySelectorAll('[data-automation-id="likerTableTr"]');
            if (statementRows.length === 0) {
                statementRows = questionElement.querySelectorAll('[data-automation-id="likerSubQuestion"]');
            }
            const isMobile = statementRows.length > 0 &&
                statementRows[0].getAttribute('data-automation-id') === 'likerSubQuestion';
            statementRows.forEach((row, idx) => {
                const desiredLetter = mapping[numericValues[idx]];
                if (!desiredLetter) return;
                const inputSelector = `input[data-choice-id="${numericValues[idx]}"]`;
                const inputEl = row.querySelector(inputSelector);
                if (inputEl) {
                    if (mode === "immediate") {
                        if (isMobile) {
                            const labelEl = inputEl.closest('label');
                            if (labelEl) {
                                labelEl.click();
                            }
                        } else {
                            inputEl.click();
                        }
                    }
                    const svgPath = inputEl.parentElement.querySelector("svg path");
                    if (svgPath) {
                        if (mode === "savemode") {
                            svgPath.style.stroke = "#ccc";
                            svgPath.style.strokeWidth = "2";
                        } else if (mode === "default") {
                            svgPath.style.stroke = "green";
                            svgPath.style.strokeWidth = "2";
                        }
                    } else {
                        const answerTextEl = row.querySelector('.text-format-content');
                        if (answerTextEl) {
                            if (mode === "savemode") {
                                answerTextEl.style.color = "gray";
                            } else if (mode === "default") {
                                answerTextEl.style.color = "green";
                                answerTextEl.style.fontWeight = "700";
                            }
                        }
                    }
                    // Dla mobilnej wersji  wyszukujemy kontener odpowiedzi od inputa
                    if (isMobile) {
                        const mobileTarget = inputEl.closest('[data-automation-id="likerOption"]');
                        if (mobileTarget) {
                            if (mode === "savemode") {
                                mobileTarget.style.border = "2px solid lightgray";
                            } else if (mode === "default") {
                                mobileTarget.style.border = "2px solid green";
                                mobileTarget.style.fontWeight = "700";
                            }
                        }
                    }
                }
            });
        }
        else {
            // console.warn("Niepoprawny format odpowiedzi dla likert.");
            return;
        }
    }



}



/*************************************************
 * 8. PRZETWARZANIE POJEDYNCZEGO PYTANIA
 *************************************************/
async function handleQuestionMicrosoftForms(questionElement, mode) {
    // === POCZTEK SUGEROWANEJ ZMIANY (BLOKADA DUPLIKATW) ===
    if (questionElement.dataset.alreadyHandled === 'true') {
        return null;
    }
    questionElement.dataset.alreadyHandled = 'true';
    // === KONIEC SUGEROWANEJ ZMIANY ===

    const unifiedQuestion = await buildUnifiedQuestion(questionElement);
    if (!unifiedQuestion) {
        // Jeli pytanie jest nieprawidowe, pozostawiamy flag, aby nie prbowa ponownie.
        return null;
    }

    if (!["singlechoice", "multiple", "shortanswer", "likert", "ranking", "date"].includes(unifiedQuestion.type)) {
        return unifiedQuestion; // Zwr dane, nawet jeli nie wysyamy do API
    }

    // === POCZTEK SUGEROWANEJ ZMIANY (POPRAWNA OBSUGA ASYNC) ===
    try {
        const response = await sendQuestionToChatGPTMicrosoftForms(unifiedQuestion);

        if (response && response.correctAnswer) {
            const correctAnswer = response.correctAnswer;
            markCorrectAnswerUnified(questionElement, correctAnswer, unifiedQuestion, mode);
        } else {
            console.error("Brak pola 'correctAnswer' w odpowiedzi z backendu:", response);
        }
    } catch (error) {
        console.error("Bd podczas wysyania pytania do ChatGPT:", error);
        // W przypadku bdu usu flag, aby umoliwi ponown prb
        delete questionElement.dataset.alreadyHandled;
    }
    // === KONIEC SUGEROWANEJ ZMIANY ===

    return unifiedQuestion;
}

/*************************************************
 * 9. PRZETWARZANIE WSZYSTKICH PYTANIA
 *************************************************/
export async function processMicrosoftForms() {
    function shouldProcessMicrosoftForms() {
        const infoElement = document.querySelector(
            '[aria-label*="Informacje o uczniu"], [aria-label*="Student Information"]'
        );
        return !infoElement;
    }

    if (!shouldProcessMicrosoftForms()) {
        return;
    }

    const { markingMode } = await new Promise((resolve) => {
        chrome.storage.local.get(['markingMode'], (result) => {
            resolve({ markingMode: result.markingMode || 'default' });
        });
    });

    const questions = document.querySelectorAll('[data-automation-id="questionItem"]');
    if (questions.length === 0) {
        // console.log("Nie znaleziono pyta na stronie Microsoft Forms.");
        return;
    }

    // === POCZTEK SUGEROWANEJ ZMIANY (PRZETWARZANIE RWNOLEGE) ===

    // Utwrz tablic obietnic, bez sztucznych opnie.
    const processingPromises = Array.from(questions).map(questionElement =>
        handleQuestionMicrosoftForms(questionElement, markingMode)
    );

    // Poczekaj na zakoczenie wszystkich operacji jednoczenie.
    const results = await Promise.all(processingPromises);
    const successfulResults = results.filter(r => r !== null);

    // console.log(`Przetworzono ${successfulResults.length} z ${questions.length} pyta.`);

}